import { FlowProcess } from "./flow_process/flow_process";

const Single = {
  type: "object",
  properties: {
    id: {
      type: "integer",
    },
    name: {
      type: "string",
    },
    user: {
      type: "string",
    },
    trigger: {
      type: "string",
    },
    is_multiple: {
      type: "boolean",
    },
    is_active: {
      type: "boolean",
    },
    process: FlowProcess,
    message_if_fail: {type: "boolean"},
    created_at: {
      type: "string",
    },
    updated_at: {
      type: "string",
    },
  },
  required: [
    "id",
    "name",
    "user",
    "trigger",
    "is_multiple",
    "is_active",
    "process",
    "message_if_fail",
    "created_at",
    "updated_at",
  ],
  additionalProperties: false,
};

const List = {
  type: "array",
  items: Single,
};

export { Single, List };
