<template>
  <div class="page">
    <heading-component
      class="page__heading"
      text=""
      size="middle"
    />
    <div class="page__container">
      <div class="page__editblock">
        <base-form
          class="page__form"
          :attributes="current"
          :settings="form_pages"
          :is_edit_mode="true"
          :is_header_required="false"
          @update="handle_update_attribute($event.name, $event.value)"
        >
        </base-form>

        <div class="page__editrow">
          <btn-component
            class="page__button"
            @click="update_action()"
            caption=""
            type="success"
            :disabled="!is_edit_block_valid"
            v-if="can_change && current_id"
          />
          <btn-component
            class="page__button"
            @click="delete_action()"
            caption=""
            type="danger"
            v-if="can_delete && current_id"
          />
        </div>
      </div>
      <search-component
        class="page__searchblock"
        :schema="search_schema"
        :model_objects="model_objects"
        :convert_obj_to_item="convert_model_to_item"
        :current_id="current_id"
        sort_by="name"
        :search_time="search_time"
        :is_searching="is_searching"
        :should_emit_search_event_on_any_keypress="true"
        @choose="current_id = $event"
        @search="search_action($event)"
      />
    </div>
  </div>
</template>

<script>
import { computed, onMounted, ref } from "vue";
import { SetUser, DeleteUser } from "../../logic/domain/command";
import { MessageBus } from "../../logic/service_layer/message_bus";
import { VueUnitOfWork } from "../../logic/service_layer/uow";
import {
  convertObject,
  useBeforeEnterPage,
  filterArrayOfObjectsByQuery,
  useCurrentMixin,
  useGetRoles,
  useGetUsers,
} from "../../logic/service_layer/use_modules";

import HeadingComponent from "../../components/HeadingComponent.vue";
import BtnComponent from "../../components/buttons/BtnComponent.vue";
import BaseForm from "../../components/forms/BaseForm.vue";
import SearchComponent from "../../components/SearchComponent.vue";

export default {
  components: {
    HeadingComponent,
    BtnComponent,
    BaseForm,
    SearchComponent,
  },
  name: "UserSettingsPage",

  setup() {
    const uow = new VueUnitOfWork();

    const convert_model_to_item = (obj) => {
      return {
        id: obj.username,
        attributes: {
          username: obj.username,
          email: obj.email,
          first_name: obj.first_name,
          last_name: obj.last_name,
          is_active: obj.is_active === true ? "Y" : "N",
          access_group_regex: obj.access_group_regex,
          role: obj.role,
          role_inside_group: obj.role_inside_group,
          role_outside_group: obj.role_outside_group,
          on_behalf: obj.on_behalf,
        },
        actions: {},
      };
    };

    const query_for_internal_search = ref({});
    const model_objects = computed(() =>
      filterArrayOfObjectsByQuery(
        uow.user_repository.values().map((obj) => obj.value),
        query_for_internal_search.value
      )
    );

    const can_change = uow.permission_repository.has("can_change_user");
    const can_delete = uow.permission_repository.has("can_delete_user");

    // STATUS
    const search_time = ref(0);
    const is_searching = ref(false);

    const search_action = (query) => (query_for_internal_search.value = query);

    const search_schema = [
      {
        name: "username",
        size: "w200",
        grow: true,
        align: 'left',
        fields: [
          {
            placeholder: " ",
            rule: "icontains",
          },
        ],
      },
      {
        name: "email",
        size: "w300",
        fields: [
          {
            placeholder: "E-mail",
            rule: "icontains",
          },
        ],
      },
      {
        name: "last_name",
        size: "w200",
        grow: true,
        fields: [
          {
            placeholder: "",
            rule: "icontains",
          },
        ],
      },
      {
        name: "first_name",
        grow: true,
        size: "w200",
        fields: [
          {
            placeholder: "",
            rule: "icontains",
          },
        ],
      },
      {
        name: "is_active",
        size: "w30",
        fields: [
          {
            type: "bool",
            placeholder: "?",
            rule: "exact",
          },
        ],
      },
    ];

    // FORM
    const init_obj = {
      username: "",
      email: "",
      first_name: "",
      last_name: "",
      patronomic: "",
      position: "",
      department: "",
      is_active: false,
      access_group_regex: "^$",
      role: "",
      role_inside_group: "",
      role_outside_group: "",
      on_behalf: null,
    };

    const to_repository_convertion = {
    };

    const from_repository_convertion = {
    };

    const { current_id, current, handle_update_attribute } = useCurrentMixin(
      init_obj,
      uow.user_repository,
      from_repository_convertion
    );

    const is_edit_block_valid = computed(() => true);

    const update_action = async () => {
      const message = new SetUser(
        ...[
          "username",
          "is_active",
          "access_group_regex",
          "role",
          "role_inside_group",
          "role_outside_group",
        ].map((k) => convertObject(current.value, to_repository_convertion)[k])
      );
      await MessageBus.handle(message, uow);
    };

    const delete_action = async () => {
      const message = new DeleteUser(
        ...["username"].map(
          (k) => convertObject(current.value, to_repository_convertion)[k]
        )
      );
      await MessageBus.handle(message, uow);
    };

    const role_variants =
      uow.user_folder_settings_repository.get_role_variants();

    const role_options = computed(() =>
      role_variants.value
        ? role_variants.value.map((x) => ({ name: x, value: x }))
        : []
    );

    const form_pages = computed(() => [
      {
        size: "w450",
        fields: [
          {
            type: "FormTextField",
            parameters: { font_weight: "bold" },
            value: " :",
          },
          {
            type: "FormTextField",
            bind: "username",
          },
          {
            type: "FormTextField",
            parameters: { font_weight: "bold" },
            value: "E-mail:",
          },
          {
            type: "FormTextField",
            bind: "email",
          },
          {
            type: "FormTextField",
            parameters: { font_weight: "bold" },
            value: ":",
          },
          {
            type: "FormTextField",
            bind: "last_name",
          },
          {
            type: "FormTextField",
            parameters: { font_weight: "bold" },
            value: ":",
          },
          {
            type: "FormTextField",
            bind: "first_name",
          },
          {
            type: "FormTextField",
            parameters: { font_weight: "bold" },
            value: ":",
          },
          {
            type: "FormTextField",
            bind: "position",
          },
          {
            type: "FormTextField",
            parameters: { font_weight: "bold" },
            value: "",
          },
          {
            type: "FormTextField",
            bind: "department",
          },
          {
            type: "FormTextField",
            parameters: { font_weight: "bold" },
            value: " ?",
          },
          {
            type: "FormBooleanInput",
            parameters: { size: 20 },
            bind: "is_active",
          },
          {
            type: "FormTextField",
            parameters: { font_weight: "bold" },
            value: "   :",
          },
          {
            type: "FormTextInput",
            bind: "access_group_regex",
          },
          {
            type: "FormTextField",
            parameters: { font_weight: "bold" },
            value: " :",
          },
          {
            type: "FormSelectInput",
            parameters: {
              options: role_options.value,
            },
            bind: "role",
          },
          {
            type: "FormTextField",
            parameters: { font_weight: "bold" },
            value: "   :",
          },
          {
            type: "FormSelectInput",
            parameters: {
              options: role_options.value,
            },
            bind: "role_inside_group",
          },
          {
            type: "FormTextField",
            parameters: { font_weight: "bold" },
            value: "   :",
          },
          {
            type: "FormSelectInput",
            parameters: {
              options: role_options.value,
            },
            bind: "role_outside_group",
          },
          {
            type: "FormTextField",
            parameters: { font_weight: "bold" },
            value: "   :",
          },
          {
            type: "FormTextField",
            bind: "on_behalf",
          },
        ],
      },
    ]);
    onMounted(async () => {
      //   a
      await useBeforeEnterPage(uow, ["can_view_user"]);
      await Promise.all([useGetRoles(uow), useGetUsers(uow)]);
    });

    return {
      // ACTION
      search_action,
      update_action,
      delete_action,

      // PERMISSION
      can_change,
      can_delete,

      // EDIT
      current_id,
      current,
      is_edit_block_valid,
      handle_update_attribute,
      form_pages,

      // SEARCH
      search_schema,
      model_objects,
      convert_model_to_item,
      search_time,
      is_searching,
    };
  },
};
</script>

<style scoped>
.page {
  display: flex;
  flex-direction: column;
  padding-left: 60px;
  padding-right: 60px;
}

.page__heading {
  padding-top: 60px;
  padding-bottom: 15px;
}
.page__container {
  display: flex;
}

.page__editblock {
  width: 350px;
  display: flex;
  flex-direction: column;
  padding-right: 15px;
}

.page__searchblock {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding-left: 15px;
  border-left: 1px solid black;
}

.page__button {
  margin: 5px;
}

.page__editrow {
  display: flex;
  width: 100%;
}
</style>
