<template>
  <div class="page">
    <heading-component class="page__heading" text="  " size="middle" />
    <div class="page__container">
      <div class="page__halfcontainer halfcontainer">
        <div class="page__titleblock">
        <form-text-field
          class="halfcontainer__title"
          value=" :"
        />
      </div>
        <div class="halfcontainer__searchblock searchblock">
          <form-text-input
            class="searchblock__searchinput searchinput"
            :value="query_left.last_name"
            :parameters="{placeholder: ''}"
            @update="query_left.last_name = $event"
          />
          <form-text-input
            class="searchblock__searchinput searchinput"
            :value="query_left.first_name"
            :parameters="{placeholder: ''}"
            @update="query_left.first_name = $event"
          />
          <form-text-input
            class="searchblock__searchinput searchinput"
            :value="query_left.username"
            :parameters="{placeholder: ' '}"
            @update:value="query_left.username = $event"
          />
        </div>
        <user-item
          class="searchblock__useritem"
          v-for="user in users_excluded_from_group"
          :key="user.username"
          :obj="user"
          :can_delete="false"
          :can_add="true"
          @add="handle_add(user)"
        />
      </div>
      <div class="page__halfcontainer halfcontainer">
        <div class="page__titleblock">
          <form-text-field
            class="halfcontainer__title"
            value=":"
          />
          <form-select-input
            class="halfcontainer__selectgroup"
            :parameters="{options: groups}"
            :value="group_id.toString()"
            @update="group_id = Number.parseInt($event)"
          />
        </div>
        <div class="halfcontainer__searchblock searchblock">
          <form-text-input
            class="searchblock__searchinput searchinput"
            :parameters="{placeholder: ''}"
            :value="query_right.last_name"
            @update="query_right.last_name = $event"
          />
          <form-text-input
            class="searchblock__searchinput searchinput"
            :parameters="{placeholder: ''}"
            :value="query_right.first_name"
            @update="query_right.first_name = $event"
          />
          <form-text-input
            class="searchblock__searchinput searchinput"
            :value="query_right.username"
            :parameters="{placeholder: ' '}"
            @update="query_right.username = $event"
          />
        </div>
        <user-item
          class="searchblock__useritem"
          v-for="user in users_included_to_group"
          :key="user.username"
          :obj="user"
          :can_delete="true"
          :can_add="false"
          @delete="handle_delete(user)"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { onMounted, computed, reactive, ref, watch } from "vue";
import {
  AddUserToUserGroup,
  DeleteUserFromUserGroup,
} from "../../logic/domain/command";
import { MessageBus } from "../../logic/service_layer/message_bus";
import { VueUnitOfWork } from "../../logic/service_layer/uow";
import HeadingComponent from "../../components/HeadingComponent.vue";
import {
  useBeforeEnterPage,
  useGetUsers,
} from "../../logic/service_layer/use_modules";
import UserItem from "../../components/user/UserItem.vue";
import FormTextInput from "../../components/fields/FormTextInput.vue";
import FormTextField from "../../components/fields/FormTextField.vue";
import FormSelectInput from "../../components/fields/FormSelectInput.vue";
import {
  useGetUserGroups,
  filterArrayOfObjects,
} from "../../logic/service_layer/use_modules";

export default {
  name: "UsersInUserGroupsSettingsPage",
  components: { HeadingComponent, UserItem, FormTextInput, FormTextField, FormSelectInput },

  setup() {
    const uow = new VueUnitOfWork();

    const groups = computed(() => {
      let arr = uow.usergroup_repository.values().map((obj) => {
        return {
          id: obj.value.id,
          name: obj.value.name,
          value: obj.value.id.toString(),
        };
      });

      arr.sort((a, b) => a.name.localeCompare(b.name));
      return arr;
    });

    const group_id = ref(0);

    //    groups,  group_id 
    watch(groups, (newValue) => {
      if (group_id.value === 0 && newValue.length > 0) {
        group_id.value = newValue[0].id;
      }
    });

    const actual_group_usernames = computed(() => {
      if (group_id.value === 0) return [];
      const group = uow.usergroup_repository.get(group_id.value);
      return group.value.users.map((user) => user.username);
    });

    const users_included_to_group = computed(() => {
      let objs = filterArrayOfObjects(
        uow.user_repository.values().map((e) => e.value),
        query_right
      );

      if (objs === null) objs = [];
      objs = objs.filter((obj) =>
        actual_group_usernames.value.includes(obj.username)
      );
      return objs;
    });

    const users_excluded_from_group = computed(() => {
      let objs = filterArrayOfObjects(
        uow.user_repository.values().map((e) => e.value),
        query_left
      );

      if (objs === null) objs = [];
      objs = objs.filter(
        (obj) => !actual_group_usernames.value.includes(obj.username)
      );
      return objs;
    });

    const handle_add = async (user) => {
      await MessageBus.handle(
        new AddUserToUserGroup(group_id.value, user.username),
        uow
      );
    };

    const handle_delete = async (user) => {
      await MessageBus.handle(
        new DeleteUserFromUserGroup(group_id.value, user.username),
        uow
      );
    };

    const query_left = reactive({
      username: "",
      first_name: "",
      last_name: "",
    });

    const query_right = reactive({
      username: "",
      first_name: "",
      last_name: "",
    });

    onMounted(async () => {
      //   a
      await useBeforeEnterPage(uow, ["can_view_usergroup"]);
      await Promise.all([useGetUsers(uow), useGetUserGroups(uow)]);
    });

    return {
      groups,
      group_id,
      actual_group_usernames,
      users_included_to_group,
      users_excluded_from_group,
      query_left,
      query_right,
      handle_add,
      handle_delete,
    };
  },
};
</script>
<style scoped>
.page {
  display: flex;
  flex-direction: column;
  padding-left: 60px;
}

.page__heading {
  padding-top: 60px;
  padding-bottom: 15px;
}

.page__container{
  display: flex;
}

.page__titleblock {
  display: flex;
  padding-left: 8px;
  padding-right: 8px;
}

.halfcontainer__selectgroup {
  margin-left: 5px;
  flex-grow: 1;
}

.halfcontainer {
  width: 300px;
  display: flex;
  flex-direction: column;
}

.searchblock {
  display: flex;
  flex-direction: column;
  padding: 3px;
}

.searchinput {
  margin: 1px 5px;
}
</style>
