import Cookies from "js-cookie";

import { VueBaseRepository } from "./base_repository";

const VueTokenMixin = {
  get_auth_token() {
    return Cookies.get("auth_token");
  },

  set_auth_token(token) {
    Cookies.set("auth_token", token, this._cookie_default);
  },
};

const VueResetMixin = {
  reset() {
    Cookies.remove("auth_token");
  },
};

class VueTokenRepository extends VueBaseRepository {
  constructor(name, keep_auth_token_days_count) {
    super(name);
    this._cookie_default = {
      expires: parseInt(keep_auth_token_days_count),
      sameSite: "strict",
    };
  }
}
Object.assign(VueTokenRepository.prototype, VueTokenMixin, VueResetMixin);

export { VueTokenRepository };
