const UpdateDocumentFilesAction = {
  $schema: "http://json-schema.org/draft-07/schema#",
  type: "object",
  required: ["name", "input"],
  additionalProperties: false,
  properties: {
    name: { const: "UPDATE_DOCUMENT_FILES" },
    input: {
      type: "object",
      required: ["doc_id", "file_ids"],
      additionalProperties: false,
      properties: {
        doc_id: { type: "string" },
        file_ids: {
          oneOf: [
            { type: "string" },
            {
              type: "array",
              items: {
                type: "string",
              },
            },
          ],
        },
      },
    },
  },
};

export { UpdateDocumentFilesAction };
