import { VueBoxRepository } from "./box_repository";

const doc_box_name = "documents";

const VueDocMixin = {
  reset_keeping_refs_for_docs() {
    this.reset_keeping_refs(doc_box_name)
  },

  doc_values() {
    return this.values(doc_box_name);
  },

  set_doc(name, value) {
    this.set(doc_box_name, name, value);
  },

  get_doc(name) {
    this.get(doc_box_name, name);
  },

  delete_doc(name) {
    this.delete(doc_box_name, name);
  },
};

class VueCartRepository extends VueBoxRepository {}
Object.assign(VueCartRepository.prototype, VueDocMixin);

export { VueCartRepository };
