<template>
  <div class="page">
    <div class="page__header">
      <heading-component
        :text="` ${
          action_progress < 100
            ? '(  ' + action_progress.toString() + '%)'
            : ''
        }  ${
          validation_progress < 100
            ? '( ' + validation_progress.toString() + '%)'
            : ''
        }`"
        size="middle"
      />
    </div>
    <div class="page__container" v-show="action_progress === 100">
      <div class="page__searchcontainer">
        <div class="page__buttonrow buttonrow">
          <text-button
            class="buttonrow__button buttonrow__button_left"
            title="  "
            :disabled="selected_item_count === 0"
            @click="perform_action_on_selected_items('add_to_cart')"
          />
          <text-button
            class="buttonrow__button"
            title=""
            :disabled="selected_item_count === 0"
            @click="perform_action_on_selected_items('approve')"
          />
          <text-button
            class="buttonrow__button"
            title=""
            :disabled="selected_item_count === 0"
            @click="perform_action_on_selected_items('reject')"
          />
          <text-button
            class="buttonrow__button"
            title="  "
            :disabled="
              selected_item_count === 0 ||
              is_approval_action_username_chosen === false
            "
            @click="
              perform_action_on_selected_items('delegate_approval_with_return')
            "
          />
          <text-button
            class="buttonrow__button"
            title="  "
            :disabled="
              selected_item_count === 0 ||
              is_approval_action_username_chosen === false
            "
            @click="
              perform_action_on_selected_items(
                'delegate_approval_without_return'
              )
            "
          />
          <form-filtered-select-input
            class="buttonrow__button buttonrow__button_selectuser"
            :parameters="{
              options: user_options,
              placeholder: '...',
              size: 3,
            }"
            :value="approval_action_username"
            @update="approval_action_username = $event"
          />
          <text-button
            class="buttonrow__button"
            title="ZIP"
            :disabled="selected_item_count === 0"
            @click="
              perform_action_on_selected_items('download_files_as_archive')
            "
          />

          <text-button
            class="buttonrow__button buttonrow__button_search"
            title="   ..."
            @click="router.push({ name: 'DocumentApprovalSearchPage' })"
          />
        </div>
        <div class="page__searchblock">
          <form-boolean-input
            class="page__checkboxbutton"
            :value="is_all_selected"
            :parameters="{ size: 30 }"
            @click="handle_select_all_approvals()"
          />
          <form-text-input
            class="page__searchtype"
            :parameters="{ placeholder: '', height: 30 }"
            :value="query.type"
            @update="query.type = $event"
          />
          <form-text-input
            class="page__searchuser"
            :parameters="{
              placeholder: '  ?',
              height: 30,
            }"
            :value="query.from_user"
            @update="query.from_user = $event"
          />
          <form-text-input
            class="page__searchuser"
            :parameters="{ placeholder: '  ?', height: 30 }"
            :value="query.to_user"
            @update="query.to_user = $event"
          />
          <form-text-input
            class="page__searchdescription"
            :value="query.description"
            :parameters="{ placeholdert: '  ?', height: 30 }"
            @update="query.description = $event"
          />
        </div>
        <div class="page__statusrow statusrow" v-if="selected_item_count > 0">
          <form-text-field
            class="statusrow__info"
            :value="
              '***  ' + selected_item_count.toString() + ' . ***'
            "
          />
        </div>
        <div class="page__listrow listrow" v-for="item in items" :key="item">
          <form-text-field
            class="listrow__error"
            :class="{ listrow__error_exist: item.total_count > 1 }"
            :value="item.total_count.toString()"
            :parameters="{
              hint: '   ',
            }"
          />
          <form-text-field
            class="listrow__error"
            :class="{ listrow__error_exist: item.errors.length > 0 }"
            :value="item.errors.length.toString()"
            :parameters="{ hint: '   ' }"
          />
          <form-boolean-input
            class="listrow__checkbox"
            :value="item.id in selected_item_ids"
            :parameters="{ size: 30 }"
            @click="handle_select_approval(item.id)"
          />
          <form-text-field
            class="listrow__type"
            :parameters="{ align: 'center' }"
            :value="item.type"
          />
          <form-text-field
            class="listrow__user"
            :parameters="{ align: 'center' }"
            :value="item.from_user"
          />
          <form-text-field
            class="listrow__user"
            :parameters="{ align: 'center' }"
            :value="item.to_user"
          />
          <router-link class="listrow__href" :to="item.route_obj">
            <form-text-field
              class="listrow__description"
              :value="item.description"
            />
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { onMounted, computed, reactive, ref, watch } from "vue";
import { useRouter } from "vue-router";
import { VueUnitOfWork } from "../logic/service_layer/uow";
import { MessageBus } from "../logic/service_layer/message_bus";
import {
  filterArrayOfObjects,
  useBeforeEnterPage,
  useGetDocumentsWaitingApproval,
  useGetFolders,
  useGetUserGroups,
  useGetUsers,
} from "../logic/service_layer/use_modules";
import HeadingComponent from "../components/HeadingComponent.vue";
import TextButton from "../components/buttons/TextButton.vue";
import FormFilteredSelectInput from "../components/fields/FormFilteredSelectInput.vue";
import FormTextField from "../components/fields/FormTextField.vue";
import FormTextInput from "../components/fields/FormTextInput.vue";
import FormBooleanInput from "../components/fields/FormBooleanInput.vue";
import {
  AddDocumentModel,
  AddDocumentToCart,
  UpdateDocumentApproval,
  DelegateDocumentApproval,
  DownloadDocumentFilesAsArchive,
  ValidateDocument,
} from "../logic/domain/command";

export default {
  components: {
    HeadingComponent,
    FormTextField,
    TextButton,
    FormFilteredSelectInput,
    FormTextInput,
    FormBooleanInput,
  },
  name: "ApprovalsPage",
  setup() {
    const router = useRouter()
    const uow = new VueUnitOfWork();

    const generate_doc_route_obj = (doc) => {
      return {
        name: "DocumentPage",
        params: {
          folder_id: doc.folder_id,
          usergroup_id: doc.usergroup_id,
          document_id: doc.id,
        },
      };
    };

    const convert_doc_approval_to_item = (approval) => {
      return {
        id: approval.id,
        type: "",
        total_count: approval.active_approvals_count_of_document,
        description:
          approval.document.raw_description == ""
            ? " .  ."
            : approval.document.raw_description,
        from_user:
          approval.from_last_name +
          " " +
          approval.from_first_name +
          " (" +
          approval.from_username +
          ")",
        to_user:
          approval.to_last_name +
          " " +
          approval.to_first_name +
          " (" +
          approval.to_username +
          ")",
        route_obj: generate_doc_route_obj(approval.document),
        errors: approval.document.errors,
        actions: {
          add_to_cart: async () =>
            await MessageBus.handle(
              new AddDocumentToCart(
                approval.document.folder_id,
                approval.document.usergroup_id,
                approval.document.id
              ),
              uow
            ),
          approve: async () => {
            await MessageBus.handle(
              new UpdateDocumentApproval(
                approval.document.folder_id,
                approval.document.usergroup_id,
                approval.document.id,
                approval.id,
                "APPROVED",
                ""
              ),
              uow
            );
          },
          reject: async () => {
            await MessageBus.handle(
              new UpdateDocumentApproval(
                approval.document.folder_id,
                approval.document.usergroup_id,
                approval.document.id,
                approval.id,
                "REJECTED",
                ""
              ),
              uow
            );
          },
          delegate_approval_with_return: async () =>
            await MessageBus.handle(
              new DelegateDocumentApproval(
                approval.document.folder_id,
                approval.document.usergroup_id,
                approval.document.id,
                approval.id,
                approval_action_username.value,
                true
              ),
              uow
            ),
          delegate_approval_without_return: async () =>
            await MessageBus.handle(
              new DelegateDocumentApproval(
                approval.document.folder_id,
                approval.document.usergroup_id,
                approval.document.id,
                approval.id,
                approval_action_username.value,
                false
              ),
              uow
            ),
          download_files_as_archive: async () =>
            await MessageBus.handle(
              new DownloadDocumentFilesAsArchive(
                approval.document.folder_id,
                approval.document.usergroup_id,
                approval.document.id,
                approval.document.files
                  .map((x) => x.size)
                  .reduce((a, b) => a + b, 0)
              ),
              uow
            ),
        },
      };
    };

    const doc_approvals = computed(() =>
      uow.waiting_doc_approval_repository
        .values()
        .map((obj) => convert_doc_approval_to_item(obj.value))
    );

    const query = reactive({
      type: "",
      from_user: "",
      to_user: "",
      description: "",
    });

    const filter_objs = (objs, query) =>
      // false -    
      filterArrayOfObjects(objs, query, false).sort((a, b) =>
        a.description.localeCompare(b.description)
      );

    const items = computed(() => filter_objs(doc_approvals.value, query));

    // SELECTION
    const selected_item_ids = ref({});
    const selected_item_count = computed(
      () =>
        Object.values(selected_item_ids.value).filter((x) => x === true).length
    );

    const handle_select_approval = (id) => {
      if (id in selected_item_ids.value)
        selected_item_ids.value[id] = !selected_item_ids.value[id];
      else selected_item_ids.value[id] = true;
    };
    const reset_selection = () => {
      is_all_selected.value = false;
      selected_item_ids.value = {};
    };
    watch(
      () => doc_approvals.value,
      () => reset_selection()
    );

    const is_all_selected = ref(false);
    const handle_select_all_approvals = () => {
      if (is_all_selected.value === true) {
        reset_selection();
        is_all_selected.value = false;
      } else {
        is_all_selected.value = true;
        items.value.forEach(
          (item) => (selected_item_ids.value[item.id] = true)
        );
      }
    };

    // USERS
    const users = computed(() =>
      uow.user_repository.values().map((e) => e.value)
    );
    const user_options = computed(() =>
      [{ name: " ", value: "NOT_CHOSEN" }].concat(
        users.value
          .filter((e) => e.is_active === true)
          .map((e) => {
            return {
              name: (
                e.last_name +
                " " +
                e.first_name +
                " (" +
                e.username +
                ")"
              ).trim(),
              value: e.username,
            };
          })
          .sort((a, b) => a.name.localeCompare(b.name))
      )
    );

    const approval_action_username = ref("NOT_CHOSEN");
    const is_approval_action_username_chosen = computed(
      () => approval_action_username.value !== "NOT_CHOSEN"
    );

    // ACTION
    const action_progress = ref(100);

    const perform_action_on_selected_items = async (action_name) => {
      action_progress.value = 0;
      let actions_executed = 0;

      const actions = items.value
        .filter(
          (item) =>
            item.id.toString() in selected_item_ids.value &&
            selected_item_ids.value[item.id.toString()] === true
        )
        .filter((item) => item.actions[action_name])
        .map((item) => item.actions[action_name]);

      //   30 
      let start = 0;
      const size = 30;

      while (start < actions.length) {
        await Promise.all(
          actions.slice(start, start + size).map(async (f) => {
            await f();
            actions_executed++;
            action_progress.value = Math.round(
              (actions_executed / actions.length) * 100
            );
          })
        );

        start += size;
      }
    };

    // VALIDATION
    const validation_progress = ref(100); // TODO

    onMounted(async () => {
      useBeforeEnterPage(uow, ["can_get_folders"]); //   a
      await Promise.all([useGetFolders(uow), useGetUserGroups(uow)]);
      await Promise.all([
        useGetDocumentsWaitingApproval(uow),
        useGetUsers(uow),
      ]);

      //   
      const actions = uow.waiting_doc_approval_repository
        .values()
        .map((obj) => obj.value.document)
        .map((doc) => {
          const folder = uow.folder_repository.get(doc.folder_id); // Ref
          folder.value.is_necessary_validate = true;
          doc.description_pattern = folder.value.description_pattern;

          return async () => {
            await MessageBus.handle(
              new AddDocumentModel(doc.folder_id, doc.usergroup_id, doc, false),
              uow
            );
            await MessageBus.handle(
              new ValidateDocument(doc.folder_id, doc.usergroup_id, doc.id),
              uow
            );
          };
        });

      //   6 
      let start = 0;
      const size = 6;

      let actions_executed = 0;
      while (start < actions.length) {
        await Promise.all(
          actions.slice(start, start + size).map(async (f) => {
            await f();
            actions_executed++;
            validation_progress.value = Math.round(
              (actions_executed / actions.length) * 100
            );
          })
        );

        start += size;
      }
    });

    return {
      router,
      doc_approvals,
      generate_doc_route_obj,
      items,
      query,

      // SELECTION
      selected_item_ids,
      selected_item_count,
      handle_select_approval,
      reset_selection,
      is_all_selected,
      handle_select_all_approvals,

      // USERS
      users,
      user_options,
      approval_action_username,
      is_approval_action_username_chosen,

      // ACTION
      action_progress,
      perform_action_on_selected_items,

      // VALIDATION
      validation_progress,
    };
  },
};
</script>

<style scoped>
.page {
  display: flex;
  flex-direction: column;
  padding-left: 60px;
  padding-right: 60px;
}

.page__header {
  display: flex;
  padding-top: 60px;
  padding-bottom: 15px;
}

.page__container {
  display: flex;
}

.page__searchcontainer {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
}

.page__buttonrow {
  display: flex;
}

.buttonrow__button {
  padding: 3px;
}

.buttonrow__button_left {
  margin-left: 60px;
}

.buttonrow__button_selectuser {
  max-width: 400px;
}

.buttonrow__button_search {
  margin-left: 50px;
}

.page__searchblock {
  display: flex;
}

.listrow {
  display: flex;
  align-items: center;
  width: 100%;
  min-height: 33px;
}

.page__checkboxbutton {
  margin-left: 60px;
}

.page__checkboxbutton,
.listrow__checkbox {
  padding: 3px;
}

.listrow__error {
  min-width: 30px;
  font-weight: bold;
  color: green;
  cursor: pointer;
}

.listrow__error_exist {
  color: red;
}

.page__searchtype,
.listrow__type {
  min-width: 150px;
  width: 150px;
  padding: 3px;
}

.page__searchdescription,
.listrow__description {
  flex-grow: 1;
  padding: 3px;
}

.page__searchuser,
.listrow__user {
  min-width: 400px;
  width: 400px;
  padding: 3px;
}

.statusrow {
  display: flex;
  justify-content: center;
  padding: 3px;
}

.statusrow__info {
  color: red;
}
</style>
            