import { AttributeRegexValidationConstraint } from "./validation/attribute_regex_validation_constraint";
import { AttributeTogetherRegexValidationConstraint } from "./validation/attribute_together_regex_validation_constraint";
import { UniqueTogetherConstraint } from "./validation/unique_together_constraint";
import { AttributeHasPreviousValueConstraint } from "./validation/attribute_has_previous_value_constraint";
import { HasFileConstraint } from "./validation/has_file_constraint";
import { HasApprovalConstraint } from "./validation/has_approval_constraint";
import { SingleUnionConstraint } from "./validation/single_union_constraint";
import { FilenameMatchPatternConstraint } from "./validation/filename_match_pattern_constraint";

import { DocumentAttributesContext } from "./form/context/document_attributes_context";
import { PreviousDocumentsAttributeContext } from "./form/context/previous_documents_attribute_context";
import { UsersEmailsContext } from "./form/context/users_emails_context";

import { FormDateInput } from "./form/field/form_date_input";
import { FormEmailToAddressField } from "./form/field/form_email_to_address_field";
import { FormEmailToAddressInput } from "./form/field/form_email_to_address_input";
import { FormHtmlField } from "./form/field/form_html_field";
import { FormHtmlInput } from "./form/field/form_html_input";
import { FormTextField } from "./form/field/form_text_field";
import { FormTextInput } from "./form/field/form_text_input";
import { FormHiddenInput } from "./form/field/form_hidden_input";
import { FormSelectInput } from "./form/field/form_select_input";
import { FormFilteredSelectInput } from "./form/field/form_filtered_select_input";

const FormFields = {
  type: "array",
  items: {
    oneOf: [
      FormDateInput,
      FormEmailToAddressField,
      FormEmailToAddressInput,
      FormHtmlField,
      FormHtmlInput,
      FormTextField,
      FormTextInput,
      FormHiddenInput,
      FormSelectInput,
      FormFilteredSelectInput,
    ],
  },
};

const FolderSettings = {
  type: "object",
  properties: {
    version: { type: "string" },
    validation_constraints: {
      type: "array",
      items: {
        oneOf: [
          AttributeRegexValidationConstraint,
          AttributeTogetherRegexValidationConstraint,
          UniqueTogetherConstraint,
          AttributeHasPreviousValueConstraint,
          HasFileConstraint,
          HasApprovalConstraint,
          SingleUnionConstraint,
          FilenameMatchPatternConstraint,
        ],
      },
    },
    search_schema: {
      type: "array",
      items: {
        type: "object",
        properties: {
          name: { type: "string" },
          description: { type: "string" },
          size: {
            enum: [
              "w50",
              "w100",
              "w150",
              "w200",
              "w250",
              "w300",
              "w350",
              "w400",
            ],
          },
          grow: { type: "boolean" },
          align: { enum: ["left", "center", "right"] },
          default: { type: ["string", "integer"] },
          is_disabled: { type: "boolean" },
          sort_rule: {
            enum: ["DEFAULT", "INTEGER", "STRING", "CHANGE_ORDER_NUMBER"],
          },
          fields: {
            type: "array",
            items: {
              type: "object",
              properties: {
                placeholder: { type: "string" },
                rule: {
                  enum: [
                    "exact",
                    "iexact",
                    "contains",
                    "icontains",
                    "gt",
                    "gte",
                    "lt",
                    "lte",
                  ],
                },
                re: { type: "string" },
                converter: { type: "string" },
              },
              required: ["placeholder", "rule"],
              additionalProperties: false,
            },
          },
        },
        required: ["name", "fields", "description"],
        additionalProperties: false,
      },
    },
    form_schema: {
      type: "object",
      properties: {
        context: {
          type: "array",
          items: {
            oneOf: [
              DocumentAttributesContext,
              PreviousDocumentsAttributeContext,
              UsersEmailsContext,
            ],
          },
        },
        pages: {
          type: "array",
          items: {
            type: "object",
            properties: {
              size: { type: "integer" },
              name: { type: "string" },
              fields: {
                type: "object",
                properties: {
                  read_mode: FormFields,
                  edit_mode: FormFields,
                },
                additionalProperties: false,
              },
            },
            required: ["fields"],
            additionalProperties: false,
          },
        },
      },
      required: ["pages"],
      additionalProperties: false,
    },
    max_file_size: { type: "integer" },
    filename_pattern: { type: "string" },
    description_pattern: { type: "string" },
    is_empty_search_allowed: { type: "boolean" },
    sort_by: {
      type: "object",
      properties: { name: { type: "string" }, asc: { type: "boolean" } },
      required: ["name", "asc"],
      additionalProperties: false,
    },
    group_by: {
      type: "object",
      properties: {
        button_active_name: { type: "string" },
        button_passive_name: { type: "string" },
        fields: { type: "array", items: { type: "string" } },
        order_by: { type: "string" },
        aggregate_function: { enum: ["MAX"] },
        is_active: { type: "boolean" },
      },
      required: [
        "button_active_name",
        "button_passive_name",
        "fields",
        "order_by",
        "aggregate_function",
      ],
      additionalProperties: false,
    },

    is_list_container_required: { type: "boolean" },
    is_doc_container_required: { type: "boolean" },
    doc_container_parsers: {
      type: "array",
      items: { enum: ["1868_amm_transmittal", "1882_dlenc_transmittal"] },
    },
    action_buttons: {
      type: "array",
      items: {
        type: "object",
        required: ["caption", "action"],
        additionalProperties: false,
        properties: {
          caption: { type: "string" },
          action: { type: "string" },
          type: {
            enum: ["primary", "secondary", "success", "warning", "danger"],
          },
          allowed_usernames: {
            type: "array",
            items: { type: "string" },
          },
          required_approvals: {
            type: "object",
            additionalProperties: false,
            properties: {
              all: {
                type: "array",
                items: { type: "string" },
              },
              anyOf: {
                type: "array",
                items: { type: "string" },
              },
            },
          },
        },
      },
    },
    search_action_buttons: {
      type: "array",
      items: {
        type: "object",
        required: ["caption", "handler"],
        additionalProperties: false,
        properties: {
          caption: { type: "string" },
          handler: { enum: ["1882_dlenc_transmittal"] },
          allowed_usernames: {
            type: "array",
            items: { type: "string" },
          },
        },
      },
    },
    approval_rules: {
      type: "object",
      additionalProperties: false,
      properties: {
        filename: {
          type: "object",
          properties: {
            pattern: { type: "string" },
            case_insensitive: { type: "boolean" },
          },
          required: ["pattern"],
          additionalProperties: false,
        },
        can_approve: {
          type: "array",
          items: { type: "string" },
        },
        can_reject: {
          type: "array",
          items: { type: "string" },
        },
        can_delegate_with_return: {
          type: "array",
          items: { type: "string" },
        },
        can_delegate_without_return: {
          type: "array",
          items: { type: "string" },
        },
      },
    },
    mode: { enum: ["view", "edit"] },
    strict_validation: { type: "array", items: { type: "string" } },
  },
  required: [
    "validation_constraints",
    "search_schema",
    "max_file_size",
    "filename_pattern",
    "description_pattern",
  ],
  additionalProperties: false,
};

export { FolderSettings };
