<template>
  <div class="page">
    <heading-component
      class="page__heading"
      text=" "
      size="middle"
    />
    <div class="page__container">
      <div class="page__halfcontainer halfcontainer">
        <div class="page__titleblock">
          <form-text-field
            class="halfcontainer__title"
            value=" :"
          />
        </div>
        <div class="halfcontainer__searchblock searchblock">
          <form-text-input
            class="searchblock__searchinput searchinput"
            :value="query_left.last_name"
            :parameters="{ placeholder: '' }"
            @update="query_left.last_name = $event"
          />
          <form-text-input
            class="searchblock__searchinput searchinput"
            :value="query_left.first_name"
            :parameters="{ placeholder: '' }"
            @update="query_left.first_name = $event"
          />
          <form-text-input
            class="searchblock__searchinput searchinput"
            :value="query_left.username"
            :parameters="{ placeholder: ' ' }"
            @update:value="query_left.username = $event"
          />
        </div>
        <user-item
          class="searchblock__useritem"
          v-for="user in users_excluded_from_replacements"
          :key="user.username"
          :obj="user"
          :can_delete="false"
          :can_add="true"
          @add="handle_add(user)"
        />
      </div>
      <div class="page__halfcontainer halfcontainer">
        <div class="page__titleblock">
          <form-text-field class="halfcontainer__title" value=":" />
          <form-select-input
            class="halfcontainer__selectgroup"
            :parameters="{ options: users }"
            :value="current_username"
            @update="current_username = $event"
          />
        </div>
        <div class="halfcontainer__searchblock searchblock">
          <form-text-input
            class="searchblock__searchinput searchinput"
            :parameters="{ placeholder: '' }"
            :value="query_right.last_name"
            @update="query_right.last_name = $event"
          />
          <form-text-input
            class="searchblock__searchinput searchinput"
            :parameters="{ placeholder: '' }"
            :value="query_right.first_name"
            @update="query_right.first_name = $event"
          />
          <form-text-input
            class="searchblock__searchinput searchinput"
            :value="query_right.username"
            :parameters="{ placeholder: ' ' }"
            @update="query_right.username = $event"
          />
        </div>
        <user-item
          class="searchblock__useritem"
          v-for="user in users_included_in_replacements"
          :key="user.username"
          :obj="user"
          :can_delete="true"
          :can_add="false"
          @delete="handle_delete(user)"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { onMounted, computed, reactive, ref, watch } from "vue";
import {
  GetUserReplacementsOfUser,
  AddUserReplacementToUser,
  DeleteUserReplacementFromUser,
} from "../../logic/domain/command";
import { MessageBus } from "../../logic/service_layer/message_bus";
import { VueUnitOfWork } from "../../logic/service_layer/uow";
import HeadingComponent from "../../components/HeadingComponent.vue";
import {
  useBeforeEnterPage,
  useGetUsers,
} from "../../logic/service_layer/use_modules";
import UserItem from "../../components/user/UserItem.vue";
import FormTextInput from "../../components/fields/FormTextInput.vue";
import FormTextField from "../../components/fields/FormTextField.vue";
import FormSelectInput from "../../components/fields/FormSelectInput.vue";
import { filterArrayOfObjects } from "../../logic/service_layer/use_modules";

export default {
  name: "UserReplacementsPage",
  components: {
    HeadingComponent,
    UserItem,
    FormTextInput,
    FormTextField,
    FormSelectInput,
  },

  setup() {
    const uow = new VueUnitOfWork();

    const users = computed(() => {
      let arr = uow.user_repository.values().map((obj) => {
        return {
          id: obj.value.username,
          name: obj.value.last_name + " " + obj.value.first_name,
          value: obj.value.username,
        };
      });

      arr.sort((a, b) => a.name.localeCompare(b.name));
      return arr;
    });

    const current_username = ref(null);

    //    users,  current_username 
    watch(users, (newValue) => {
      if (current_username.value === null && newValue.length > 0) {
        current_username.value = newValue[0].id;
      }
    });

    //    current_username,     
    watch(current_username, () => handle_get());

    const current_user_replacements = computed(() => {
      if (current_username.value === null) return [];
      return uow.user_replacement_repository
        .values(current_username.value)
        .map((e) => e.value.username);
    });

    const filter_users = (query, condition_func) => {
      let objs = filterArrayOfObjects(
        uow.user_repository.values().map((e) => e.value),
        query
      );

      if (objs === null) objs = [];
      objs = objs.filter((obj) => condition_func(obj.username));
      objs.sort((a, b) => a.last_name.localeCompare(b.last_name));
      return objs;
    };

    const users_included_in_replacements = computed(() =>
      filter_users(query_right, (x) =>
        current_user_replacements.value.includes(x)
      )
    );

    const users_excluded_from_replacements = computed(() =>
      filter_users(
        query_left,
        (x) => !current_user_replacements.value.includes(x)
      )
    );

    // const users_included_in_replacements = computed(() => {
    //   let objs = filterArrayOfObjects(
    //     uow.user_repository.values().map((e) => e.value),
    //     query_right
    //   );

    //   if (objs === null) objs = [];
    //   objs = objs.filter((obj) =>
    //     current_user_replacements.value.includes(obj.username)
    //   );
    //   objs.sort((a, b) => a.last_name.localeCompare(b.last_name));
    //   return objs;
    // });

    // const users_excluded_from_replacements = computed(() => {
    //   let objs = filterArrayOfObjects(
    //     uow.user_repository.values().map((e) => e.value),
    //     query_left
    //   );

    //   if (objs === null) objs = [];
    //   objs = objs.filter(
    //     (obj) => !current_user_replacements.value.includes(obj.username)
    //   );
    //   objs.sort((a, b) => a.last_name.localeCompare(b.last_name));
    //   return objs;
    // });

    const handle_get = async () => {
      await MessageBus.handle(
        new GetUserReplacementsOfUser(current_username.value),
        uow
      );
    };

    const handle_add = async (user) => {
      await MessageBus.handle(
        new AddUserReplacementToUser(current_username.value, user.username),
        uow
      );
    };

    const handle_delete = async (user) => {
      await MessageBus.handle(
        new DeleteUserReplacementFromUser(
          current_username.value,
          user.username
        ),
        uow
      );
    };

    const query_left = reactive({
      username: "",
      first_name: "",
      last_name: "",
    });

    const query_right = reactive({
      username: "",
      first_name: "",
      last_name: "",
    });

    onMounted(async () => {
      //   a
      await useBeforeEnterPage(uow, ["can_view_user"]);
      await Promise.all([useGetUsers(uow)]);
    });

    return {
      users,
      current_username,
      current_user_replacements,
      users_included_in_replacements,
      users_excluded_from_replacements,
      query_left,
      query_right,
      handle_add,
      handle_delete,
    };
  },
};
</script>
<style scoped>
.page {
  display: flex;
  flex-direction: column;
  padding-left: 60px;
}

.page__heading {
  padding-top: 60px;
  padding-bottom: 15px;
}

.page__container {
  display: flex;
}

.page__titleblock {
  display: flex;
  padding-left: 8px;
  padding-right: 8px;
}

.halfcontainer__selectgroup {
  margin-left: 5px;
  flex-grow: 1;
}

.halfcontainer {
  width: 300px;
  display: flex;
  flex-direction: column;
}

.searchblock {
  display: flex;
  flex-direction: column;
  padding: 3px;
}

.searchinput {
  margin: 1px 5px;
}
</style>
