<template>
  <div class="page">
    <heading-component
      class="page__heading"
      text="  "
      size="middle"
    />
    <div class="page__container">
      <document-page-search-row
        class="page__searchrow"
        :search_schema="search_schema"
        :required_space_for_link="true"
        @search:docs="handle_search_action($event)"
      />
      <document-page-sort-row
        class="page__sortrow"
        :search_schema="search_schema"
        :sort_asc="sort_asc"
        :sort_by="sort_by"
        @toggle:direction="sort_asc = !sort_asc"
        @sort:by="[sort_by, sort_rule] = $event"
      />
      <document-search-status-row
        class="page__searchstatus"
        :count="Math.max(search_count, items.length)"
        :time="search_time"
        :min_index="pagination_min_index"
        :max_index="pagination_max_index"
        :is_searching="is_searching"
      />
      <document-pagination-row
        class="page__paginationrow"
        v-if="items.length > 0"
        :total_count="Math.max(search_count, items.length)"
        :count_per_page="pagination_count_per_page"
        :current_page="pagination_current_page"
        :variants="pagination_variants"
        :is_intermediate_part_shown="is_intermediate_pages_shown"
        @set:page_range="handle_set_page_range($event.min, $event.max)"
        @select:page="pagination_current_page = $event"
        @set:count_per_page="pagination_count_per_page = $event"
        @show:pages="is_intermediate_pages_shown = true"
      />
      <div class="page__listrow" v-for="obj in items" :key="obj.id">
        <document-attribute-row
          class="page__attributerow"
          :id="obj.id"
          :search_schema="search_schema"
          :has_link="true"
          :link_route_obj="obj.link_to_single_page"
          :attributes="obj.attributes"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { computed, onMounted, ref, watch } from "vue";
import { GetManyDocumentsApprovals } from "../../logic/domain/command";
import { VueUnitOfWork } from "../../logic/service_layer/uow";
import {
  useBeforeEnterPage,
  convertDateToDateTimeString,
  useExecutionTimeDecorator,
  useGetUsers,
  useGetUserGroups,
} from "../../logic/service_layer/use_modules";

import HeadingComponent from "../../components/HeadingComponent.vue";
import DocumentPageSearchRow from "../../components/document/DocumentPageSearchRow.vue";
import DocumentPageSortRow from "../../components/document/DocumentPageSortRow.vue";
import DocumentPaginationRow from "../../components/document/DocumentPaginationRow.vue";
import DocumentAttributeRow from "../../components/document/DocumentAttributeRow.vue";
import DocumentSearchStatusRow from "../../components/document/DocumentSearchStatusRow.vue";

export default {
  components: {
    HeadingComponent,
    DocumentPageSearchRow,
    DocumentAttributeRow,
    DocumentPageSortRow,
    DocumentPaginationRow,
    DocumentSearchStatusRow,
  },
  name: "DocumentApprovalSearchPage",

  setup() {
    const uow = new VueUnitOfWork();

    // USER
    const user = uow.user_repository.get_current();

    const users = computed(() =>
      uow.user_repository
        .values()
        .map((e) => e.value)
        .filter((e) => e.is_active === true)
    );

    const user_options = computed(() =>
      users.value
        .map((e) => {
          return {
            name: make_full_name(e.last_name, e.first_name, e.username),
            value: e.username,
          };
        })
        .sort((a, b) => a.name.localeCompare(b.name))
    );

    // USERGROUP
    const usergroup_options = computed(() => {
      let arr = uow.usergroup_repository
        .values()
        .filter(
          (obj) =>
            !obj.value.is_private || obj.value.has_username(user.value.username)
        )
        .map((obj) => {
          return {
            name: obj.value.name,
            value: obj.value.id.toString(),
          };
        });

      arr.sort((a, b) => a.name.localeCompare(b.name));
      return arr;
    });

    // ITEMS
    const make_full_name = (surname, name, username) =>
      surname + " " + name + " (" + username + ")";

    const convert_state = (x) => {
      switch (x) {
        case "APPROVED":
          return "";
        case "REJECTED":
          return "";
        case "DELEGATED":
          return "";
        case "NONE":
          return " ";
      }
    };

    const convert_usergroup = (usergroup_id) => {
      const group = uow.usergroup_repository.get(usergroup_id);
      return group ? group.value.name : usergroup_id.toString();
    };

    const convert_model_to_item = (obj) => {
      return {
        id: obj.id,
        attributes: {
          id: obj.id.toString(),
          USERGROUP: convert_usergroup(obj.document.usergroup_id),
          STATE: convert_state(obj.state),
          FROM_USER: make_full_name(
            obj.from_last_name,
            obj.from_first_name,
            obj.from_username
          ),
          TO_USER: make_full_name(
            obj.to_last_name,
            obj.to_first_name,
            obj.to_username
          ),
          DESCRIPTION: obj.document.raw_description,
          UPDATED_AT: convertDateToDateTimeString(obj.updated_at),
        },
        link_to_single_page: {
          name: "DocumentPage",
          params: {
            folder_id: obj.document.folder_id,
            usergroup_id: obj.document.usergroup_id,
            document_id: obj.document.id,
          },
        },
        actions: {},
      };
    };

    const items = computed(() =>
      uow.search_doc_approval_repository
        .values()
        .map((obj) => convert_model_to_item(obj.value))
    );

    // STATUS
    const search_time = ref(0);
    const is_searching = ref(false);

    // SEARCH
    const current_id = ref(0);
    const current_query = ref({});

    const handle_search_action = async (query) => {
      //   ,   
      if (query !== current_query.value) reset_pagination();

      current_query.value = query;
      await search();
    };

    const search = async () => {
      if (is_searching.value) return;

      await useExecutionTimeDecorator(
        uow,
        new GetManyDocumentsApprovals(
          current_query.value,
          pagination_count_per_page.value * (pagination_current_page.value - 1),
          pagination_count_per_page.value,
          sort_by.value,
          sort_asc.value
        ),
        search_time,
        is_searching
      );
    };

    const search_count = uow.search_doc_approval_repository.get_count(); // Ref

    const search_schema = computed(() => [
      {
        name: "USERGROUP",
        size: "w200",
        fields: [
          {
            type: "select",
            options: [{ name: ".  ?", value: "" }].concat(
              usergroup_options.value
            ),
            default: "",
            rule: "exact",
          },
        ],
      },
      {
        name: "FROM_USER",
        size: "w350",
        fields: [
          {
            type: "select",
            options: [{ name: "  ?", value: "" }].concat(
              user_options.value
            ),
            default: "",
            rule: "exact",
          },
        ],
      },
      {
        name: "TO_USER",
        size: "w350",
        fields: [
          {
            type: "select",
            options: [{ name: " ?", value: "" }].concat(user_options.value),
            default: "",
            rule: "exact",
          },
        ],
      },
      {
        name: "STATE",
        size: "w150",
        fields: [
          {
            type: "select",
            options: [
              { name: " ", value: "" },
              { name: " ", value: "NONE" },
              { name: "", value: "APPROVED" },
              { name: "", value: "REJECTED" },
              { name: "", value: "DELEGATED" },
            ],
            default: "",
            rule: "exact",
          },
        ],
      },
      {
        name: "DESCRIPTION",
        size: "w200",
        align: "left",
        grow: true,
        fields: [
          {
            placeholder: "",
            rule: "icontains",
          },
        ],
      },
      {
        name: "UPDATED_AT",
        size: "w150",
        fields: [
          {
            placeholder: " ()",
            rule: "gte",
            re: "^\\d{2}\\.\\d{2}\\.\\d{4}$",
            converter: "date_to_iso_string",
          },
          {
            placeholder: " ()",
            rule: "lte",
            re: "^\\d{2}\\.\\d{2}\\.\\d{4}$",
            converter: "date_to_iso_string",
          },
        ],
      },
    ]);

    // SORTING
    const sort_by = ref("UPDATED_AT");
    const sort_rule = ref("DEFAULT");
    const sort_asc = ref(false);

    watch(sort_by, async () => await search());
    watch(sort_asc, async () => await search());
    watch(sort_rule, async () => await search());

    // PAGINATION
    const is_intermediate_pages_shown = ref(false);
    const default_count_per_page = 20;
    const default_current_page = 1;
    const pagination_variants = ref([20, 50, 100]);

    const pagination_count_per_page = ref(0);
    const pagination_current_page = ref(0);
    const pagination_min_index = ref(0);
    const pagination_max_index = ref(0);

    const handle_set_page_range = async (min, max) => {
      pagination_min_index.value = min;
      pagination_max_index.value = max;
    };

    const reset_pagination = () => {
      pagination_count_per_page.value = default_count_per_page;
      pagination_current_page.value = default_current_page;
      pagination_min_index.value =
        (default_current_page - 1) * default_count_per_page;
      pagination_max_index.value =
        default_current_page * default_count_per_page;
    };

    reset_pagination();

    watch(pagination_count_per_page, async () => await search({}));
    watch(pagination_current_page, async () => await search({}));

    onMounted(async () => {
      //   a
      await useBeforeEnterPage(uow, ["can_search_documentapproval"]);
      await Promise.all([useGetUsers(uow), useGetUserGroups(uow)]);
    });

    return {
      //USER
      user_options,

      // USERGROUP
      usergroup_options,

      // ACTION
      handle_search_action,
      current_id,

      // SEARCH
      search_schema,
      items,
      convert_model_to_item,
      search_time,
      is_searching,
      search_count,
      current_query,

      // SORTING
      sort_by,
      sort_rule,
      sort_asc,

      // PAGINATION
      is_intermediate_pages_shown,
      handle_set_page_range,
      pagination_variants,
      pagination_count_per_page,
      pagination_current_page,
      pagination_min_index,
      pagination_max_index,
    };
  },
};
</script>

<style scoped>
.page {
  display: flex;
  flex-direction: column;
  padding-left: 60px;
  padding-right: 60px;
}

.page__heading {
  padding-top: 60px;
  padding-bottom: 15px;
}

.page__searchbutton {
  margin-left: 25px;
  margin-bottom: 15px;
}

.page__container {
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.page__listrow:nth-child(even) {
  background-color: rgb(215, 242, 255);
}
</style>
