import { VueBoxRepository } from "./box_repository";
import { CountMixin } from "./mixins/count";

const VueDocMixin = {
  reset(folder_id) {
    this.keys(folder_id).value.forEach((key) => this.delete(folder_id, key));
    this.set_count(0);
  },

  get_union(folder_id, doc_id) {
    const doc = this.get(folder_id, doc_id).value;
    return doc.union;
  },

  set_union(folder_id, doc_id, union_id) {
    const doc = this.get(folder_id, doc_id).value;
    if (doc !== null) doc.union = union_id;
  },

  set_attribute(folder_id, doc_id, name, value, to_be_decoded) {
    const doc = this.get(folder_id, doc_id).value;
    if (doc !== null) doc.set_attribute(name, value, to_be_decoded);
  },

  mark_as_modified(folder_id, doc_id) {
    const doc = this.get(folder_id, doc_id).value;
    doc.modify();
  },

  mark_as_saved(folder_id, doc_id) {
    const doc = this.get(folder_id, doc_id).value;
    doc.save();
  },

  mark_all_as_saved(folder_id) {
    this.values(folder_id).forEach((doc) => doc.value.save());
  },

  mark_as_deleted(folder_id, doc_id) {
    const doc = this.get(folder_id, doc_id).value;
    doc.delete();
  },

  mark_as_not_deleted(folder_id, doc_id) {
    const doc = this.get(folder_id, doc_id).value;
    doc.recover();
  },

  uploaded(folder_id, doc_id) {
    const doc = this.get(folder_id, doc_id).value;
    return doc.files.every((file) => file.uploaded);
  },

  is_saved(folder_id, doc_id) {
    const doc = this.get(folder_id, doc_id).value;
    return doc.is_saved;
  },

  is_deleted(folder_id, doc_id) {
    const doc = this.get(folder_id, doc_id).value;
    return doc.is_deleted;
  },

  is_blocked(folder_id, doc_id) {
    const doc = this.get(folder_id, doc_id).value;
    return doc.is_blocked;
  },

  is_created_first_time(folder_id, doc_id) {
    const doc = this.get(folder_id, doc_id).value;
    return doc.is_created_first_time;
  },

  raw_attributes(folder_id, doc_id) {
    const doc = this.get(folder_id, doc_id).value;
    return doc.raw_attributes;
  },

  attributes(folder_id, doc_id) {
    const doc = this.get(folder_id, doc_id).value;
    return doc.attributes;
  },

  files(folder_id, doc_id) {
    const doc = this.get(folder_id, doc_id).value;
    return doc.files;
  },

  get_file(folder_id, doc_id, file_id) {
    // console.log('get_file', folder_id, doc_id, file_id);
    const doc = this.get(folder_id, doc_id).value;
    // console.log(doc)
    return doc.get_file(file_id);
  },

  add_file(folder_id, doc_id, file) {
    const doc = this.get(folder_id, doc_id).value;
    doc.add_file(file.id, file);
  },

  remove_file(folder_id, doc_id, file_id) {
    const doc = this.get(folder_id, doc_id).value;
    doc.remove_file(file_id);
  },

  file_in_repository(folder_id, file_id) {
    const is_doc_has_not_file = (doc) =>
      doc.files.every((file) => file.id != file_id);

    return !this.values(folder_id)
      .map((obj) => obj.value)
      .every(is_doc_has_not_file);
  },

  add_error(folder_id, doc_id, errors_container_id, error_text) {
    // console.log('ADD ERROR')
    // console.log(folder_id, doc_id, errors_container_id, error_text)

    const doc = this.get(folder_id, doc_id).value;
    doc.add_error(errors_container_id, error_text);
  },

  add_error_easy(folder_id, doc_id, error_text) {
    // console.log('ADD ERROR EASY')
    // console.log(folder_id, doc_id, error_text)

    const doc = this.get(folder_id, doc_id).value;
    doc.add_error(doc.errors_container_id, error_text);
  },

  errors_container_id(folder_id, doc_id) {
    const doc = this.get(folder_id, doc_id).value;
    return doc.errors_container_id;
  },

  reset_errors(folder_id, doc_id) {
    // console.log('RESET ERRORS')

    const doc = this.get(folder_id, doc_id).value;
    doc.reset_errors();
  },

  has_errors(folder_id, doc_id) {
    // console.log('HAS ERRORS', folder_id, doc_id)
    const doc = this.get(folder_id, doc_id).value;
    if (!doc) return false;
    // console.log('DOC EXIST')
    return doc.errors.length > 0;
  },

  validation_progress(folder_id) {
    const docs = this.values(folder_id).map((obj) => obj.value);

    const count_of_validated_docs = docs
      .map((obj) => (obj.is_validation_in_progress ? 0 : 1))
      .reduce((a, b) => a + b, 0);

    return docs.length === 0
      ? 100
      : Math.round((count_of_validated_docs / docs.length) * 100);
  },

  start_validation(folder_id, doc_id, count = 1) {
    const doc = this.get(folder_id, doc_id).value;
    return doc.start_validation(count);
  },

  stop_validation(folder_id, doc_id) {
    const doc = this.get(folder_id, doc_id).value;
    return doc.stop_validation();
  },
};

class VueDocRepository extends VueBoxRepository {}
Object.assign(VueDocRepository.prototype, VueDocMixin, CountMixin);

export { VueDocRepository };
