const evaluate_pattern = (pattern, attributes, escape_regex_symbols = true) => {
  const arr = [...pattern.matchAll(new RegExp("{%([^{}%]*)%}", "g"))];
  let result = pattern;

  arr.forEach((e) => {
    const part_to_be_replaced = e[0];
    let expression = e[1];

    //           
    Object.keys(attributes).forEach((key) => {
      //       
      // const change_with = escapeRegExp(attributes[key]);
      const change_with = attributes[key];
      expression = expression.replace(
        "$$" + key + "$$",
        // ,      
        "String.raw`" + change_with + "`"
      );
    });

    //   
    let evaluated_expression = "";
    try {
      evaluated_expression = eval(expression);
    } catch (e) {
      console.log(`Error during evaluation of pattern "${pattern}".`);
      console.log(e);
    }

    //    
    // https://developer.mozilla.org/en-US/docs/Web/JavaScript/Guide/Regular_Expressions
    // $& means the whole matched string
    if (escape_regex_symbols) {
      const escapeRegExp = (s) => s.replace(/[.*+?^${}()|[\]\\]/g, "\\$&");
      evaluated_expression = escapeRegExp(evaluated_expression);
    }

    while (result.indexOf(part_to_be_replaced) !== -1)
      result = result.replace(part_to_be_replaced, evaluated_expression);
  });

  return result;
};

