class ApprovalFlow {
  // class ApprovalFlow extends Model {
  // constructor(...params) {
  //   super(
  //     [
  //       // getter, to_dict, key, default
  //       //    default    params
  //       [true, false, "id"],
  //       [true, false, "is_active"],
  //       [true, false, "success"],
  //       [true, false, "steps"],
  //       [true, false, "created_at"],
  //       [true, false, "updated_at"],
  //       [true, false, "is_saved", true],
  //     ],
  //     params
  //   );
  // }
  constructor(id, is_active, success, steps, created_at, updated_at) {
    this._id = id;
    this._is_active = is_active;
    this._success = success;
    this._steps = steps;
    this._created_at = created_at;
    this._updated_at = updated_at;
    this._is_saved = true;
  }

  get id() {
    return this._id;
  }

  get is_active() {
    return this._is_active;
  }

  get is_saved() {
    return this._is_saved;
  }

  get success() {
    return this._success;
  }

  get steps() {
    return this._steps;
  }

  get created_at() {
    return this._created_at;
  }

  get updated_at() {
    return this._updated_at;
  }

  validate_index(index) {
    if (index < 0 && index >= this._steps.length) {
      console.log("  ", index);
      return false;
    }

    return true;
  }

  flush_empty_steps() {
    this._steps = this._steps.filter((e) => e.length != 0);
    this._is_saved = false;
  }

  add_step() {
    this._steps.push([]);
    this._is_saved = false;
  }

  delete_step(index) {
    if (this.validate_index(index)) this._steps.splice(index, 1);
    this._is_saved = false;
  }

  add_username_to_step(index, username) {
    // console.log(index, username);
    if (this.validate_index(index)) this._steps[index].push(username);
    this._is_saved = false;
  }

  delete_username_from_step(index, username) {
    this._steps[index] = this._steps[index].filter((e) => e != username);
    this._is_saved = false;
  }

  save() {
    this._is_saved = true;
  }
}

