<template>
  <div class="page">
    <heading-component
      class="page__heading"
      text=" PDF "
      size="middle"
    />
    <div class="page__container">
      <div class="page__listcontainer">
        <div class="page__buttonblock buttonblock">
          <text-button
            v-if="is_all_uploaded && pdf_docs.length > 0"
            class="buttonblock__button"
            title=""
            @click="handle_download()"
          />
          <text-button
            v-if="is_all_uploaded && pdf_docs.length > 0"
            class="buttonblock__button buttonblock__button_important"
            title=" "
            @click="handle_delete_all()"
          />
          <toggle-text-button
            class="buttonblock__button"
            v-if="!to_be_editted"
            activeTitle=" "
            passiveTitle=" "
            :isActive="keep_bookmarks"
            @click="keep_bookmarks = !keep_bookmarks"
          />
          <toggle-text-button
            class="buttonblock__button"
            v-if="!to_be_editted"
            activeTitle="     "
            passiveTitle="    "
            :isActive="add_filenames_as_bookmarks"
            @click="add_filenames_as_bookmarks = !add_filenames_as_bookmarks"
          />
          <text-button
            v-if="
              is_all_uploaded &&
              pdf_docs.length > 0 &&
              is_same_page_count_and_not_edited
            "
            class="buttonblock__button"
            title="  "
            @click="handle_merge_annotations()"
          />
        </div>
        <div class="page__headerblock headerblock">
          <sized-div class="headerblock__sizedblock" size="w50">
            <form-text-field
              class="headerblock__textfield"
              value="%"
              :parameters="{ align: 'center' }"
            />
          </sized-div>

          <sized-div class="headerblock__sizedblock" size="w50">
            <form-text-field class="headerblock__textfield" value="No." />
          </sized-div>

          <sized-div class="headerblock__sizedblock" size="w100" :grow="true">
            <form-text-field
              class="headerblock__textfield"
              value=""
            />
          </sized-div>
          <sized-div
            class="headerblock__sizedblock headerblock__sizedblock_select"
            size="w100"
          >
            <form-text-field class="headerblock__textfield" value=" (.)" />
          </sized-div>
          <sized-div
            class="headerblock__sizedblock headerblock__sizedblock_select"
            size="w100"
          >
            <form-text-field class="headerblock__textfield" value=" (.)" />
          </sized-div>
          <sized-div
            class="headerblock__sizedblock headerblock__sizedblock_select"
            size="w50"
          >
            <form-text-field class="headerblock__textfield" value="." />
          </sized-div>
          <sized-div class="headerblock__sizedblock" size="w50">
            <form-text-field class="headerblock__textfield" value="" />
          </sized-div>
          <sized-div class="headerblock__sizedblock" size="w50">
            <form-text-field class="headerblock__textfield" value="" />
          </sized-div>
          <sized-div class="headerblock__sizedblock" size="w100">
            <form-text-field
              class="headerblock__textfield"
              value="- ."
            />
          </sized-div>
          <sized-div class="headerblock__sizedblock" size="w100">
            <form-text-field class="headerblock__textfield" value="" />
          </sized-div>
          <sized-div class="headerblock__sizedblock" size="w100">
            <form-text-field class="headerblock__textfield" value="" />
          </sized-div>
        </div>
        <div class="page__documentblock" v-for="doc in pdf_docs" :key="doc.id">
          <pdf-document-row
            class="page__documentrow"
            :order="doc.order"
            :page_count="doc.page_count"
            :page_to="doc.page_to"
            :page_from="doc.page_from"
            :angle="doc.angle"
            :name="doc.file.name"
            :progress="doc.file.progress"
            :size="doc.file.formatted_size()"
            :uploaded="doc.uploaded"
            @delete="handle_delete(doc.id)"
            @move:up="handle_move_up(doc.id)"
            @move:down="handle_move_down(doc.id)"
            @update:page_from="handle_update_page_from(doc, $event)"
            @update:page_to="handle_update_page_to(doc, $event)"
            @update:angle="handle_update_angle(doc, $event)"
          />
        </div>
      </div>
      <div class="page__servicecontainer">
        <file-drop-zone
          class="page__filedropzone"
          :class="{ page__filedropzone_dragging: is_drag_above_file_zone }"
          @add:file="handle_new_file_drop($event)"
          @update:dragging="is_drag_above_file_zone = $event"
        >
          <span v-if="!is_drag_above_file_zone" class="page__dropzonetitle">
            &lt; {{ max_file_size }} </span
          >
        </file-drop-zone>
        <div class="page__explanation">
          <p class="page__explanationblock">
                 PDF . 
                .
          </p>
          <p class="page__explanationblock">
                   ,
                   .
          </p>
          <p class="page__explanationblock page__explanationblock_important">
               (, )   
             .
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { v4 as uuidv4 } from "uuid";
import { onMounted, onBeforeUnmount, ref, computed } from "vue";
import { useBeforeEnterPage } from "../../logic/service_layer/use_modules";
import { VueUnitOfWork } from "../../logic/service_layer/uow";
import { MessageBus } from "../../logic/service_layer/message_bus";
import HeadingComponent from "../../components/HeadingComponent.vue";
import FileDropZone from "../../components/file/FileDropZone.vue";
import TextButton from "../../components/buttons/TextButton.vue";
import FormTextField from "../../components/fields/FormTextField.vue";
import SizedDiv from "../../components/SizedDiv.vue";
import ToggleTextButton from "../../components/buttons/ToggleTextButton.vue";
import {
  GetPdfMergeFiles,
  UploadPdfMergeFile,
  UpdatePdfMergeFile,
  DeletePdfMergeFile,
  DeleteAllPdfMergeFiles,
  DownloadPdfMergeResult,
  DownloadPdfMixResult,
  UpdateUploadProgress,
} from "../../logic/domain/command";

import {
  UploadFileTooBigError,
  UploadFileWrongExtensionError,
} from "../../logic/domain/event";
import PdfDocumentRow from "../../components/PdfDocumentRow.vue";
import { get_extension_of_file } from "../../logic/service_layer/utils/filename";

export default {
  components: {
    HeadingComponent,
    FileDropZone,
    PdfDocumentRow,
    TextButton,
    FormTextField,
    ToggleTextButton,
    SizedDiv,
  },
  name: "PdfMergeToolPage",
  setup() {
    const uow = new VueUnitOfWork();
    const max_file_size = 256; // MB
    const keep_bookmarks = ref(true);
    const add_filenames_as_bookmarks = ref(true);

    // PDF FILE DOCUMENTS
    const pdf_docs = computed(() => {
      let items = uow.pdf_merge_file_repository
        .values()
        .map((obj) => obj.value);
      items.sort((a, b) => a.order - b.order);
      return items;
    });

    const max_order = computed(() => {
      const orders = pdf_docs.value.map((obj) => obj.order);
      if (orders.length === 0) return 0;
      return Math.max(...orders);
    });

    const to_be_editted = computed(() => {
      let result = false;
      pdf_docs.value.forEach((e) => {
        if (e.to_be_editted === true) result = true;
      });
      return result;
    });

    const is_all_uploaded = computed(
      () => !pdf_docs.value.map((doc) => doc.uploaded).includes(false)
    );

    const is_same_page_count_and_not_edited = computed(() =>
      pdf_docs.value.reduce(
        (acc, doc) =>
          acc &&
          doc.page_count === pdf_docs.value[0].page_count &&
          doc.page_from === 1 &&
          doc.page_to === pdf_docs.value[0].page_count &&
          doc.angle == 0,
        true
      )
    );

    // DRAGGING
    const is_drag_above_file_zone = ref(false);

    // DROP
    const handle_new_file_drop = async (file) => {
      let message = null;
      if (file.size > max_file_size * 1024 * 1024) {
        message = new UploadFileTooBigError(file.name);
      } else if (get_extension_of_file(file.name).toLowerCase() !== "pdf") {
        message = new UploadFileWrongExtensionError(file.name);
      } else {
        message = new UploadPdfMergeFile(uuidv4(), max_order.value + 1, file);
      }
      await MessageBus.handle(message, uow);
    };

    // DELETE
    const handle_delete = async (id) => {
      await MessageBus.handle(new DeletePdfMergeFile(id), uow);
    };

    const handle_delete_all = async () => {
      await MessageBus.handle(new DeleteAllPdfMergeFiles(), uow);
    };

    // DOWNLOAD
    const handle_download = async () => {
      await MessageBus.handle(
        new DownloadPdfMergeResult(
          keep_bookmarks.value,
          add_filenames_as_bookmarks.value
        ),
        uow
      );
    };

    const handle_merge_annotations = async () => {
      await MessageBus.handle(new DownloadPdfMixResult(), uow);
    };

    // TIMERS
    const update_upload_progress = () => {
      MessageBus.handle(new UpdateUploadProgress(), uow);
    };

    // MOVE
    const swap = async (id_1, id_2) => {
      const obj_1 = pdf_docs.value[id_1];
      const obj_2 = pdf_docs.value[id_2];

      const order_1 = obj_1.order;
      const order_2 = obj_2.order;

      await Promise.all([
        MessageBus.handle(
          new UpdatePdfMergeFile(
            obj_1.id,
            order_2,
            obj_1.page_from,
            obj_1.page_to,
            obj_1.angle
          ),
          uow
        ),
        MessageBus.handle(
          new UpdatePdfMergeFile(
            obj_2.id,
            order_1,
            obj_2.page_from,
            obj_2.page_to,
            obj_2.angle
          ),
          uow
        ),
      ]);
    };

    const handle_move_up = async (id) => {
      const idx = pdf_docs.value.findIndex((x) => x.id === id);
      if (idx > 0) swap(idx, idx - 1);
    };

    const handle_move_down = async (id) => {
      const idx = pdf_docs.value.findIndex((x) => x.id === id);
      if (idx < pdf_docs.value.length - 1) swap(idx, idx + 1);
    };

    uow.upload_progress_timer.set_callback(update_upload_progress);
    uow.upload_progress_timer.start();

    onMounted(async () => {
      useBeforeEnterPage(uow, ["can_view_service"]); //   a
      //     
      await MessageBus.handle(new GetPdfMergeFiles(), uow);
    });

    // PAGES & ANGLE
    const handle_update_page_from = async (obj, value) => {
      await MessageBus.handle(
        new UpdatePdfMergeFile(
          obj.id,
          obj.order,
          value,
          obj.page_to,
          obj.angle
        ),
        uow
      );
    };

    const handle_update_page_to = async (obj, value) => {
      await MessageBus.handle(
        new UpdatePdfMergeFile(
          obj.id,
          obj.order,
          obj.page_from,
          value,
          obj.angle
        ),
        uow
      );
    };

    const handle_update_angle = async (obj, value) => {
      await MessageBus.handle(
        new UpdatePdfMergeFile(
          obj.id,
          obj.order,
          obj.page_from,
          obj.page_to,
          value
        ),
        uow
      );
    };

    onBeforeUnmount(async () => uow.upload_progress_timer.stop());

    return {
      max_file_size,
      keep_bookmarks,
      add_filenames_as_bookmarks,
      handle_new_file_drop,
      handle_delete,
      handle_delete_all,
      handle_download,
      handle_move_up,
      handle_move_down,
      handle_update_page_from,
      handle_update_page_to,
      handle_update_angle,
      is_drag_above_file_zone,
      pdf_docs,
      max_order,
      to_be_editted,
      is_all_uploaded,
      is_same_page_count_and_not_edited,
      handle_merge_annotations,
    };
  },
};
</script>

<style scoped>
.page {
  display: flex;
  flex-direction: column;
  padding-left: 60px;
}

.page__heading {
  padding-top: 60px;
  padding-bottom: 15px;
}

.page__container {
  display: flex;
}

.page__listcontainer {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  padding: 3px;
}

.page__headerblock {
  display: flex;
  padding: 3px;
}

.headerblock__sizedblock {
  font-weight: bold;
  justify-content: center;
}

.headerblock__sizedblock_select {
  margin-right: 10px;
}

.page__buttonblock {
  padding: 5px;
  display: flex;
}

.page__documentblock {
  padding: 5px;
}
.page__documentblock:nth-child(even) {
  background-color: rgb(215, 242, 255);
}

.page__servicecontainer {
  display: flex;
  flex-direction: column;
  min-width: 300px;
  max-width: 300px;
  padding: 3px;
}

.page__filedropzone {
  height: 80px;
  border: 2px dashed grey;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px;
}

.page__filedropzone_dragging {
  background: repeating-linear-gradient(
    45deg,
    #606dbc,
    #606dbc 10px,
    #465298 10px,
    #465298 20px
  );
}

.page__dropzonetitle {
  font-size: 24px;
}

.page__explanation,
.page__explanationblock {
  padding: 3px;
}

.buttonblock__button_important,
.page__explanationblock_important {
  color: red;
}
</style>
