import { Single as MailChannelSingle } from "./mail_channel";

const Single = {
  type: "object",
  properties: {
    id: { type: "integer" },

    out_protocol: { enum: ["SMTP"] },
    out_encryption: { enum: ["NONE", "SSL/TLS", "STARTTLS"] },
    out_port: { type: "integer", minimum: 0, maximum: 1000 },
    out_server: { type: "string" },

    in_protocol: { enum: ["POP3", "IMAP"] },
    in_encryption: { enum: ["NONE", "SSL/TLS", "STARTTLS"] },
    in_port: { type: "integer", minimum: 0, maximum: 1000 },
    in_server: { type: "string" },

    username: { type: "string" },
    password: { type: "string" },
    name: { type: "string" },
    mailchannels: {
      type: "array",
      items: MailChannelSingle,
    },
  },
  required: [
    "id",
    "out_protocol",
    "out_encryption",
    "out_port",
    "out_server",
    "in_protocol",
    "in_encryption",
    "in_port",
    "in_server",
    "username",
    "password",
    "name",
    "mailchannels"
  ],
  additionalProperties: false,
};

const List = {
  type: "array",
  items: Single,
};

export { Single, List };
