class WrongResponseError extends Error {}

const convert_mailbox_response_obj_to_model = (obj) => {
  const item = new MailBox(
    obj.id,
    obj.out_protocol,
    obj.out_encryption,
    obj.out_port,
    obj.out_server,
    obj.in_protocol,
    obj.in_encryption,
    obj.in_port,
    obj.in_server,
    obj.username,
    obj.password,
    obj.name
  );

  //      
  if (obj.mailchannels)
    obj.mailchannels.forEach((data) => {
      const channel = convert_mail_channel_response_obj_to_model(data);
      item.add_mail_channel(channel);
    });

  return item;
};

const get_mailboxes = async (event, uow) => {
  uow.mailbox_repository.reset_keeping_refs();

  const request = new GetMailBoxesRequest({});
  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return false;
  }

  if (response instanceof GetMailBoxesResponse) {
    response.data.forEach((obj) => {
      const mailbox = convert_mailbox_response_obj_to_model(obj);
      uow.mailbox_repository.set(mailbox.id, mailbox);
    });
    return true;
  }

  throw new WrongResponseError();
};

const add_mailbox = async (event, uow) => {
  const request = new AddMailBoxRequest({
    out_protocol: event.out_protocol,
    out_encryption: event.out_encryption,
    out_port: event.out_port,
    out_server: event.out_server,
    in_protocol: event.in_protocol,
    in_encryption: event.in_encryption,
    in_port: event.in_port,
    in_server: event.in_server,
    username: event.username,
    password: event.password,
    name: event.name,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return false;
  }

  if (response instanceof AddMailBoxResponse) {
    const mailbox = convert_mailbox_response_obj_to_model(response.data);
    uow.mailbox_repository.set(mailbox.id, mailbox);
    uow.push_message(new MailBoxAddedSuccess());
    return true;
  }

  throw new WrongResponseError();
};

