const convert_mail_message_response_obj_to_model = (obj) => {
  const base_files = obj.attachments.map((f) => {
    const base_file = new BaseFile(f.id, f.name, f.size);
    base_file.set_size(f.size);
    return base_file;
  });

  const mail = new MailMessage(
    obj.id,
    obj.subject,
    obj.address_from,
    obj.address_to,
    obj.address_cc,
    obj.address_bcc,
    new Date(obj.received_at),
    obj.mailbox,
    obj.mailchannel,
    obj.has_text,
    obj.has_html
  );

  //    
  base_files.forEach((base_file) => {
    mail.add_attachment(base_file.id, base_file);
  });

  return mail;
};

const get_mail_messages = async (event, uow) => {
  uow.mail_message_repository.reset_keeping_refs();

  const request = new GetMailMessagesRequest({ query: event.query });

  const response = await uow.api.call(request);
  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof GetMailMessagesResponse) {
    response.data.forEach((obj) =>
      uow.mail_message_repository.set(
        obj.id,
        convert_mail_message_response_obj_to_model(obj)
      )
    );
    return;
  }

  throw new WrongResponseError();
};

const delete_mail_message = async (event, uow) => {
  const request = new DeleteMailMessageRequest({ id: event.id });

  const response = await uow.api.call(request);
  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof DeleteMailMessageResponse) {
    uow.mail_message_repository.delete(event.id);
    uow.push_message(new MailMessageDeletedSuccess());
    return true;
  }

  throw new WrongResponseError();
};

const download_mail_message = async (event, uow) => {
  const id = uuidv4();

  const file = new BaseFile(id, "AAAAA", 1000);
  uow.download_progress.set(id, file);

  const request = new DownloadMailMessageRequest({
    set_size_method: (size) => uow.download_progress.set_size(id, size),
    id: event.id,
    format: event.format,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof DownloadMailMessageResponse) {
    js_download_file({
      data: response.data.file,
      filename: response.data.name,
    });
    return;
  }

  throw new WrongResponseError();
};

const download_mail_message_attachment = async (event, uow) => {
  const id = uuidv4();

  const file = new BaseFile(id, event.name, event.size);
  uow.download_progress.set(id, file);

  const request = new DownloadMailMessageAttachmentRequest({
    set_size_method: (size) => uow.download_progress.set_size(id, size),
    message_id: event.message_id,
    id: event.id,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof DownloadMailMessageAttachmentResponse) {
    js_download_file({
      data: response.data.file,
      filename: response.data.name,
      inline: event.inline,
    });
    return;
  }

  throw new WrongResponseError();
};

