<template>
  <div class="page">
    <heading-component class="page__heading" text="" size="middle" />
    <div class="page__container">
      <div class="page__searchblock">
        <div class="page__searchrow">
          <toggle-text-button
            class="page__buttonrow"
            activeTitle=" "
            passiveTitle=" "
            :isActive="is_files_shown"
            @click="is_files_shown = !is_files_shown"
          />
          <div class="page__buttonrow">
            <text-button
              class="page__buttonrow page__buttonrow_red"
              title=""
              @click="perform_action_on_items('delete')"
            />
          </div>
          <div class="page__buttonrow">
            <text-button
              class="page__buttonrow"
              title="TEXT"
              @click="perform_action_on_items('download_in_text_format')"
            />
          </div>
          <div class="page__buttonrow">
            <text-button
              class="page__buttonrow"
              title="HTML"
              @click="perform_action_on_items('download_in_html_format')"
            />
          </div>
          <div class="page__buttonrow">
            <text-button
              class="page__buttonrow"
              title="EML"
              @click="perform_action_on_items('download_in_raw_format')"
            />
          </div>
        </div>
        <search-component
          class="page__searchblock"
          :schema="search_schema"
          :model_objects="model_objects"
          :convert_obj_to_item="convert_model_to_item"
          :current_id="current_id"
          sort_by="received_at"
          :search_time="search_time"
          :is_searching="is_searching"
          :should_emit_search_event_on_any_keypress="false"
          :is_files_shown="is_files_shown"
          :is_selection_required="true"
          @choose="current_id = $event"
          @search="search_action($event)"
          @download:file="handle_download_file($event)"
          @copy_link_to:file="handle_copy_link_to_file_into_clipboard($event)"
          @select="handle_change_selected_items($event)"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { onMounted, ref, computed } from "vue";
import {
  GetMailMessages,
  DeleteMailMessage,
  DownloadMailMessageInTextFormat,
  DownloadMailMessageInHtmlFormat,
  DownloadMailMessageInRawFormat,
  DownloadMailMessageAttachment,
} from "../../logic/domain/command";
import { FileLinkCopied } from "../../logic/domain/event";
import { MessageBus } from "../../logic/service_layer/message_bus";
import { VueUnitOfWork } from "../../logic/service_layer/uow";
import HeadingComponent from "../../components/HeadingComponent.vue";
import {
  useBeforeEnterPage,
  convertDateToDateTimeString,
  useGetMailBoxes,
  useGetMailChannels,
  useExecutionTimeDecorator,
  usePerformActionMixin,
  filterArrayOfObjectsByQuery,
} from "../../logic/service_layer/use_modules";
import TextButton from "../../components/buttons/TextButton.vue";
import ToggleTextButton from "../../components/buttons/ToggleTextButton.vue";
import SearchComponent from "../../components/SearchComponent.vue";

import { useRouter } from "vue-router";

export default {
  components: {
    HeadingComponent,
    TextButton,
    ToggleTextButton,
    SearchComponent,
  },
  name: "MailMessagesPage",

  setup() {
    const router = useRouter();
    const uow = new VueUnitOfWork();

    const convert_model_to_item = (obj) => {
      return {
        id: obj.id,
        attributes: {
          id: obj.id.toString(),
          subject: obj.subject,
          mailbox: uow.mailbox_repository.get(obj.mailbox).value
            ? uow.mailbox_repository.get(obj.mailbox).value.name
            : obj.mailbox.toString(),
          mailchannel: uow.mail_channel_repository.get(obj.mailchannel).value
            ? uow.mail_channel_repository.get(obj.mailchannel).value.name
            : obj.mailchannel.toString(),
          has_text: obj.has_text === true ? "Y" : "N",
          has_html: obj.has_html === true ? "Y" : "N",
          received_at: convertDateToDateTimeString(obj.received_at),
        },
        attachments: obj.attachments,
        actions: {
          delete: async () => {
            await MessageBus.handle(new DeleteMailMessage(obj.id), uow);
          },
          download_in_text_format: async () => {
            await MessageBus.handle(
              new DownloadMailMessageInTextFormat(obj.id),
              uow
            );
          },
          download_in_html_format: async () => {
            await MessageBus.handle(
              new DownloadMailMessageInHtmlFormat(obj.id),
              uow
            );
          },
          download_in_raw_format: async () => {
            await MessageBus.handle(
              new DownloadMailMessageInRawFormat(obj.id),
              uow
            );
          },
        },
      };
    };
    const query_for_internal_search = ref({});
    const model_objects = computed(() =>
      filterArrayOfObjectsByQuery(
        uow.mail_message_repository.values().map((obj) => obj.value),
        query_for_internal_search.value
      )
    );

    // STATUS
    const search_time = ref(0);
    const is_searching = ref(false);

    const search_action = async (query) => {
      await useExecutionTimeDecorator(
        uow,
        new GetMailMessages(query),
        search_time,
        is_searching
      );
    };

    // ACTION
    const current_id = ref(0);

    const selected_items = ref([]);
    const handle_change_selected_items = (items) => (selected_items.value = items);

    const { perform_action_on_items } =
      //    1 ,   js-file-download
      //      > 1 
      //      Chrominium,   Firefox
      //   .
      usePerformActionMixin(selected_items, 1);

    // FILES
    const is_files_shown = ref(false);

    const handle_download_file = async ({id, file, inline}) => {
      const message = new DownloadMailMessageAttachment(
        id,
        file.id,
        file.name,
        file.size,
        inline
      );
      await MessageBus.handle(message, uow);
    };

    const handle_copy_link_to_file_into_clipboard = ({ id, file }) => {
      const props = router.resolve({
        name: "DownloadMailMessageFilePage",
        params: {
          message_id: id,
          file_id: file.id,
        },
      });

      navigator.clipboard
        .writeText(window.location.origin + props.href)
        .then(() => MessageBus.handle(new FileLinkCopied(), uow));
    };

    onMounted(async () => {
      //   a
      await useBeforeEnterPage(uow, [
        "can_view_mail",
        "can_view_mailbox",
        "can_view_mailchannel",
      ]);

      await Promise.all([useGetMailBoxes(uow), useGetMailChannels(uow)]);
    });

    const search_schema = [
      {
        name: "id",
        size: "w50",
        fields: [
          {
            placeholder: "ID",
            rule: "exact",
          },
        ],
      },
      {
        name: "subject",
        size: "w200",
        grow: true,
        align: "left",
        fields: [
          {
            placeholder: "",
            rule: "icontains",
          },
        ],
      },
      {
        name: "mailbox",
        size: "w200",
        fields: [
          {
            placeholder: "",
            rule: "icontains",
          },
        ],
      },
      {
        name: "mailchannel",
        size: "w200",
        fields: [
          {
            placeholder: "",
            rule: "icontains",
          },
        ],
      },
      {
        name: "has_text",
        size: "w30",
        fields: [
          {
            type: "bool",
            placeholder: "    text?",
            rule: "exact",
          },
        ],
      },
      {
        name: "has_html",
        size: "w30",
        fields: [
          {
            type: "bool",
            placeholder: "    html?",
            rule: "exact",
          },
        ],
      },
      {
        name: "received_at",
        size: "w200",
        fields: [
          {
            placeholder: " ()",
            rule: "gte",
            re: "^\\d{2}\\.\\d{2}\\.\\d{4}$",
            converter: "date_to_iso_string",
          },
          {
            placeholder: " ()",
            rule: "lte",
            re: "^\\d{2}\\.\\d{2}\\.\\d{4}$",
            converter: "date_to_iso_string",
          },
        ],
      },
    ];

    return {
      // ACTION
      current_id,
      perform_action_on_items,
      search_action,

      // SEARCH
      search_schema,
      model_objects,
      convert_model_to_item,
      search_time,
      is_searching,

      // FILES
      is_files_shown,
      handle_download_file,
      handle_copy_link_to_file_into_clipboard,

      // SELECTION
      selected_items,
      handle_change_selected_items,
    };
  },
};
</script>

<style scoped>
.page {
  display: flex;
  flex-direction: column;
  padding-left: 60px;
  padding-right: 60px;
}

.page__heading {
  padding-top: 60px;
  padding-bottom: 15px;
}
.page__container {
  display: flex;
}

.page__searchblock {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.page__searchstatus {
  display: flex;
  justify-content: center;
  padding: 10px;
}

.page__searchrow {
  display: flex;
}

.page__row {
  display: flex;
  flex-direction: column;
  cursor: pointer;
}

.page__row:nth-child(even) {
  background-color: rgb(215, 242, 255);
}

.page__buttonrow {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 5px;
}

.page__buttonrow_red {
  color: red;
}

.page__attributerow {
  width: 100%;
}

.page__filerow {
  flex-wrap: wrap;
}

.page__button {
  margin: 5px;
}

.page__sortrow {
  margin-left: 30px;
}
</style>
