import {
  NegativeResponse,
  GetPdfMergeFilesRequest,
  GetPdfMergeFilesResponse,
  UploadPdfMergeFileRequest,
  UploadPdfMergeFileResponse,
  UpdatePdfMergeFileRequest,
  UpdatePdfMergeFileResponse,
  DeletePdfMergeFileRequest,
  DeletePdfMergeFileResponse,
  DownloadPdfMergeResultRequest,
  DownloadPdfMergeResultResponse,
  DownloadPdfMixResultRequest,
  DownloadPdfMixResultResponse,
  GetShareFilesRequest,
  GetShareFilesResponse,
  UploadShareFileRequest,
  UploadShareFileResponse,
  DeleteShareFileRequest,
  DeleteShareFileResponse,
  DownloadShareFileRequest,
  DownloadShareFileResponse,
} from "../../adapters/api";

import { DeletePdfMergeFile } from "../../domain/command";

import {
  ApiError,
  UploadFileError,
  UploadFileSuccess,
  FileDeletedSuccess,
} from "../../domain/event";

import { BaseFile, PdfDocument } from "../../domain/model";
import { js_download_file } from "../utils/file_download";
import { v4 as uuidv4 } from "uuid";

class WrongResponseError extends Error {}

const get_pdf_merge_files = async (event, uow) => {
  uow.pdf_merge_file_repository.reset();

  const request = new GetPdfMergeFilesRequest({});
  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof GetPdfMergeFilesResponse) {
    response.data.forEach((obj) => {
      const file = new BaseFile(obj.file.id, obj.file.name, obj.file.size);
      uow.file_common_repository.set(obj.file.id, file);
      uow.file_common_repository.set_size_complete(obj.file.id);

      const pdf_doc = new PdfDocument(
        obj.id,
        obj.order,
        obj.page_count,
        obj.page_from,
        obj.page_to,
        obj.angle,
        file,
        true // uploaded
      );
      uow.pdf_merge_file_repository.set(obj.id, pdf_doc);
    });
    return;
  }
};

const upload_pdf_merge_file = async (event, uow) => {
  const file = new BaseFile(event.id, event.file.name, event.file.size);
  uow.file_common_repository.set(event.id, file);

  const pdf_doc = new PdfDocument(
    event.id,
    event.order,
    0,
    0,
    0,
    0,
    file,
    false
  );
  uow.pdf_merge_file_repository.set(event.id, pdf_doc);

  //    .    
  uow.upload_progress_timer.add(event.id);

  const request = new UploadPdfMergeFileRequest({
    id: event.id,
    order: event.order,
    original_file: event.file,
  });

  let response = null;

  try {
    response = await uow.api.call(request);
  } catch (error) {
    uow.push_message(new UploadFileError(event.file.name));
    console.log(error);
  }

  //       
  uow.upload_progress_timer.remove(event.id);

  if (response instanceof NegativeResponse) {
    uow.pdf_merge_file_repository.delete(event.id);
    uow.file_common_repository.delete(event.id);
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof UploadPdfMergeFileResponse) {
    //   ,    100%
    uow.file_common_repository.set_size_complete(event.id);
    //      .    
    const obj = uow.pdf_merge_file_repository.get(event.id).value;
    obj.page_count = response.data.page_count;
    obj.page_from = response.data.page_from;
    obj.page_to = response.data.page_to;
    obj.angle = response.data.angle;
    obj.uploaded = true;
    uow.push_message(new UploadFileSuccess());
    return;
  }

  uow.pdf_merge_file_repository.delete(event.id);
  uow.file_common_repository.delete(event.id);
  throw new WrongResponseError();
};

const update_pdf_merge_file = async (event, uow) => {
  const request = new UpdatePdfMergeFileRequest({
    id: event.id,
    order: event.order,
    page_from: event.page_from,
    page_to: event.page_to,
    angle: event.angle,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof UpdatePdfMergeFileResponse) {
    const obj = uow.pdf_merge_file_repository.get(response.data.id).value;
    obj.order = response.data.order;
    obj.page_from = response.data.page_from;
    obj.page_to = response.data.page_to;
    obj.angle = response.data.angle;
    return;
  }

  throw new WrongResponseError();
};

const delete_pdf_merge_file = async (event, uow) => {
  const request = new DeletePdfMergeFileRequest({
    id: event.id,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof DeletePdfMergeFileResponse) {
    uow.pdf_merge_file_repository.delete(event.id);
    uow.file_common_repository.delete(event.id);
    uow.push_message(new FileDeletedSuccess());
    return;
  }

  throw new WrongResponseError();
};

const delete_all_pdf_merge_files = (event, uow) => {
  uow.pdf_merge_file_repository
    .pure_keys()
    .forEach((id) => uow.push_message(new DeletePdfMergeFile(id)));
};

const download_pdf_merge_result = async (event, uow) => {
  const id = uuidv4();

  const file = new BaseFile(id, "AAAAA", 1000);
  uow.download_progress.set(id, file);

  const request = new DownloadPdfMergeResultRequest({
    set_size_method: (size) => uow.download_progress.set_size(id, size),
    // folder_id: event.folder_id,
    // id: event.file_id,
    keep_bookmarks: event.keep_bookmarks,
    add_filenames_as_bookmarks: event.add_filenames_as_bookmarks,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof DownloadPdfMergeResultResponse) {
    js_download_file({
      data: response.data.file,
      filename: response.data.name,
    });
    return;
  }

  throw new WrongResponseError();
};

