<template>
  <div class="page">
    <heading-component class="page__heading" text=" " size="middle" />
    <div class="page__container">
      <search-component
        class="page__searchblock"
        :schema="search_schema"
        :model_objects="model_objects"
        :convert_obj_to_item="convert_model_to_item"
        :current_id="current_id"
        sort_by="started_at"
        :search_time="search_time"
        :is_searching="is_searching"
        :should_emit_search_event_on_any_keypress="false"
        :has_link="true"
        @choose="current_id = $event"
        @search="search_action($event)"
      />
    </div>
  </div>
</template>

<script>
import { computed, onMounted, ref } from "vue";
import { GetManyFlowItems } from "../../logic/domain/command";
import { VueUnitOfWork } from "../../logic/service_layer/uow";
import {
  useBeforeEnterPage,
  convertDateToDateTimeString,
  filterArrayOfObjectsByQuery,
  useExecutionTimeDecorator,
} from "../../logic/service_layer/use_modules";

import HeadingComponent from "../../components/HeadingComponent.vue";
import SearchComponent from "../../components/SearchComponent.vue";

export default {
  components: {
    HeadingComponent,
    SearchComponent,
  },
  name: "FlowItemsPage",

  setup() {
    const uow = new VueUnitOfWork();

    const convert_model_to_item = (obj) => {
      return {
        id: obj.id,
        attributes: {
          id: obj.id.toString(),
          name: obj.name,
          username: obj.username,
          is_active: obj.is_active === true ? "Y" : "N",
          success: obj.success === true ? "Y" : "N",
          started_at: convertDateToDateTimeString(obj.started_at),
          finished_at: obj.finished_at
            ? convertDateToDateTimeString(obj.finished_at)
            : "-",
        },
        link_to_single_page: {
          name: "FlowItemPage",
          params: {
            flowitem_id: obj.id,
          },
        },
        actions: {},
      };
    };

    const query_for_internal_search = ref({});
    const model_objects = computed(() =>
      filterArrayOfObjectsByQuery(
        uow.flow_item_repository.values().map((obj) => obj.value),
        query_for_internal_search.value
      )
    );

    // STATUS
    const search_time = ref(0);
    const is_searching = ref(false);

    const current_id = ref(0)


    const search_action = async (query) => {
      await useExecutionTimeDecorator(
        uow,
        new GetManyFlowItems(query),
        search_time,
        is_searching
      );
    };

    const search_schema = [
      {
        name: "id",
        size: "w50",
        fields: [
          {
            placeholder: "ID",
            rule: "exact",
          },
        ],
      },
      {
        name: "username",
        size: "w200",
        grow: true,
        fields: [
          {
            placeholder: "",
            rule: "icontains",
          },
        ],
      },
      {
        name: "name",
        size: "w200",
        align: "left",
        grow: true,
        fields: [
          {
            placeholder: "",
            rule: "icontains",
          },
        ],
      },
      {
        name: "is_active",
        size: "w30",
        fields: [
          {
            type: "bool",
            placeholder: "?",
            rule: "exact",
          },
        ],
      },
      {
        name: "success",
        size: "w30",
        fields: [
          {
            type: "bool",
            placeholder: "?",
            rule: "exact",
          },
        ],
      },
      {
        name: "started_at",
        size: "w150",
        fields: [
          {
            placeholder: " ()",
            rule: "gte",
            re: "^\\d{2}\\.\\d{2}\\.\\d{4}$",
            converter: "date_to_iso_string",
          },
          {
            placeholder: " ()",
            rule: "lte",
            re: "^\\d{2}\\.\\d{2}\\.\\d{4}$",
            converter: "date_to_iso_string",
          },
        ],
      },
      {
        name: "finished_at",
        size: "w150",
        fields: [
          {
            placeholder: " ()",
            rule: "gte",
            re: "^\\d{2}\\.\\d{2}\\.\\d{4}$",
            converter: "date_to_iso_string",
          },
          {
            placeholder: " ()",
            rule: "lte",
            re: "^\\d{2}\\.\\d{2}\\.\\d{4}$",
            converter: "date_to_iso_string",
          },
        ],
      },
    ];

    onMounted(async () => {
      //   a
      await useBeforeEnterPage(uow, ["can_view_flow"]);
    });

    return {
      // ACTION
      search_action,
      current_id,

      // SEARCH
      search_schema,
      model_objects,
      convert_model_to_item,
      search_time,
      is_searching,
    };
  },
};
</script>

<style scoped>
.page {
  display: flex;
  flex-direction: column;
  padding-left: 60px;
  padding-right: 60px;
}

.page__heading {
  padding-top: 60px;
  padding-bottom: 15px;
}

.page__container {
  display: flex;
}


.page__searchblock {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}
</style>
