import { List as FileList } from "./file";

const Single = {
  type: "object",
  properties: {
    id: { type: "integer" },
    subject: { type: ["null", "string"] },
    message_id: { type: "string" },
    address_from: { type: "string" },
    address_to: { type: "string" },
    address_cc: { type: ["null", "string"] },
    address_bcc: { type: ["null", "string"] },
    received_at: { type: "string" },
    mailbox: { type: "integer" },
    mailchannel: { type: "integer" },
    has_text: { type: "boolean" },
    has_html: { type: "boolean" },
    attachments: FileList,
  },
  required: [
    "id",
    "subject",
    "message_id",
    "address_from",
    "address_to",
    "address_cc",
    "address_bcc",
    "received_at",
    "mailbox",
    "mailchannel",
    "has_text",
    "has_html",
    "attachments",
  ],
  additionalProperties: false,
};

const List = {
  type: "array",
  items: Single,
};

export { Single, List };
