const Single = {
  type: "object",
  properties: {
    id: {
      type: "integer",
    },
    name: {
      type: "string",
    },
    value: {
      type: "string",
    },
  },
  required: ["id", "name", "value"],
};

const List = {
  type: "array",
  items: Single,
};

export { Single, List };
