<template>
  <div class="page">
    <heading-component
      class="page__heading"
      text="   "
      size="middle"
    />
    <div class="page__container">
      <div class="page__editblock">
        <base-form
          class="page__form"
          :attributes="current"
          :settings="form_pages"
          :is_edit_mode="true"
          :is_header_required="false"
          @update="handle_update_attribute($event.name, $event.value)"
        >
        </base-form>

        <div class="page__editrow">
          <btn-component
            class="page__button"
            @click="add_action()"
            caption=""
            type="primary"
            :disabled="!is_edit_block_valid"
            v-if="can_add && !current_id"
          />
          <btn-component
            class="page__button"
            @click="update_action()"
            caption=""
            type="success"
            :disabled="!is_edit_block_valid"
            v-if="can_change && current_id"
          />
          <btn-component
            class="page__button"
            @click="delete_action()"
            caption=""
            type="danger"
            v-if="can_delete && current_id"
          />
        </div>
      </div>
      <search-component
        class="page__searchblock"
        :schema="search_schema"
        :model_objects="model_objects"
        :convert_obj_to_item="convert_model_to_item"
        :current_id="current_id"
        sort_by="username"
        :search_time="search_time"
        :is_searching="is_searching"
        :should_emit_search_event_on_any_keypress="true"
        @choose="current_id = $event"
        @search="search_action($event)"
      />
    </div>
  </div>
</template>

<script>
import { computed, onMounted, ref } from "vue";
import {
  AddUserFolderSettings,
  UpdateUserFolderSettings,
  DeleteUserFolderSettings,
} from "../../logic/domain/command";
import { MessageBus } from "../../logic/service_layer/message_bus";
import { VueUnitOfWork } from "../../logic/service_layer/uow";
import {
  convertObject,
  useBeforeEnterPage,
  filterArrayOfObjectsByQuery,
  useCurrentMixin,
  useGetUserFolderSettings,
  useGetRoles,
  useGetFolders,
} from "../../logic/service_layer/use_modules";

import HeadingComponent from "../../components/HeadingComponent.vue";
import BtnComponent from "../../components/buttons/BtnComponent.vue";
import BaseForm from "../../components/forms/BaseForm.vue";
import SearchComponent from "../../components/SearchComponent.vue";

export default {
  components: {
    HeadingComponent,
    BtnComponent,
    BaseForm,
    SearchComponent,
  },
  name: "UserFolderSettingsPage",

  setup() {
    const uow = new VueUnitOfWork();

    const convert_model_to_item = (obj) => {
      return {
        id: obj.id,
        attributes: {
          id: obj.id.toString(),
          folder_id: obj.folder_id,
          usergroup_slug: obj.usergroup_slug,
          username: obj.username,
          role: obj.role,
          search_schema: obj.search_schema ? "Y" : "N",
        },
        actions: {},
      };
    };

    const query_for_internal_search = ref({});
    const model_objects = computed(() =>
      filterArrayOfObjectsByQuery(
        uow.user_folder_settings_repository.values().map((obj) => obj.value),
        query_for_internal_search.value
      )
    );

    const can_add = uow.permission_repository.has("can_add_role");
    const can_change = uow.permission_repository.has("can_change_role");
    const can_delete = uow.permission_repository.has("can_delete_role");

    // STATUS
    const search_time = ref(0);
    const is_searching = ref(false);

    const search_action = (query) => (query_for_internal_search.value = query);

    const search_schema = [
      {
        name: "id",
        size: "w50",
        fields: [
          {
            placeholder: "ID",
            rule: "exact",
          },
        ],
      },
      {
        name: "username",
        size: "w200",
        align: "left",
        fields: [
          {
            placeholder: " ",
            rule: "icontains",
          },
        ],
      },
      {
        name: "folder_id",
        size: "w100",
        fields: [
          {
            placeholder: "ID ",
            rule: "exact",
          },
        ],
      },
      {
        name: "usergroup_slug",
        size: "w300",
        fields: [
          {
            placeholder: " ",
            rule: "icontains",
          },
        ],
      },
      {
        name: "role",
        size: "w200",
        fields: [
          {
            placeholder: "",
            rule: "icontains",
          },
        ],
      },
      {
        name: "search_schema",
        size: "w100",
        fields: [
          {
            type: "bool",
            placeholder: " ?",
            rule: "exact",
          },
        ],
      },
    ];

    // FORM
    const init_obj = {
      id: null,
      folder_id: "",
      usergroup_slug: "",
      username: "",
      role: null,
      search_schema: null,
    };

    const to_repository_convertion = {
      folder_id: parseInt,
      role: (x) => x === '__NONE__' ? null : x,
      search_schema: (x) => JSON.parse(x),
    };

    const from_repository_convertion = {
      folder_id: (x) => x.toString(),
      role: (x) => x === null ? '__NONE__' : x,
      search_schema: (x) => JSON.stringify(x, null, 3), //    - 3 
    };

    const { current_id, current, handle_update_attribute } = useCurrentMixin(
      init_obj,
      uow.user_folder_settings_repository,
      from_repository_convertion
    );

    const is_edit_block_valid = computed(
      () =>
        current.value.folder_id > 0 &&
        current.value.usergroup_slug.length > 0 &&
        current.value.username.length > 0 &&
        current.value.role !== null &&
        current.value.role.length > 0
    );

    const add_action = async () => {
      const message = new AddUserFolderSettings(
        ...[
          "folder_id",
          "usergroup_slug",
          "username",
          "role",
          "search_schema",
        ].map((k) => convertObject(current.value, to_repository_convertion)[k])
      );
      await MessageBus.handle(message, uow);
    };

    const update_action = async () => {
      const message = new UpdateUserFolderSettings(
        ...[
          "id",
          "folder_id",
          "usergroup_slug",
          "username",
          "role",
          "search_schema",
        ].map((k) => convertObject(current.value, to_repository_convertion)[k])
      );
      await MessageBus.handle(message, uow);
    };

    const delete_action = async () => {
      const message = new DeleteUserFolderSettings(current_id.value);
      await MessageBus.handle(message, uow);
      current_id.value = null;
    };

    const role_variants =
      uow.user_folder_settings_repository.get_role_variants();

    const role_options = computed(() =>
      role_variants.value
            ? [{ name: "", value: '__NONE__' }].concat(
        role_variants.value
            .map((x) => ({ name: x, value: x })))
        : []
    );

    const form_pages = computed(() => [
      {
        size: "w450",
        fields: [
          {
            type: "FormTextField",
            bind: "id",
          },
          {
            type: "FormTextField",
            parameters: { font_weight: "bold" },
            value: "ID :",
          },
          {
            type: "FormTextInput",
            bind: "folder_id",
          },
          {
            type: "FormTextField",
            parameters: { font_weight: "bold" },
            value: "Slug  :",
          },
          {
            type: "FormTextInput",
            bind: "usergroup_slug",
          },
          {
            type: "FormTextField",
            parameters: { font_weight: "bold" },
            value: " :",
          },
          {
            type: "FormTextInput",
            bind: "username",
          },
          {
            type: "FormTextField",
            parameters: { font_weight: "bold" },
            value: ":",
          },
          {
            type: "FormSelectInput",
            parameters: {
              options: role_options.value,
            },
            bind: "role",
          },
          {
            type: "FormTextField",
            parameters: { font_weight: "bold" },
            value: "    JSON",
          },
          {
            type: "FormMultilineTextInput",
            parameters: {
              rows: 15,
              resize: "vertical",
              is_disabled: true,
            },
            bind: "search_schema",
          },
        ],
      },
    ]);
    onMounted(async () => {
      //   a
      await useBeforeEnterPage(uow, ["can_view_role"]);
      await Promise.all([
        useGetRoles(uow),
        useGetUserFolderSettings(uow),
        useGetFolders(uow),
      ]);
    });

    return {
      // ACTION
      search_action,
      add_action,
      update_action,
      delete_action,

      // PERMISSION
      can_add,
      can_change,
      can_delete,

      // EDIT
      current_id,
      current,
      is_edit_block_valid,
      handle_update_attribute,
      form_pages,

      // SEARCH
      search_schema,
      model_objects,
      convert_model_to_item,
      search_time,
      is_searching,
    };
  },
};
</script>

<style scoped>
.page {
  display: flex;
  flex-direction: column;
  padding-left: 60px;
  padding-right: 60px;
}

.page__heading {
  padding-top: 60px;
  padding-bottom: 15px;
}
.page__container {
  display: flex;
}

.page__editblock {
  width: 600px;
  display: flex;
  flex-direction: column;
  padding-right: 15px;
}

.page__searchblock {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding-left: 15px;
  border-left: 1px solid black;
}

.page__button {
  margin: 5px;
}

.page__editrow {
  display: flex;
  width: 100%;
}
</style>
