import { VueFlatRepository } from "./flat_repository";
import { ref } from "vue";

const _variants = '__VARIANTS__'

const RoleMixin = {
  get_role_variants() {
    if (!Object.prototype.hasOwnProperty.call(this._properties, _variants)) {
      this._properties[_variants] = ref([]);
    }
    return this._properties[_variants];
  },

  set_role_variants(value) {
    if (!Object.prototype.hasOwnProperty.call(this._properties, _variants)) {
      this._properties[_variants] = ref(value);
    } else {
      this._properties[_variants].value = value;
    }
  },

};

const VueSettingsMixin = {
  set_role(id, role) {
    const obj = this.get(id).value;
    obj.role = role;
  },
};

class VueUserFolderSettingsRepository extends VueFlatRepository {}
Object.assign(
  VueUserFolderSettingsRepository.prototype,
  RoleMixin,
  VueSettingsMixin
);

export { VueUserFolderSettingsRepository };
