const Single = {
  type: "object",
  properties: {
    id: { type: "integer" },
    name: { type: "string" },
    description: { type: "string" },
    value: { type: "integer" },
    increment: { type: "integer" },
  },
  required: ["id", "name", "description", "value", "increment"],
  additionalProperties: false,
};

const List = {
  type: "array",
  items: Single,
};

export { Single, List };
