import { Single as PermissionSingle } from "./permission";

const Single = {
  type: "object",
  properties: {
    id: {
      type: "number",
    },
    name: {
      type: "string",
    },
    permissions: {
      type: "array",
      items: PermissionSingle
    },
  },
  required: ["id", "name", "permissions"],
};

const List = {
  type: "array",
  items: Single,
};

export { Single, List };
