<template>
  <div class="page">
    <heading-component
      class="page__heading"
      text="    "
      size="middle"
    />
    <div class="page__container">
      <div class="page__halfcontainer halfcontainer">
        <div class="page__titleblock">
          <form-text-field
            class="halfcontainer__title"
            value="  :"
          />
        </div>
        <div class="halfcontainer__searchblock searchblock">
          <form-text-input
            class="searchblock__searchinput searchinput"
            :value="query_left.name"
            :parameters="{placeholder: ''}"
            @update="query_left.name = $event"
          />
        </div>
        <group-item
          class="searchblock__groupitem"
          v-for="item in items_excluded_from_group"
          :key="item.id"
          :description="item.name"
          :can_delete="false"
          :can_add="true"
          @add="handle_add(item)"
        />
      </div>
      <div class="page__halfcontainer halfcontainer">
        <div class="page__titleblock">
          <form-text-field
            class="halfcontainer__title"
            value=" :"
          />
          <form-select-input
            class="halfcontainer__selectgroup"
            :parameters="{options: groups}"
            :value="group_id.toString()"
            @update="group_id = Number.parseInt($event)"
          />
        </div>
        <div class="halfcontainer__searchblock searchblock">
          <form-text-input
            class="searchblock__searchinput searchinput"
            :value="query_right.name"
            :parameters="{placeholder: ''}"
            @update="query_right.name = $event"
          />
        </div>
        <group-item
          class="searchblock__groupitem"
          v-for="item in items_included_to_group"
          :key="item.id"
          :description="item.name"
          :can_delete="true"
          :can_add="false"
          @delete="handle_delete(item)"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { onMounted, computed, reactive, ref, watch } from "vue";
import {
  AddMailChannelToMailBox,
  DeleteMailChannelFromMailBox,
} from "../../logic/domain/command";
import { MessageBus } from "../../logic/service_layer/message_bus";
import { VueUnitOfWork } from "../../logic/service_layer/uow";
import HeadingComponent from "../../components/HeadingComponent.vue";
import {
  useBeforeEnterPage,
  filterArrayOfObjects,
  useGetMailBoxes,
  useGetMailChannels
} from "../../logic/service_layer/use_modules";
import GroupItem from "../../components/GroupItem.vue";
import FormTextInput from "../../components/fields/FormTextInput.vue";
import FormTextField from "../../components/fields/FormTextField.vue";
import FormSelectInput from "../../components/fields/FormSelectInput.vue";

export default {
  name: "MailChannelsInMailBoxesSettingsPage",
  components: { HeadingComponent, GroupItem, FormTextInput, FormTextField, FormSelectInput },

  setup() {
    const uow = new VueUnitOfWork();

    const groups = computed(() => {
      let arr = uow.mailbox_repository.values().map((obj) => {
        return {
          id: obj.value.id,
          name: obj.value.name,
          value: obj.value.id.toString(),
        };
      });

      arr.sort((a, b) => a.name.localeCompare(b.name));
      return arr;
    });

    const group_id = ref(0);

    //    groups,  group_id 
    watch(groups, (newValue) => {
      if (group_id.value === 0 && newValue.length > 0) {
        group_id.value = newValue[0].id;
      }
    });

    const actual_group_ids = computed(() => {
      if (group_id.value === 0) return [];
      const group = uow.mailbox_repository.get(group_id.value);
      return group.value.mail_channels.map((channel) => channel.id);
    });

    const items_included_to_group = computed(() => {
      let objs = filterArrayOfObjects(
        uow.mail_channel_repository.values().map((e) => e.value),
        query_right
      );

      if (objs === null) objs = [];
      objs = objs.filter((obj) => actual_group_ids.value.includes(obj.id));
      return objs;
    });

    const items_excluded_from_group = computed(() => {
      let objs = filterArrayOfObjects(
        uow.mail_channel_repository.values().map((e) => e.value),
        query_left
      );

      if (objs === null) objs = [];
      objs = objs.filter((obj) => !actual_group_ids.value.includes(obj.id));
      return objs;
    });

    const handle_add = async (item) => {
      await MessageBus.handle(
        new AddMailChannelToMailBox(group_id.value, item.id),
        uow
      );
    };

    const handle_delete = async (item) => {
      await MessageBus.handle(
        new DeleteMailChannelFromMailBox(group_id.value, item.id),
        uow
      );
    };

    const query_left = reactive({ name: "" });
    const query_right = reactive({ name: "" });

    onMounted(async () => {
      //   a
      await useBeforeEnterPage(uow, ["can_view_mailbox"]);
      await Promise.all([useGetMailBoxes(uow), useGetMailChannels(uow)]);
    });

    return {
      groups,
      group_id,
      actual_group_ids,
      items_included_to_group,
      items_excluded_from_group,
      query_left,
      query_right,
      handle_add,
      handle_delete,
    };
  },
};
</script>
<style scoped>
.page {
  display: flex;
  flex-direction: column;
  padding-left: 60px;
}

.page__heading {
  padding-top: 60px;
  padding-bottom: 15px;
}

.page__container {
  display: flex;
}

.page__titleblock {
  display: flex;
  padding-left: 8px;
  padding-right: 8px;
}

.halfcontainer__title {
  width: 200px;
}

.halfcontainer__selectgroup {
  margin-left: 5px;
  flex-grow: 1;
}

.halfcontainer {
  width: 400px;
  display: flex;
  flex-direction: column;
}

.searchblock {
  display: flex;
  flex-direction: column;
  padding: 3px;
}

.searchinput {
  margin: 1px 5px;
}
</style>
