import { Single as SearchSchemaSingle } from "./search_schema";

const Single = {
  type: "object",
  properties: {
    id: {
      type: "integer",
    },
    user: {
      type: "string",
    },
    folder: {
      type: "integer",
    },
    usergroup: {
      type: "string",
    },
    role: {
      type: ["string", "null"],
    },
    search_schema: SearchSchemaSingle,
  },
  required: ["id", "user", "folder", "usergroup", "role", "search_schema"],
  additionalProperties: false,
};

const List = {
  type: "array",
  items: Single,
};

export { Single, List };
