<template>
  <download-file-page :generate_message="make_message" />
</template>

<script>
import { onMounted } from "vue";
import { useRoute } from "vue-router";
import { VueUnitOfWork } from "../../logic/service_layer/uow";
import { useBeforeEnterPage } from "../../logic/service_layer/use_modules";
import { DownloadShareFile } from "../../logic/domain/command";
import DownloadFilePage from "./DownloadFilePage.vue";

export default {
  components: {
    DownloadFilePage,
  },
  name: "DownloadFileFromFolderPage",

  setup() {
    const route = useRoute();
    const uow = new VueUnitOfWork();

    const make_message = (name, size) =>
      new DownloadShareFile(route.params.file_id, name, size);

    onMounted(async () => await useBeforeEnterPage(uow, ["can_view_service"]));
    return { make_message };
  },
};
</script>