    "Database.Error.Integrity": ["error", "  "],
    "Database.Error.Validation": ["error", "  "],
    "Database.Error.Protected": ["error", "   "],

    "Request.Body.NotJson": ["error", " "],
    "Parameter.Body.Wrong": ["error", " "],
    "Parameter.Path.Wrong": ["error", " "],
    "Parameter.Query.Wrong": ["error", " "],

