import {
  NegativeResponse,
  GetFoldersRequest,
  GetFoldersResponse,
  AddFolderRequest,
  AddFolderResponse,
  DeleteFolderRequest,
  DeleteFolderResponse,
  UpdateFolderRequest,
  UpdateFolderResponse,
  GetUserFolderSettingsRequest,
  GetUserFolderSettingsResponse,
  AddUserFolderSettingsRequest,
  AddUserFolderSettingsResponse,
  UpdateUserFolderSettingsRequest,
  UpdateUserFolderSettingsResponse,
  DeleteUserFolderSettingsRequest,
  DeleteUserFolderSettingsResponse,
  GetUserSearchSchemaRequest,
  GetUserSearchSchemaResponse,
  SetUserSearchSchemaRequest,
  SetUserSearchSchemaResponse,
  DeleteUserSearchSchemaRequest,
  DeleteUserSearchSchemaResponse,
} from "../../adapters/api";

import {
  ApiError,
  FoldersAddSuccess,
  FoldersUpdateSuccess,
  FoldersDeleteSuccess,
  AddUserFolderSettingsSuccess,
  UpdateUserFolderSettingsSuccess,
  DeleteUserFolderSettingsSuccess,
  SetUserSearchSchemaSuccess,
} from "../../domain/event";

import { Folder, UserFolderSetting } from "../../domain/model";

class WrongResponseError extends Error {}

const convert_folder_response_obj_to_model = (obj) => {
  const folder = new Folder(obj.id);
  folder.name = obj.name;
  folder.storage_path = obj.storage_path;
  folder.master_usergroup_slug = obj.master_usergroup_slug;
  folder.access_group = obj.access_group;

  folder.version = obj.settings.version;
  folder.validation_constraints = obj.settings.validation_constraints;
  folder.search_schema = obj.settings.search_schema;
  folder.form_schema = obj.settings.form_schema;
  folder.max_file_size = obj.settings.max_file_size;
  folder.set_filename_pattern(obj.settings.filename_pattern);
  folder.description_pattern = obj.settings.description_pattern;
  folder.is_empty_search_allowed = obj.settings.is_empty_search_allowed;

  //   
  folder.is_list_container_required = obj.settings.is_list_container_required;
  folder.is_doc_container_required = obj.settings.is_doc_container_required;
  folder.doc_container_parsers = obj.settings.doc_container_parsers;

  //   
  if (obj.settings.sort_by) {
    folder.default_sort_attribute = obj.settings.sort_by.name;
    folder.default_sort_order = obj.settings.sort_by.asc;
  }

  //    
  if (obj.settings.sort_rules) folder.sort_rules = obj.settings.sort_rules;

  //   
  if (obj.settings.mode) folder.default_mode = obj.settings.mode;

  // 
  folder.group_by = obj.settings.group_by;

  //  
  folder.strict_validation = obj.settings.strict_validation;

  //  
  if (obj.settings.action_buttons) folder.action_buttons = obj.settings.action_buttons;
  if (obj.settings.search_action_buttons) folder.search_action_buttons = obj.settings.search_action_buttons;

  //   
  if (obj.settings.approval_rules) folder.approval_rules = obj.settings.approval_rules;

  return folder;
};

const get_folders = async (event, uow) => {
  const request = new GetFoldersRequest();

  const response = await uow.api.call(request);
  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof GetFoldersResponse) {
    response.data.forEach((obj) =>
      uow.folder_repository.set(
        obj.id,
        convert_folder_response_obj_to_model(obj)
      )
    );
    return;
  }

  throw new WrongResponseError();
};

// OK
const add_folder = async (event, uow) => {
  const request = new AddFolderRequest({
    name: event.name,
    storage_path: event.storage_path,
    master_usergroup_slug: event.master_usergroup_slug,
    access_group: event.access_group,
    settings: event.settings,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof AddFolderResponse) {
    const folder = convert_folder_response_obj_to_model(response.data);
    uow.folder_repository.set(folder.id, folder);
    uow.push_message(new FoldersAddSuccess());
    return;
  }

  throw new WrongResponseError();
};

// OK
