<template>
  <div class="page">
    <heading-component
      class="page__heading"
      text=" "
      size="middle"
    />
    <div class="page__container">
      <div class="page__row" v-for="item in items" :key="item.id">
        <form-text-field
          class="page__rowitem page__rowitem_name"
          :parameters="{align: 'left'}"
          :value="item.name"
        />
        <form-text-input
          class="page__rowitem page__rowitem_value"
          :value="item.value"
          @update="item.value = $event"
        />
        <btn-component
          v-if="item.has_modified && can_change"
          class="page__button"
          @click="update_action(item.id, item.value)"
          caption=""
          type="warning"
          size="small"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { onMounted, computed } from "vue";
import { UpdateServerSetting } from "../../logic/domain/command";
import { MessageBus } from "../../logic/service_layer/message_bus";
import { VueUnitOfWork } from "../../logic/service_layer/uow";
import HeadingComponent from "../../components/HeadingComponent.vue";
import BtnComponent from "../../components/buttons/BtnComponent.vue";
import {
  useBeforeEnterPage,
  useGetServerSettings,
} from "../../logic/service_layer/use_modules";
import FormTextInput from "../../components/fields/FormTextInput.vue";
import FormTextField from "../../components/fields/FormTextField.vue";

export default {
  components: { HeadingComponent, BtnComponent, FormTextInput, FormTextField },
  name: "ServerSettingSettingsPage",

  setup() {
    const uow = new VueUnitOfWork();

    const items = computed(() => {
      let arr = uow.server_setting_repository.values().map((obj) => obj.value);
      arr.sort((a, b) => a.name.localeCompare(b.name));
      return arr;
    });

    const can_change = uow.permission_repository.has(
      "can_change_serversetting"
    );

    const update_action = async (id, value) => {
      const message = new UpdateServerSetting(id, value);
      await MessageBus.handle(message, uow);
    };

    onMounted(async () => {
      //   a
      await useBeforeEnterPage(uow, ["can_view_settings"]);
      await useGetServerSettings(uow);
    });

    return {
      items,
      update_action,
      can_change,
    };
  },
};
</script>
<style scoped>
.page {
  display: flex;
  flex-direction: column;
  padding-left: 60px;
}

.page__heading {
  padding-top: 60px;
  padding-bottom: 15px;
}

.page__container {
  width: 800px;
  display: flex;
  flex-direction: column;
}

.page__row {
  display: flex;
  align-items: center;
  padding: 3px;
}

.page__button {
  margin-left: 10px;
}

.page__rowitem_name {
  min-width: 300px;
}

.page__rowitem_value {
  flex-grow: 1;
}
</style>
