<template>
  <div class="page">
    <heading-component
      class="page__heading"
      text="   "
      size="middle"
    />
    <form-text-input
      class="page__search"
      :value="query"
      :parameters="{ placeholder: '...' }"
      @update="query = $event"
    />
    <toggle-text-button
      class="page__expandbutton"
      activeTitle="   "
      passiveTitle=" "
      :isActive="is_all_expanded"
      @click="is_all_expanded = !is_all_expanded"
    />
    <div class="page__container">
      <folder-tree-node
        :tree="folders"
        :usergroup_id="usergroup_id"
        :expand_all="is_all_expanded"
      />
    </div>
  </div>
</template>

<script>
import { onMounted, computed, ref } from "vue";
import { VueUnitOfWork } from "../logic/service_layer/uow";
import HeadingComponent from "../components/HeadingComponent.vue";
import FormTextInput from "../components/fields/FormTextInput.vue";
import FolderTreeNode from "../components/folder/FolderTreeNode.vue";
import ToggleTextButton from "../components/buttons/ToggleTextButton.vue";
import {
  useBeforeEnterPage,
  useGetFolders,
  useGetUserGroups,
} from "../logic/service_layer/use_modules";

export default {
  components: {
    HeadingComponent,
    FolderTreeNode,
    FormTextInput,
    ToggleTextButton,
  },
  name: "FoldersPage",

  setup() {
    const uow = new VueUnitOfWork();

    // USERGROUP
    const usergroup_id = computed(() => {
      const current_group = uow.usergroup_repository.get_current();
      return current_group.value === null ? 0 : current_group.value.id;
    });

    // FOLDER

    const folders = computed(() => {
      //     
      let arr = uow.folder_repository
        .values()
        .map((obj) => obj.value)
        .filter((folder) =>
          folder.name.toLowerCase().includes(query.value.toLowerCase())
        );
      arr.sort((a, b) => a.name - b.name);

      //    
      const insert_into = (obj, array_of_keys, value) => {
        Array.prototype.forEach.call(array_of_keys, (key) => {
          if (!Object.prototype.hasOwnProperty.call(obj, key)) obj[key] = {};
          obj = obj[key];
        });

        obj["_target"] = value;
      };

      //   
      let folder_tree = {};
      arr.forEach((folder) => {
        //     |
        insert_into(
          folder_tree,
          folder.name.split("|").map((value) => value.trim()),
          folder
        );
      });

      return folder_tree;
    });

    // SEARCH
    const query = ref("");

    // EXPAND
    const is_all_expanded = ref(false);

    const user = uow.user_repository.get_current();

    onMounted(async () => {
      await useBeforeEnterPage(uow, ["can_get_folders"]); //   a
      await Promise.all([useGetFolders(uow), useGetUserGroups(uow)]);
    });

    return {
      usergroup_id,
      folders,
      query,
      user,
      is_all_expanded,
    };
  },
};
</script>
<style scoped>
.page {
  display: flex;
  flex-direction: column;
  padding-left: 60px;
  padding-right: 60px;
}

.page__heading {
  padding-top: 60px;
  padding-bottom: 15px;
}
.page__container {
  display: flex;
  flex-direction: column;
}

.page__search {
  width: 50%;
  height: 40px;
  font-size: 1.2em;
}
</style>
