import { VueFlatRepository } from "./flat_repository";

const VueFolderMixin = {
  set_user_search_schema(folder_id, search_schema) {
    const folder = this.get(folder_id).value;
    if (folder) folder.user_search_schema = search_schema;
  },

  delete_user_search_schema(folder_id) {
    const folder = this.get(folder_id).value;
    folder.user_search_schema = null;
  },

  get_validation_schema(folder_id) {
    const folder = this.get(folder_id).value;
    if (!folder) return null;
    return folder.validation_schema;
  },

  get_validation_constraints(folder_id) {
    const folder = this.get(folder_id).value;
    if (!folder) return null;
    return folder.validation_constraints;
  },

  get_is_necessary_validate(folder_id) {
    const folder = this.get(folder_id).value;
    if (!folder) return null;
    return folder.is_necessary_validate;
  },

  get_filename_pattern(folder_id, attributes) {
    const folder = this.get(folder_id).value;
    return folder.get_filename_pattern(attributes);
  },

  get_strict_validation(folder_id, permission) {
    const folder = this.get(folder_id).value;
    if (!folder) return true;
    return folder.strict_validation_for_permission(permission);
  },
};

class VueFolderRepository extends VueFlatRepository {}
Object.assign(VueFolderRepository.prototype, VueFolderMixin);

export { VueFolderRepository };
