import {
  NegativeResponse,
  GetManyDocumentsApprovalsRequest,
  GetManyDocumentsApprovalsResponse,
  GetDocumentApprovalsRequest,
  GetDocumentApprovalsResponse,
  AddDocumentApprovalRequest,
  AddDocumentApprovalResponse,
  UpdateDocumentApprovalRequest,
  UpdateDocumentApprovalResponse,
  DeleteDocumentApprovalRequest,
  DeleteDocumentApprovalResponse,
  DelegateDocumentApprovalRequest,
  DelegateDocumentApprovalResponse,
  GetDocumentApprovalSpareFilesRequest,
  GetDocumentApprovalSpareFilesResponse,
  UploadDocumentApprovalFileRequest,
  UploadDocumentApprovalFileResponse,
  DeleteDocumentApprovalFileRequest,
  DeleteDocumentApprovalFileResponse,
  GetWaitingDocumentApprovalsRequest,
  GetWaitingDocumentApprovalsResponse,
} from "../../adapters/api";

import {
  ApiError,
  UploadFileError,
  UploadFileSuccess,
  FileDeletedSuccess,
  DocumentApprovalAddedSuccess,
  DocumentApprovalUpdatedSuccess,
  DocumentApprovalDeletedSuccess,
  DocumentApprovalDelegatedSuccess,
} from "../../domain/event";

import { convert_doc_response_obj_to_model } from "./document_handlers";

import { GetDocumentApprovalSpareFiles } from "../../domain/command";

import { DocumentApproval, BaseFile } from "../../domain/model";

class WrongResponseError extends Error {}

const convert_approval_response_obj_to_model = (obj) => {
  const base_files = obj.files.map((f) => {
    const base_file = new BaseFile(f.id, f.name, f.size);
    base_file.set_size(f.size);
    return base_file;
  });

  const approval = new DocumentApproval(
    obj.id,
    obj.state,
    obj.created_by.username,
    obj.created_by.first_name,
    obj.created_by.last_name,
    obj.user.username,
    obj.user.first_name,
    obj.user.last_name,
    obj.delegated_from,
    new Date(obj.created_at),
    new Date(obj.updated_at)
  );

  //    
  base_files.forEach((base_file) => {
    approval.add_file(base_file.id, base_file);
  });

  if (obj.document)
    approval.document = convert_doc_response_obj_to_model(obj.document);

  if (obj.active_approvals_count_of_document)
    approval.active_approvals_count_of_document =
      obj.active_approvals_count_of_document;

  return approval;
};

const get_many_documents_approvals = async (event, uow) => {
  uow.search_doc_approval_repository.reset_keeping_refs();

  const query = structuredClone(event.query);

  if (event.limit) query["_LIMIT"] = event.limit;
  if (event.offset) query["_OFFSET"] = event.offset;
  if (event.sort_by) query["_SORT_BY"] = event.sort_by;
  if (event.sort_asc === false) query["_SORT_ASC"] = "DESC";

  const request = new GetManyDocumentsApprovalsRequest({ query: query });
  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof GetManyDocumentsApprovalsResponse) {
    response.data.approvals.forEach((obj) => {
      const approval = convert_approval_response_obj_to_model(obj);
      uow.search_doc_approval_repository.set(approval.id, approval);
    });
    uow.search_doc_approval_repository.set_count(response.data.count);
    return;
  }

  throw new WrongResponseError();
};

const get_document_approvals = async (event, uow) => {
  uow.doc_approval_repository.reset_keeping_refs(event.document_id);

  const request = new GetDocumentApprovalsRequest({
    folder_id: event.folder_id,
    usergroup_id: event.usergroup_id,
    document_id: event.document_id,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return false;
  }

  if (response instanceof GetDocumentApprovalsResponse) {
    response.data.forEach((obj) => {
      const approval = convert_approval_response_obj_to_model(obj);
      uow.doc_approval_repository.set(event.document_id, approval.id, approval);
    });
    return true;
  }

  throw new WrongResponseError();
};

const add_document_approval = async (event, uow) => {
  const request = new AddDocumentApprovalRequest({
    folder_id: event.folder_id,
    usergroup_id: event.usergroup_id,
    document_id: event.document_id,
    username: event.username,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return false;
  }

  if (response instanceof AddDocumentApprovalResponse) {
    const approval = convert_approval_response_obj_to_model(response.data);
    uow.doc_approval_repository.set(event.document_id, approval.id, approval);
    uow.push_message(new DocumentApprovalAddedSuccess());
    return true;
  }

  throw new WrongResponseError();
};

const update_document_approval = async (event, uow) => {
  const non_attached_document_approval_files =
    uow.doc_approval_file_repository.pure_keys();

  const request = new UpdateDocumentApprovalRequest({
    folder_id: event.folder_id,
    usergroup_id: event.usergroup_id,
    document_id: event.document_id,
    approval_id: event.approval_id,
    state: event.state,
    files: non_attached_document_approval_files,
    comments: event.comments
      .replace(/<(div|br)>/g, "\n")
      .replace(/<(.|\n)*?>/g, ""),
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return false;
  }

  if (response instanceof UpdateDocumentApprovalResponse) {
    const approval = convert_approval_response_obj_to_model(response.data);
    uow.doc_approval_repository.set(
      event.document_id,
      event.approval_id,
      approval
    );
    uow.waiting_doc_approval_repository.delete(event.approval_id);

    uow.push_message(new DocumentApprovalUpdatedSuccess());
    uow.push_message(
      new GetDocumentApprovalSpareFiles(
        event.folder_id,
        event.usergroup_id,
        event.document_id
      )
    );
    return true;
  }

  throw new WrongResponseError();
};

const delete_document_approval = async (event, uow) => {
  const request = new DeleteDocumentApprovalRequest({
    folder_id: event.folder_id,
    usergroup_id: event.usergroup_id,
    document_id: event.document_id,
    approval_id: event.approval_id,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return false;
  }

  if (response instanceof DeleteDocumentApprovalResponse) {
    uow.doc_approval_repository.delete(event.document_id, event.approval_id);
    uow.push_message(new DocumentApprovalDeletedSuccess());
    return true;
  }

  throw new WrongResponseError();
};

const delegate_document_approval = async (event, uow) => {
  const request = new DelegateDocumentApprovalRequest({
    folder_id: event.folder_id,
    usergroup_id: event.usergroup_id,
    document_id: event.document_id,
    approval_id: event.approval_id,
    username: event.username,
    with_return: event.with_return,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return false;
  }

  if (response instanceof DelegateDocumentApprovalResponse) {
    response.data.forEach((obj) => {
      const approval = convert_approval_response_obj_to_model(obj);
      uow.doc_approval_repository.set(event.document_id, approval.id, approval);
      uow.waiting_doc_approval_repository.set(approval.id, approval);
    });

    if (event.with_return === false) {
      uow.waiting_doc_approval_repository.delete(event.approval_id);
    }

    uow.push_message(new DocumentApprovalDelegatedSuccess());
    return true;
  }

  throw new WrongResponseError();
};

const get_document_approval_spare_files = async (event, uow) => {
  uow.doc_approval_file_repository.reset();

  const request = new GetDocumentApprovalSpareFilesRequest({
    folder_id: event.folder_id,
    usergroup_id: event.usergroup_id,
    document_id: event.document_id,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof GetDocumentApprovalSpareFilesResponse) {
    response.data.forEach((obj) => {
      const file = new BaseFile(obj.id, obj.name, obj.size);
      uow.file_common_repository.set(obj.id, file);
      uow.file_common_repository.set_size_complete(obj.id);
      uow.doc_approval_file_repository.set(obj.id, file);
    });
    return;
  }
};

const upload_document_approval_file = async (event, uow) => {
  const file = new BaseFile(event.file_id, event.file.name, event.file.size);
  uow.file_common_repository.set(event.file_id, file);
  uow.doc_approval_file_repository.set(event.file_id, file);

  //    .    
  uow.upload_progress_timer.add(event.file_id);

  const request = new UploadDocumentApprovalFileRequest({
    folder_id: event.folder_id,
    usergroup_id: event.usergroup_id,
    document_id: event.document_id,
    file_id: event.file_id,
    original_file: event.file,
  });

  let response = null;

  try {
    response = await uow.api.call(request);
  } catch (error) {
    uow.push_message(new UploadFileError(event.file.name));
    console.log(error);
  }

  //       
  uow.upload_progress_timer.remove(event.file_id);

  if (response instanceof NegativeResponse) {
    uow.doc_approval_file_repository.delete(event.file_id);
    uow.file_common_repository.delete(event.file_id);
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof UploadDocumentApprovalFileResponse) {
    //   ,    100%
    uow.file_common_repository.set_size_complete(event.file_id);
    uow.push_message(new UploadFileSuccess());
    return;
  }

  uow.doc_approval_file_repository.delete(event.file_id);
  uow.file_common_repository.delete(event.file_id);
  throw new WrongResponseError();
};

const delete_document_approval_file = async (event, uow) => {
  const request = new DeleteDocumentApprovalFileRequest({
    folder_id: event.folder_id,
    usergroup_id: event.usergroup_id,
    document_id: event.document_id,
    file_id: event.file_id,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof DeleteDocumentApprovalFileResponse) {
    uow.doc_approval_file_repository.delete(event.file_id);
    uow.file_common_repository.delete(event.file_id);
    uow.push_message(new FileDeletedSuccess());
    return;
  }

  throw new WrongResponseError();
};

const get_waiting_document_approvals = async (event, uow) => {
  uow.waiting_doc_approval_repository.reset_keeping_refs();

  const request = new GetWaitingDocumentApprovalsRequest();
  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof GetWaitingDocumentApprovalsResponse) {
    response.data.forEach((obj) => {
      const approval = convert_approval_response_obj_to_model(obj);
      uow.waiting_doc_approval_repository.set(approval.id, approval);
    });
    return;
  }

  throw new WrongResponseError();
};

export {
  convert_approval_response_obj_to_model,
  get_many_documents_approvals,
  get_document_approvals,
  add_document_approval,
  update_document_approval,
  delete_document_approval,
  delegate_document_approval,
  get_document_approval_spare_files,
  upload_document_approval_file,
  delete_document_approval_file,
  get_waiting_document_approvals,
};
