<template>
  <div class="container">
    <navigation-component
      :version="version"
      :links="navigation_links"
      :show_usergroup="show_usergroup"
      :can_change_usergroup="can_change_usergroup"
    />
    <router-view v-slot="{ Component, route }">
      <transition :name="route.meta.transitionName">
        <component :is="Component" :key="route.path" />
      </transition>
    </router-view>
  </div>
</template>

<script>
import { computed, inject, watch, ref } from "vue";
import { useRoute } from "vue-router";
import NavigationComponent from "../components/navigation/NavigationComponent.vue";
import { VueUnitOfWork } from "../logic/service_layer/uow";
export default {
  components: { NavigationComponent },
  name: "BasePage",
  setup() {
    const route = useRoute();

    const uow = new VueUnitOfWork();
    const user = uow.user_repository.get_current();

    const version = inject("version");

    const can_view_settings =
      uow.permission_repository.has("can_view_settings");

    const navigation_links = computed(() => {
      const links = [
        { route_name: "LoginPage", title: "" },
        { route_name: "FoldersPage", title: "" },
        { route_name: "ServicePage", title: "" },
      ];

      if (can_view_settings.value === true)
        links.push({ route_name: "SettingsPage", title: "" });

      return links;
    });

    //    
    const pages_with_usergroup_selection = [
      "FoldersPage",
      "SearchDocumentPage",
      "DocumentPage",
    ];

    const show_usergroup = ref(false);
    const can_change_usergroup = ref(false);

    watch(
      () => route.name,
      (to) => {
        //        
        can_change_usergroup.value = to === "FoldersPage";
        //      
        show_usergroup.value = pages_with_usergroup_selection.includes(to);
      }
    );

    return {
      user,
      navigation_links,
      version,
      show_usergroup,
      can_change_usergroup,
    };
  },
};
</script>

<style>
/*  box sizing */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/*    */
ul[class],
ol[class] {
  padding: 0;
}

/*    */
body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

html {
  height: 100%;
}

/*    -  body */
body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  height: 100%;
}

.container {
  height: 100%;
  display: flex;
  flex-direction: column;
}

/*      ul  il,     class*/
ul[class],
ol[class] {
  list-style: none;
}

/*  a,    ,     */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/*     */
img {
  max-width: 100%;
  display: block;
}

/*        article*/
article > * + * {
  margin-top: 1em;
}

/*       */
input,
button,
textarea,
select {
  font: inherit;
}

/*       ,      */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

/*   */

.app {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto,
    Helvetica Neue, Arial, Noto Sans, sans-serif, Apple Color Emoji,
    Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
  font-size: 16px;
  height: 100%;
}
</style>
