const Type = { const: "FormFilteredSelectInput" };

const Parameters = {
  type: "object",
  properties: {
    size: { type: "integer", minimum: 1 },
    is_disabled: { type: "boolean" },
    placeholder: { type: "string" },
    default: { type: "string" },
    options: {
      type: "array",
      items: {
        type: "object",
        properties: {
          name: { type: "string" },
          value: { type: "string" },
        },
        required: ["name", "value"],
        additionalProperties: false,
      },
    },
  },
  required: ["options"],
  additionalProperties: false,
};

const FormFilteredSelectInput = {
  oneOf: [
    {
      type: "object",
      properties: {
        type: Type,
        parameters: Parameters,
        value: { type: "string" },
      },
      required: ["type", "value"],
      additionalProperties: false,
    },
    {
      type: "object",
      properties: {
        type: Type,
        parameters: Parameters,
        bind: { type: "string" },
      },
      required: ["type", "bind"],
      additionalProperties: false,
    },
  ],
};

export { FormFilteredSelectInput };
