import {
  NegativeResponse,
  GetRolesRequest,
  GetRolesResponse,
  AddRoleRequest,
  AddRoleResponse,
  DeleteRoleRequest,
  DeleteRoleResponse,
} from "../../adapters/api";

import {
  ApiError,
  AddRoleSuccess,
  DeleteRoleSuccess,
} from "../../domain/event";


class WrongResponseError extends Error {}


const get_roles = async (event, uow) => {
  const request = new GetRolesRequest();
  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof GetRolesResponse) {
    //   
    uow.user_folder_settings_repository.set_role_variants(
      response.data.map((e) => e.name)
    );

    //   
    uow.role_permissions_repository.reset();

    //     
    response.data.forEach((role) => {
      role.permissions.forEach((perm) => {
        uow.role_permissions_repository.set(role.name, perm.name, true);
      });
    });
    return;
  }

  throw new WrongResponseError();
};

// OK
const add_role = async (event, uow) => {
  const request = new AddRoleRequest({
    name: event.name,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof AddRoleResponse) {
    uow.push_message(new AddRoleSuccess());
    return;
  }

  throw new WrongResponseError();
};

// OK
const delete_role = async (event, uow) => {
  const request = new DeleteRoleRequest({
    name: event.name,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof DeleteRoleResponse) {
    uow.push_message(new DeleteRoleSuccess());
    return;
  }

  throw new WrongResponseError();
};


export {
  get_roles,
  add_role,
  delete_role,
};
