import { FolderSettings } from "./folder/folder_settings";

const Single = {
  type: "object",
  properties: {
    id: {
      type: "number",
    },
    name: {
      type: "string",
    },
    storage_path: {
      type: "string",
    },
    master_usergroup_slug: {
      type: "string",
    },
    access_group: {
      type: "string",
    },
    settings: FolderSettings,
  },
  required: ["id", "name", "storage_path", "master_usergroup_slug", "access_group", "settings"],
  additionalProperties: false,
};

const List = {
  type: "array",
  items: Single,
};

export { Single, List };
