import { VueFlatRepository } from "./flat_repository";

const ApprovalMixin = {
  get_approval_actions_count() {
    return this.get("approval_actions_count");
  },

  set_approval_actions_count(value) {
    if (value !== undefined) this.set("approval_actions_count", value);
  },

  get_approval_orders_count() {
    return this.get("approval_orders_count");
  },

  set_approval_orders_count(value) {
    if (value !== undefined) this.set("approval_orders_count", value);
  },
};

class VueInfoRepository extends VueFlatRepository {}
Object.assign(VueInfoRepository.prototype, ApprovalMixin);

export { VueInfoRepository };
