<template>
  <download-file-page :generate_message="make_message" />
</template>

<script>
import { onMounted } from "vue";
import { useRoute } from "vue-router";
import { VueUnitOfWork } from "../../logic/service_layer/uow";
import {
  useBeforeEnterPage,
  useValidateIntOr404,
} from "../../logic/service_layer/use_modules";
import { DownloadMailMessageAttachment } from "../../logic/domain/command";
import DownloadFilePage from "./DownloadFilePage.vue";

export default {
  components: {
    DownloadFilePage,
  },
  name: "DownloadMailMessageFilePage",

  setup() {
    const route = useRoute();
    const uow = new VueUnitOfWork();

    const message_id = useValidateIntOr404(route.params.message_id);

    const make_message = (name, size) =>
      new DownloadMailMessageAttachment(
        message_id,
        route.params.file_id,
        name,
        size
      );

    onMounted(async () => await useBeforeEnterPage(uow, ["can_view_mail"]));

    return { make_message };
  },
};
</script>