import { MailChannelSettings } from "./mail_channel/mail_channel_settings";

const Single = {
  type: "object",
  properties: {
    id: { type: "integer" },
    name: { type: "string" },
    folder: { type: "string" },
    storage_path: { type: "string" },
    settings: MailChannelSettings,
    is_active: { type: "boolean" },
  },
  required: ["id", "name", "folder", "storage_path", "settings", "is_active"],
  additionalProperties: false,
};

const List = {
  type: "array",
  items: Single,
};

export { Single, List };
