import {
  NegativeResponse,
  GetUserGroupsRequest,
  GetUserGroupsResponse,
  AddUserGroupRequest,
  AddUserGroupResponse,
  SetUserGroupRequest,
  SetUserGroupResponse,
  DeleteUserGroupRequest,
  DeleteUserGroupResponse,
  AddUserToUserGroupRequest,
  AddUserToUserGroupResponse,
  DeleteUserFromUserGroupRequest,
  DeleteUserFromUserGroupResponse,
} from "../../adapters/api";

import { User, UserGroup } from "../../domain/model";

import {
  ApiError,
  AddUserGroupSuccess,
  UpdateUserGroupSuccess,
  DeleteUserGroupSuccess,
} from "../../domain/event";

class WrongResponseError extends Error {}

const set_user_group_into_repository = (uow, obj) => {
  //  
  const group = new UserGroup(obj.id, obj.name, obj.slug, obj.is_private);
  uow.usergroup_repository.set(obj.id, group);

  //    ,   
  //    
  if (obj.users)
    obj.users.forEach((data) => {
      const user = new User(
        data.username,
        data.email,
        data.first_name,
        data.last_name,
        data.patronomic,
        data.position,
        data.department,
        data.is_active,
        data.role,
        data.role_inside_group,
        data.role_outside_group,
        data.on_behalf
      );

      group.add_user(user);
    });
};

const get_user_groups = async (event, uow) => {
  uow.usergroup_repository.reset_keeping_refs();

  const request = new GetUserGroupsRequest({});
  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof GetUserGroupsResponse) {
    response.data.forEach((obj) => set_user_group_into_repository(uow, obj));
    return;
  }

  throw new WrongResponseError();
};

const add_user_group = async (event, uow) => {
  const request = new AddUserGroupRequest({
    name: event.name,
    slug: event.slug,
    is_private: event.is_private,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof AddUserGroupResponse) {
    set_user_group_into_repository(uow, response.data);
    uow.push_message(new AddUserGroupSuccess());
    return;
  }

  throw new WrongResponseError();
};

const update_user_group = async (event, uow) => {
  const request = new SetUserGroupRequest({
    id: event.id,
    name: event.name,
    slug: event.slug,
    is_private: event.is_private,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof SetUserGroupResponse) {
    set_user_group_into_repository(uow, response.data);
    uow.push_message(new UpdateUserGroupSuccess());
    return;
  }

  throw new WrongResponseError();
};

const delete_user_group = async (event, uow) => {
  const request = new DeleteUserGroupRequest({
    id: event.id,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof DeleteUserGroupResponse) {
    uow.usergroup_repository.delete(event.id);
    uow.push_message(new DeleteUserGroupSuccess());
    return;
  }

  throw new WrongResponseError();
};

const add_user_to_user_group = async (event, uow) => {
  const request = new AddUserToUserGroupRequest({
    id: event.id,
    username: event.username,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof AddUserToUserGroupResponse) {
    set_user_group_into_repository(uow, response.data);
    uow.push_message(new UpdateUserGroupSuccess());
    return;
  }

  throw new WrongResponseError();
};

const delete_user_from_user_group = async (event, uow) => {
  const request = new DeleteUserFromUserGroupRequest({
    id: event.id,
    username: event.username,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof DeleteUserFromUserGroupResponse) {
    set_user_group_into_repository(uow, response.data);
    uow.push_message(new UpdateUserGroupSuccess());
    return;
  }

  throw new WrongResponseError();
};

const set_current_user_group = (event, uow) => {
  //    
  if (event.usergroup === null) console.log("  ");

  const actual_group = uow.usergroup_repository.get_current();
  if (actual_group.value && actual_group.value.id === event.usergroup.id)
    return;

  uow.usergroup_repository.set_current(event.usergroup);
  //     
  uow.folder_repository.values().forEach((folder_ref) => {
    uow.doc_repository.reset(folder_ref.value.id);
  });
};

export {
  get_user_groups,
  add_user_group,
  update_user_group,
  delete_user_group,
  add_user_to_user_group,
  delete_user_from_user_group,
  set_current_user_group,
};
