import { v4 as uuidv4 } from "uuid";

import {
  ValidateDocumentAgainstAttributeRegexValidationConstraint,
  ValidateDocumentAgainstAttributeTogetherRegexValidationConstraint,
  ValidateDocumentAgainstUniqueTogetherConstraint,
  ValidateDocumentAgainstAttributeHasPreviousValueConstraint,
  ValidateDocumentAgainstHasFileConstraint,
  ValidateDocumentAgainstHasApprovalConstraint,
  ValidateDocumentAgainstSingleUnionConstraint,
  ValidateDocumentAgainstFilenamePatternConstraint,
} from "../../domain/command";

import {
  NegativeResponse,
  GetManyDocumentsRequest,
  GetManyDocumentsResponse,
  GetDocumentApprovalsRequest,
  GetDocumentApprovalsResponse,
} from "../../adapters/api";

import { convert_approval_response_obj_to_model } from "./document_approval_handlers";

import { ApiError } from "../../domain/event";

class WrongResponseError extends Error {}

const reset_document_validation_errors = (event, uow) => {
  uow.doc_repository.reset_errors(event.folder_id, event.document_id);
};

const validate_document_against_constraints = (event, uow) => {
  //    , 
  if (!uow.folder_repository.get_is_necessary_validate(event.folder_id)) return;

  const constraints = uow.folder_repository.get_validation_constraints(
    event.folder_id
  );

  //    , 
  if (!constraints) return;

  //   id  ,  -
  //     
  const folder = uow.folder_repository.get(event.folder_id);
  const usergroup_slug = folder.value.master_usergroup_slug;

  let usergroup_id =
    usergroup_slug === ""
      ? null
      : uow.usergroup_repository.get_id_by_slug(usergroup_slug);

  //     ,   
  //  ,   
  if (usergroup_id === null) {
    usergroup_id = event.usergroup_id;
  }

  // console.log('USE, usergroup_idRGROUP ID', usergroup_id)

  const handlers = {
    attribute_regex_validation:
      ValidateDocumentAgainstAttributeRegexValidationConstraint,
    attribute_together_regex_validation:
      ValidateDocumentAgainstAttributeTogetherRegexValidationConstraint,
    has_file: ValidateDocumentAgainstHasFileConstraint,
    has_approval: ValidateDocumentAgainstHasApprovalConstraint,
    unique_together: ValidateDocumentAgainstUniqueTogetherConstraint,
    attribute_has_previous_value:
      ValidateDocumentAgainstAttributeHasPreviousValueConstraint,
    single_union: ValidateDocumentAgainstSingleUnionConstraint,
    filename_match_pattern: ValidateDocumentAgainstFilenamePatternConstraint,
  };

  //   
  uow.doc_repository.start_validation(
    event.folder_id,
    event.document_id,
    constraints.length
  );

  constraints.forEach((e) => {
    const handler_command = handlers[e.name];
    if (!handler_command) {
      console.log("Unknown validation contraint '" + e.name + "' ");
      return;
    }

    uow.push_message(
      new handler_command(
        event.folder_id,
        usergroup_id,
        event.document_id,
        e.parameters,
        e.error_message
      )
    );
  });
};

//             
const validate_document_against_unique_together_constraint = async (
  event,
  uow
) => {
  const stop_validation = () => {
    uow.doc_repository.stop_validation(event.folder_id, event.document_id);
  };

  //  async,       
  const errors_container_id = uow.doc_repository.errors_container_id(
    event.folder_id,
    event.document_id
  );

  //    
  const push_error = () =>
    uow.doc_repository.add_error(
      event.folder_id,
      event.document_id,
      errors_container_id,
      event.error_message
    );

  const attrs = uow.doc_repository.attributes(
    event.folder_id,
    event.document_id
  );

  //        
  // unique_together 
  //    2 
  let query = { _LIMIT: 2 };
  let is_all_required_attrs_filled = true;
  event.parameters.fields.forEach((name) => {
    if (!attrs[name]) is_all_required_attrs_filled = false;
    query[name + "__exact"] = attrs[name];
  });

  //        ,
  //  
  if (!is_all_required_attrs_filled) {
    stop_validation();
    return;
  }

  const request = new GetManyDocumentsRequest({
    folder_id: event.folder_id,
    usergroup_id: event.usergroup_id,
    query: query,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    stop_validation();
    return;
  }

  if (response instanceof GetManyDocumentsResponse) {
    if (
      response.data.count > 1 ||
      (response.data.count === 1 &&
        response.data.documents[0].id !== event.document_id)
    ) {
      //        
      //    id,  .  
      // console.log("push error after request");
      push_error();
      stop_validation();
      return;
    }
  } else throw new WrongResponseError();

  //      ,    
  //    

  const docs = uow.doc_repository.values(event.folder_id);

  let count_of_docs_with_same_attrs = 0;
  docs.forEach((obj) => {
    // console.log(obj.value.id, 'vs', event.document_id)
    if (obj.value.id == event.document_id) return;
    let are_same = true;
    event.parameters.fields.forEach((key) => {
      if (query[key + "__exact"] !== obj.value.get_attribute(key))
        are_same = false;
    });

    if (are_same === true) count_of_docs_with_same_attrs++;
  });

  //     unique_together   
  //  
  if (count_of_docs_with_same_attrs > 1) {
    // console.log("push, usergroup_id error after internal check");
    push_error();
  }

  stop_validation();
};

//             
const validate_document_against_has_previous_value_constraint = async (
  event,
  uow
) => {
  const stop_validation = () => {
    uow.doc_repository.stop_validation(event.folder_id, event.document_id);
  };

  //  async,       
  const errors_container_id = uow.doc_repository.errors_container_id(
    event.folder_id,
    event.document_id
  );

  //    
  const push_error = () =>
    uow.doc_repository.add_error(
      event.folder_id,
      event.document_id,
      errors_container_id,
      event.error_message
    );

  const attrs = uow.doc_repository.attributes(
    event.folder_id,
    event.document_id
  );

  //   ,  
  let is_all_required_attrs_filled = true;
  if (!attrs[event.parameters.field_to_be_checked])
    is_all_required_attrs_filled = false;
  const value_to_be_checked = attrs[event.parameters.field_to_be_checked];

  //     ,  
  if (!is_all_required_attrs_filled) {
    stop_validation();
    return;
  }

  //       
  const value_index =
    event.parameters.allowed_values.indexOf(value_to_be_checked);

  //   =   ,  
  if (value_index == 0) {
    stop_validation();
    return;
  }

  //      ,  
  if (value_index == -1) {
    push_error();
    stop_validation();
    return;
  }

  //        
  // unique_together 
  //    2- 
  let query = { _LIMIT: 2 };

  query[event.parameters.field_to_be_checked + "__exact"] =
    event.parameters.allowed_values[value_index - 1];

  event.parameters.fields.forEach((name) => {
    if (!attrs[name]) is_all_required_attrs_filled = false;
    query[name + "__exact"] = attrs[name];
  });

  //        ,
  //  
  if (!is_all_required_attrs_filled) {
    stop_validation();
    return;
  }

  const request = new GetManyDocumentsRequest({
    folder_id: event.folder_id,
    usergroup_id: event.usergroup_id,
    query: query,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    stop_validation();
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof GetManyDocumentsResponse) {
    if (
      response.data.count > 1 ||
      (response.data.count === 1 &&
        response.data.documents[0].id !== event.document_id)
    ) {
      //        
      //    id.  
      stop_validation();
      return;
    }
  } else throw new WrongResponseError();

  //      ,    
  //    

  const docs = uow.doc_repository.values(event.folder_id);

  let count_of_docs_with_same_attrs = 0;
  docs.forEach((obj) => {
    let are_same = true;

    if (
      query[event.parameters.field_to_be_checked + "__exact"] !==
      value_to_be_checked
    )
      are_same = false;

    event.parameters.fields.forEach((key) => {
      if (query[key + "__exact"] !== obj.value.get_attribute(key))
        are_same = false;
    });

    if (are_same === true) count_of_docs_with_same_attrs++;
  });

  //       
  //  
  if (count_of_docs_with_same_attrs > 0) {
    stop_validation();
    return;
  }

  //    ,  .
  push_error();
  stop_validation();
};

//     
const validate_document_against_has_file_constraint = (event, uow) => {
  const stop_validation = () => {
    uow.doc_repository.stop_validation(event.folder_id, event.document_id);
  };

  //   
  const flags = event.parameters.case_insensitive === true ? "i" : "";
  const regexp = new RegExp(event.parameters.pattern, flags);

  //  
  const doc = uow.doc_repository.get(event.folder_id, event.document_id); //Ref

  //  - ,     
  let count = 0;
  doc.value.files.forEach((file) => {
    if (file.name.toString().match(regexp)) count++;
  });

  //    
  if (
    (Number.isInteger(event.parameters.min_count) &&
      count < event.parameters.min_count) ||
    (Number.isInteger(event.parameters.max_count) &&
      count > event.parameters.max_count)
  ) {
    //  ,   (   )
    uow.doc_repository.add_error_easy(
      event.folder_id,
      event.document_id,
      event.error_message
    );
  }

  stop_validation();
};

//      
const validate_document_against_attribute_regex_validation_constraint = (
  event,
  uow
) => {
  const stop_validation = () => {
    uow.doc_repository.stop_validation(event.folder_id, event.document_id);
  };

  //   
  const flags = event.parameters.case_insensitive === true ? "i" : "";
  const regexp = new RegExp(event.parameters.pattern, flags);

  //  
  const doc = uow.doc_repository.get(event.folder_id, event.document_id); //Ref

  //  
  const attr = get_nested_attribute_from_document(doc, event.parameters.field);

  //     
  if (attr !== undefined && attr !== null)
    if (typeof attr === "object" || attr.match(regexp)) {
      stop_validation();
      return;
    }

  //  ,   (   )
  uow.doc_repository.add_error_easy(
    event.folder_id,
    event.document_id,
    event.error_message
  );

  stop_validation();
};

//      
//       
const validate_document_against_attribute_together_regex_validation_constraint =
  (event, uow) => {
    const stop_validation = () => {
      uow.doc_repository.stop_validation(event.folder_id, event.document_id);
    };

    //   
    const flags = event.parameters.case_insensitive === true ? "i" : "";
    const subject_regexp = new RegExp(event.parameters.subject_pattern, flags);
    const object_regexp = new RegExp(event.parameters.object_pattern, flags);

    //  
    const doc = uow.doc_repository.get(event.folder_id, event.document_id); //Ref

    //  
    // const subject_attr = doc.value.get_attribute(
    //   event.parameters.subject_field
    // );
    const subject_attr = get_nested_attribute_from_document(
      doc,
      event.parameters.subject_field
    );
    const object_attr = get_nested_attribute_from_document(
      doc,
      event.parameters.object_field
    );
    // const object_attr = doc.value.get_attribute(event.parameters.object_field);

    //     
    if (
      subject_attr !== undefined &&
      subject_attr !== null &&
      object_attr !== undefined &&
      object_attr !== null
    ) {
      if (subject_attr.toString().match(subject_regexp)) {
        if (object_attr.toString().match(object_regexp)) {
          stop_validation();
          return;
        }
      } else {
        stop_validation();
        return;
      }

      //  ,   (   )
      uow.doc_repository.add_error_easy(
        event.folder_id,
        event.document_id,
        event.error_message
      );
    }

    stop_validation();
  };

//         ,     union
const validate_document_against_single_union_constraint = async (
  event,
  uow
) => {
  const stop_validation = () => {
    uow.doc_repository.stop_validation(event.folder_id, event.document_id);
  };

  //  async,       
  const errors_container_id = uow.doc_repository.errors_container_id(
    event.folder_id,
    event.document_id
  );

  //    
  const push_error = () =>
    uow.doc_repository.add_error(
      event.folder_id,
      event.document_id,
      errors_container_id,
      event.error_message
    );

  const attrs = uow.doc_repository.attributes(
    event.folder_id,
    event.document_id
  );

  //        
  // unique_together 
  let query = {};
  let is_all_required_attrs_filled = true;
  event.parameters.fields.forEach((name) => {
    if (!attrs[name]) is_all_required_attrs_filled = false;
    query[name + "__exact"] = attrs[name];
  });

  //        ,
  //  
  if (!is_all_required_attrs_filled) {
    stop_validation();
    return;
  }

  const request = new GetManyDocumentsRequest({
    folder_id: event.folder_id,
    usergroup_id: event.usergroup_id,
    query: query,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    stop_validation();
    return;
  }

  if (response instanceof GetManyDocumentsResponse) {
    let union_id_already_assigned = false;

    //  -  union   
    let union_ids_obj = {};
    response.data.documents.forEach((doc) => {
      if (doc.union !== null) union_ids_obj[doc.union] = true;
    });
    const union_ids = Object.keys(union_ids_obj);

    if (union_ids.length > 1) {
      //       union_id,  .  
      push_error();
      stop_validation();
      return;
    }

    //    union ID
    if (union_ids.length === 1) {
      uow.doc_repository.set_union(
        event.folder_id,
        event.document_id,
        union_ids[0]
      );
      union_id_already_assigned = true;
      stop_validation();
      return;
    }

    //      ,    
    //    

    const docs = uow.doc_repository.values(event.folder_id);

    docs.forEach((obj) => {
      //      , 
      if (obj.value.id === event.document_id || union_id_already_assigned) {
        return;
      }

      //     ,  
      let are_same = true;
      event.parameters.fields.forEach((key) => {
        if (query[key + "__exact"] !== obj.value.get_attribute(key))
          are_same = false;
      });

      //            union,
      //  
      if (are_same === true && obj.union !== null) {
        uow.doc_repository.set_union(
          event.folder_id,
          event.document_id,
          obj.value.union
        );
        union_id_already_assigned = true;
        return;
      }
    });

    //  union id  
    if (union_id_already_assigned) {
      stop_validation();
      return;
    }

    //     ,      
    //   union_id
    uow.doc_repository.set_union(event.folder_id, event.document_id, uuidv4());
  }

  stop_validation();
  throw new WrongResponseError();
};

//      
//       
const validate_document_against_filename_pattern_constraint = (event, uow) => {
  const stop_validation = () => {
    uow.doc_repository.stop_validation(event.folder_id, event.document_id);
  };

  //  
  const doc = uow.doc_repository.get(event.folder_id, event.document_id); //Ref

  // 
  const filename_pattern = uow.folder_repository.get_filename_pattern(
    event.folder_id,
    doc.value.attributes
  );

  //   
  if (filename_pattern === null || filename_pattern == "") return;
  const regexp = new RegExp(filename_pattern);

  //   - ,    
  const min_count = event.parameters.min_count;

  //       
  let wrong_filenames = [];
  doc.value.files.forEach((file) => {
    if (file.name.toString().match(regexp)) {
      return;
    }
    wrong_filenames.push(file.name);
  });

  if (
    (min_count &&
      doc.value.files.length - wrong_filenames.length < min_count) ||
    (!min_count && wrong_filenames.length > 0)
  )
    //  ,   (   )
    wrong_filenames.forEach((filename) =>
      uow.doc_repository.add_error_easy(
        event.folder_id,
        event.document_id,
        filename + " | " + event.error_message
      )
    );

  stop_validation();
};

//   
const validate_document_against_has_approval_constraint = async (
  event,
  uow
) => {
  const stop_validation = () => {
    uow.doc_repository.stop_validation(event.folder_id, event.document_id);
  };

  //  
  const doc = uow.doc_repository.get(event.folder_id, event.document_id); //Ref

  //    ,  ,     
  //          .
  if (!doc.value.is_saved) {
    stop_validation();
    return;
  }

  //  async,       
  const errors_container_id = uow.doc_repository.errors_container_id(
    event.folder_id,
    event.document_id
  );

  //    
  const push_error = () =>
    uow.doc_repository.add_error(
      event.folder_id,
      event.document_id,
      errors_container_id,
      event.error_message
    );

  const request = new GetDocumentApprovalsRequest({
    folder_id: event.folder_id,
    usergroup_id: event.usergroup_id,
    document_id: event.document_id,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    stop_validation();
    return;
  }

  if (response instanceof GetDocumentApprovalsResponse) {
    //  ,    
    //   
    const approvals = response.data
      .map((obj) => convert_approval_response_obj_to_model(obj))
      .filter(
        (x) =>
          x.updated_at > doc.value.updated_at &&
          ["APPROVED", "DELEGATED"].includes(x.state)
      );

    approvals.sort((a, b) => a.updated_at > b.updated_at);

    const approvals_by_id = {};
    approvals.forEach((x) => (approvals_by_id[x.id] = x));

    //       
    const state_by_username = {};
    approvals.forEach((x) => {
      const state = x.state;
      let obj = x;

      state_by_username[obj.to_username] = state;
      while (obj.delegated_from) {
        obj = approvals_by_id[obj.delegated_from];
        state_by_username[obj.to_username] = state;
      }
    });

    //      
    let usernames_who_approved = [];
    Object.keys(state_by_username).forEach((username) => {
      if (state_by_username[username] === "APPROVED")
        usernames_who_approved.push(username);
    });

    //    
    const difference = event.parameters.usernames.filter(
      (x) => !usernames_who_approved.includes(x)
    );

    //          
    if (difference.length > 0) {
      push_error();
    }

    stop_validation();
    return;
  }

  stop_validation();
  throw new WrongResponseError();
};

const get_nested_attribute_from_document = (doc, attr_name) => {
  const attr_names = attr_name.split("__");
  const doc_attr_key = attr_names.shift();
  let value = doc.value.get_attribute(doc_attr_key);

  while (attr_names.length > 0) {
    const nested_key = attr_names.shift();
    value = value[nested_key];
  }

  return value;
};

export {
  reset_document_validation_errors,
  get_nested_attribute_from_document,
  validate_document_against_constraints,
  validate_document_against_attribute_regex_validation_constraint,
  validate_document_against_attribute_together_regex_validation_constraint,
  validate_document_against_unique_together_constraint,
  validate_document_against_has_previous_value_constraint,
  validate_document_against_has_file_constraint,
  validate_document_against_single_union_constraint,
  validate_document_against_filename_pattern_constraint,
  validate_document_against_has_approval_constraint,
};
