const SendMailMessageAction = {
  $schema: "http://json-schema.org/draft-07/schema#",
  type: "object",
  required: ["name", "input"],
  additionalProperties: false,
  properties: {
    name: { const: "SEND_MAIL_MESSAGE" },
    input: {
      type: "object",
      required: [
        "mailbox_id",
        "address_to",
        "address_cc",
        "address_bcc",
        "subject",
        "text_body",
        "html_body",
        "files",
        "save_storage_path",
        "save_filename"
      ],
      additionalProperties: false,
      properties: {
        mailbox_id: { type: ["integer", "string"] },
        address_to: { type: "string" },
        address_cc: { type: "string" },
        address_bcc: { type: "string" },
        subject: { type: "string" },
        text_body: { type: "string" },
        html_body: { type: "string" },
        files: {
          oneOf: [
            { type: "string" },
            {
              type: "object",
              required: ["id", "name", "storage_path"],
              properties: {
                id: { type: "string" },
                name: { type: "string" },
                storage_path: { type: "string" },
              },
              additionalProperties: false,
            },
          ],
        },
        save_storage_path: { type: "string" },
        save_filename: { type: "string" },
      },
    },
  },
};

export { SendMailMessageAction };
