<template>
  <div class="page">
    <heading-component class="page__heading" text="" size="middle" />
    <div class="page__container">
      <div class="page__row">
        <form-text-input
          class="page__roleinput"
          :value="new_role"
          :parameters="{ placeholder: ' ' }"
          @change.prevent="new_role = $event.target.value"
        />
        <btn-component
          class="page__rolebutton"
          caption=" "
          type="success"
          @click.prevent="add_role()"
        />
        <btn-component
          class="page__rolebutton"
          caption=" "
          type="danger"
          @click.prevent="delete_role()"
        />
      </div>
      <div class="page__row">
        <div class="page__namecolumn"></div>
        <div
          v-for="role in role_variants"
          :key="role"
          class="page__valuecolumn"
        >
          {{ role }}
        </div>
      </div>
      <div v-for="perm in permission_names" :key="perm.name" class="page__row">
        <div class="page__namecolumn">{{ perm.description }}</div>
        <div
          v-for="role in role_variants"
          :key="role"
          class="page__valuecolumn"
        >
          <input
            type="checkbox"
            class="page__checkbox"
            :checked="permissions[role][perm.name].value"
            @click.prevent="
              permissions[role][perm.name].value === true
                ? delete_permission(role, perm.name)
                : add_permission(role, perm.name)
            "
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { computed, onMounted, ref } from "vue";
import {
  AddPermission,
  DeletePermission,
  AddRole,
  DeleteRole,
} from "../../logic/domain/command";
import { MessageBus } from "../../logic/service_layer/message_bus";
import { VueUnitOfWork } from "../../logic/service_layer/uow";
import HeadingComponent from "../../components/HeadingComponent.vue";
import {
  useBeforeEnterPage,
  useGetRoles,
} from "../../logic/service_layer/use_modules";
import FormTextInput from "../../components/fields/FormTextInput.vue";
import BtnComponent from "../../components/buttons/BtnComponent.vue";

export default {
  name: "PermissionSettingsPage",
  components: { HeadingComponent, FormTextInput, BtnComponent },

  setup() {
    const uow = new VueUnitOfWork();

    const role_variants =
      uow.user_folder_settings_repository.get_role_variants(); // Ref

    const permission_names = [
      { name: "can_get_folders", description: "   - " },
      { name: "can_view_settings", description: " - " },

      { name: "can_view_folder", description: " - " },
      { name: "can_add_folder", description: " - " },
      { name: "can_change_folder", description: " - " },
      { name: "can_delete_folder", description: " - " },

      {
        name: "can_view_usergroup",
        description: "  - ",
      },
      {
        name: "can_add_usergroup",
        description: "  - ",
      },
      {
        name: "can_change_usergroup",
        description: "  - ",
      },
      {
        name: "can_delete_usergroup",
        description: "  - ",
      },

      { name: "can_view_user", description: " - " },
      { name: "can_change_user", description: " - " },
      { name: "can_delete_user", description: " - " },

      { name: "can_view_role", description: " - " },
      { name: "can_view_permission", description: " - " },
      { name: "can_change_permission", description: " - " },
      { name: "can_add_role", description: " - " },
      { name: "can_change_role", description: " - " },
      { name: "can_delete_role", description: " - " },
      {
        name: "can_view_userfoldersetting",
        description: "    - ",
      },
      {
        name: "can_add_userfoldersetting",
        description: "    - ",
      },
      {
        name: "can_change_userfoldersetting",
        description: "    - ",
      },
      {
        name: "can_delete_userfoldersetting",
        description: "    - ",
      },
      { name: "can_view_document", description: " - " },
      { name: "can_add_document", description: " - " },
      { name: "can_change_document", description: " - " },
      { name: "can_delete_document", description: " - " },

      { name: "can_view_file", description: " - " },
      { name: "can_add_file", description: " - " },
      { name: "can_delete_file", description: " - " },

      { name: "can_view_service", description: " - " },

      { name: "can_view_flow", description: " - " },
      { name: "can_add_flow", description: " - " },
      { name: "can_change_flow", description: " - " },
      { name: "can_delete_flow", description: " - " },
      { name: "can_view_flowitem", description: "  - " },
      {
        name: "can_add_flowitem",
        description: "  - ",
      },
      {
        name: "can_change_flowitem",
        description: "  - ",
      },
      {
        name: "can_delete_flowitem",
        description: "  - ",
      },
      { name: "can_view_flowitemstep", description: "  - " },

      {
        name: "can_view_documentapproval",
        description: "  - ",
      },
      {
        name: "can_add_documentapproval",
        description: "  - ",
      },
      {
        name: "can_change_documentapproval",
        description: "  - ",
      },
      {
        name: "can_delete_documentapproval",
        description: "  - ",
      },
      {
        name: "can_delegate_documentapproval",
        description: "  - ",
      },

      {
        name: "can_view_documentapprovalflow",
        description: "   - ",
      },
      {
        name: "can_change_documentapprovalflow",
        description: "   - ",
      },
      {
        name: "can_delete_documentapprovalflow",
        description: "   - ",
      },

      { name: "can_empty_cart", description: " - " },
      { name: "can_view_cart", description: " - " },
      { name: "can_add_to_cart", description: " - " },
      { name: "can_remove_from_cart", description: " - " },

      {
        name: "can_view_userreplacement",
        description: "   - ",
      },
      {
        name: "can_add_userreplacement",
        description: "   - ",
      },
      {
        name: "can_delete_userreplacement",
        description: "   - ",
      },
      {
        name: "can_view_alluserreplacement",
        description: "   - ",
      },
      {
        name: "can_add_alluserreplacement",
        description: "   - ",
      },
      {
        name: "can_delete_alluserreplacement",
        description: "   - ",
      },

      {
        name: "can_view_onbehalfuser",
        description: "     - ",
      },
      {
        name: "can_change_onbehalfuser",
        description: "     - ",
      },
      {
        name: "can_delete_onbehalfuser",
        description: "     - ",
      },

      {
        name: "can_view_serversetting",
        description: "  - ",
      },
      {
        name: "can_change_serversetting",
        description: "  - ",
      },
      {
        name: "can_view_mailbox",
        description: "  - ",
      },
      {
        name: "can_add_mailbox",
        description: "  - ",
      },
      {
        name: "can_change_mailbox",
        description: "  - ",
      },
      {
        name: "can_delete_mailbox",
        description: "  - ",
      },
      {
        name: "can_view_mailchannel",
        description: "  - ",
      },
      {
        name: "can_add_mailchannel",
        description: "  - ",
      },
      {
        name: "can_change_mailchannel",
        description: "  - ",
      },
      {
        name: "can_delete_mailchannel",
        description: "  - ",
      },
      {
        name: "can_fetch_mailchannel",
        description: "  -  ",
      },
      {
        name: "can_view_mail",
        description: " - ",
      },
      {
        name: "can_delete_mail",
        description: " - ",
      },
    ];

    const permissions = computed(() => {
      let obj = {};
      const names = permission_names.map((e) => e.name);
      role_variants.value.forEach((role) => {
        let role_perms = {};
        names.forEach(
          (name) =>
            (role_perms[name] = uow.role_permissions_repository.get(
              role,
              name,
              false
            ))
        );
        obj[role] = role_perms;
      });

      return obj;
    });

    const add_permission = async (role, perm) => {
      await MessageBus.handle(new AddPermission(role, perm), uow);
    };

    const delete_permission = async (role, perm) => {
      await MessageBus.handle(new DeletePermission(role, perm), uow);
    };

    const new_role = ref("");

    const add_role = async () => {
      await MessageBus.handle(new AddRole(new_role.value), uow);
    };

    const delete_role = async () => {
      await MessageBus.handle(new DeleteRole(new_role.value), uow);
    };

    onMounted(async () => {
      //   a
      await useBeforeEnterPage(uow, ["can_view_role", "can_view_permission"]);
      await useGetRoles(uow);
    });

    return {
      permission_names,
      role_variants,
      permissions,
      add_permission,
      delete_permission,
      new_role,
      add_role,
      delete_role,
    };
  },
};
</script>

<style scoped>
.page {
  display: flex;
  flex-direction: column;
  padding-left: 60px;
}

.page__heading {
  padding-top: 60px;
  padding-bottom: 15px;
}

.page__container {
  display: flex;
  flex-direction: column;
}
.page__row {
  display: flex;
  padding: 3px;
}

.page__namecolumn {
  width: 400px;
  padding: 3px;
}

.page__valuecolumn {
  display: flex;
  justify-content: center;
  min-width: 80px;
  max-width: 80px;
  padding: 3px;
}

.page__roleinput {
  width: 300px;
}

.page__roleinput,
.page__rolebutton {
  margin: 3px;
}
</style>

