<template>
  <div class="page">
    <div class="page__header">
      <heading-component :text="' '" size="middle" />
    </div>
    <div class="page__container">
      <form-text-field
        class="container__header"
        value="  :"
        :parameters="{ align: 'left' }"
      />
      <div class="container__row">
        <div class="container__cell container__cell_left">
          <text-button
            title=" "
            :disabled="is_replacement_username_chosen === false"
            @click="handle_add_user_replacement()"
          />
        </div>
        <div class="container__cell">
          <form-filtered-select-input
            :value="replacement_username"
            :parameters="{
              options: user_options,
              placeholder: '...',
              size: 3,
            }"
            @update="replacement_username = $event"
          />
        </div>
        <div class="container__cell">
          <text-button
            class="container__cellbutton"
            v-for="user in user_replacements"
            :key="user.id"
            :title="user.last_name + ' ' + user.first_name + ' (X)'"
            @click="handle_delete_user_replacement(user.username)"
          />
        </div>
      </div>
      <div class="container__row">
        <div class="container__cell container__cell_left">
          <form-text-field
            class="container__celllabel"
            value="  :"
          />
        </div>
        <div class="container__cell">
          <form-select-input
            :value="behalf_username"
            :parameters="{
              options: behalf_users_options,
            }"
            @update="handle_change_on_behalf_user($event)"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { onMounted, computed, ref } from "vue";
import { VueUnitOfWork } from "../logic/service_layer/uow";
import { MessageBus } from "../logic/service_layer/message_bus";
import {
  useBeforeEnterPage,
  useGetUsers,
  useGetUserReplacements,
  useGetOnBehalfUsers,
} from "../logic/service_layer/use_modules";
import HeadingComponent from "../components/HeadingComponent.vue";
import TextButton from "../components/buttons/TextButton.vue";
import {
  AddUserReplacement,
  DeleteUserReplacement,
  SetOnBehalfUser,
} from "../logic/domain/command";
import FormTextField from "../components/fields/FormTextField.vue";
import FormSelectInput from "../components/fields/FormSelectInput.vue";
import FormFilteredSelectInput from "../components/fields/FormFilteredSelectInput.vue";

export default {
  components: {
    HeadingComponent,
    TextButton,
    FormTextField,
    FormSelectInput,
    FormFilteredSelectInput,
  },
  name: "CartPage",
  setup() {
    const uow = new VueUnitOfWork();

    // USERS
    const user = uow.user_repository.get_current();
    const users = computed(() =>
      uow.user_repository.values().map((e) => e.value)
    );
    const user_replacements = computed(() =>
      uow.user_replacement_repository
        .values(user.value.username)
        .map((e) => e.value)
    );
    const user_options = computed(() =>
      [{ name: " ", value: "NOT_CHOSEN" }].concat(
        users.value
          .filter((e) => e.is_active === true)
          .map((e) => {
            return {
              name: (
                e.last_name +
                " " +
                e.first_name +
                " (" +
                e.username +
                ")"
              ).trim(),
              value: e.username,
            };
          })
          .sort((a, b) => a.name.localeCompare(b.name))
      )
    );

    const replacement_username = ref("NOT_CHOSEN");
    const is_replacement_username_chosen = computed(
      () => replacement_username.value !== "NOT_CHOSEN"
    );

    const handle_add_user_replacement = async () => {
      MessageBus.handle(
        new AddUserReplacement(replacement_username.value),
        uow
      );
    };

    const handle_delete_user_replacement = async (username) => {
      MessageBus.handle(new DeleteUserReplacement(username), uow);
    };

    // ON BEHALF USERS
    const behalf_users = uow.user_repository.get_on_behalf_users(); //Ref
    const behalf_users_options = computed(() => {
      if (behalf_users.value === null) return [];

      let arr = behalf_users.value.map((obj) => {
        return {
          name: obj.last_name + " " + obj.first_name,
          value: obj.username,
        };
      });

      arr.sort((a, b) => a.name.localeCompare(b.name));
      return arr;
    });

    const behalf_username = computed(() => {
      if (user.value === null) return null;

      return user.value.on_behalf === null
        ? user.value.username
        : user.value.on_behalf;
    });

    const handle_change_on_behalf_user = (username) => {
      if (username) MessageBus.handle(new SetOnBehalfUser(username), uow);
    };

    onMounted(async () => {
      //   a
      useBeforeEnterPage(uow, []);
      await Promise.all([
        useGetUsers(uow),
        useGetUserReplacements(uow),
        useGetOnBehalfUsers(uow),
      ]);
    });

    return {
      // USERS
      user,
      users,

      // USER REPLACEMENT
      user_replacements,
      user_options,
      replacement_username,
      is_replacement_username_chosen,
      handle_add_user_replacement,
      handle_delete_user_replacement,

      // BEHALF USERS
      behalf_users,
      behalf_users_options,
      behalf_username,
      handle_change_on_behalf_user,
    };
  },
};
</script>

<style scoped>
.page {
  display: flex;
  flex-direction: column;
  padding-left: 60px;
  padding-right: 60px;
}

.page__header {
  display: flex;
  padding-top: 60px;
  padding-bottom: 15px;
}

.page__container {
  display: flex;
  flex-direction: column;
}

.container__header {
  font-weight: bold;
  margin-bottom: 10px;
}

.container__row {
  display: flex;
  margin-bottom: 5px;
  align-items: flex-start;
}

.container__cell {
  display: flex;
}

.container__cell_left {
  min-width: 200px;
}

.container__celllabel {
  padding-left: 5px;
  padding-right: 5px;
}
</style>
            