import { Single as UserSingle } from "./user";

const Single = {
  oneOf: [
    { type: "null" },
    {
      type: "object",
      properties: {
        id: {
          type: "integer",
        },
        created_by: UserSingle,
        started_by: UserSingle,
        finished_by: UserSingle,
        is_active: {
          type: "boolean",
        },
        success: {
          type: "boolean",
        },
        steps: {
          type: "array",
          items: { type: "array", items: { type: "string" } },
        },
        created_at: {
          type: "string",
        },
        updated_at: {
          type: "string",
        },
      },
      required: [
        "id",
        "created_by",
        "started_by",
        "finished_by",
        "is_active",
        "success",
        "steps",
        "created_at",
        "updated_at",
      ],
      additionalProperties: false,
    },
  ],
};

export { Single };
