import { createRouter, createWebHistory } from 'vue-router'
// LOGIN
import LoginPage from '../views/LoginPage.vue'

// APPLICATION
import SearchDocumentPage from '../views/SearchDocumentPage.vue'
import ApprovalsPage from '../views/ApprovalsPage.vue'
import DocumentPage from '../views/DocumentPage.vue'
import FoldersPage from '../views/FoldersPage.vue'
import CartPage from '../views/CartPage.vue'

// PROFILE
import ProfilePage from '../views/ProfilePage.vue'

// SETTINGS
import SettingsPage from '../views/settings/SettingsPage'
import UserSettingsPage from '../views/settings/UserSettingsPage'
import UserReplacementsPage from '../views/settings/UserReplacementsPage'
import UserFolderSettingsPage from '../views/settings/UserFolderSettingsPage'
import FolderSettingsPage from '../views/settings/FolderSettingsPage'
import PermissionSettingsPage from '../views/settings/PermissionSettingsPage'
import UserGroupSettingsPage from '../views/settings/UserGroupSettingsPage'
import UsersInUserGroupsSettingsPage from '../views/settings/UsersInUserGroupsSettingsPage'
import ServerSettingsPage from '../views/settings/ServerSettingsPage'
import CounterSettingsPage from '../views/settings/CounterSettingsPage'

// SETTINGS - MAIL
import MailBoxSettingsPage from '../views/settings/MailBoxSettingsPage'
import MailChannelSettingsPage from '../views/settings/MailChannelSettingsPage'
import MailChannelsInMailBoxesSettingsPage from '../views/settings/MailChannelsInMailBoxesSettingsPage'
import MailMessagesPage from '../views/settings/MailMessagesPage'

// SERVICE
import ServicePage from '../views/service/ServicePage'
import PdfMergeToolPage from '../views/service/PdfMergeToolPage'
import ShareFilePage from '../views/service/ShareFilePage'
import SelfTestingPage from '../views/service/SelfTestingPage'
import MessagePage from '../views/service/MessagePage'
import DocumentApprovalSearchPage from '../views/service/DocumentApprovalSearchPage'

// DOWNLOAD
import DownloadFileFromFolderPage from '../views/download/DownloadFileFromFolderPage'
import DownloadShareFilePage from '../views/download/DownloadShareFilePage'
import DownloadMailMessageFilePage from '../views/download/DownloadMailMessageFilePage'


// SETTINGS - FLOW
import FlowsPage from '../views/settings/FlowsPage'
import FlowItemPage from '../views/settings/FlowItemPage'
import FlowItemsPage from '../views/settings/FlowItemsPage'


const routes = [
  {
    path: '/folder/:folder_id/usergroup/:usergroup_id/file/:file_id',
    name: 'DownloadFileFromFolderPage',
    component: DownloadFileFromFolderPage
  },
  {
    path: '/share/file/:file_id',
    name: 'DownloadShareFilePage',
    component: DownloadShareFilePage
  },
  {
    path: '/mailmessage/:message_id/file/:file_id',
    name: 'DownloadMailMessageFilePage',
    component: DownloadMailMessageFilePage
  },
  {
    path: '/folder/:folder_id/usergroup/:usergroup_id',
    name: 'SearchDocumentPage',
    component: SearchDocumentPage
  },
  {
    path: '/folder/:folder_id/usergroup/:usergroup_id/document/:document_id',
    name: 'DocumentPage',
    component: DocumentPage,
    meta: { transition: 'slide-right' },
  },
  {
    path: '/login',
    name: 'LoginPage',
    component: LoginPage
  },
  {
    path: '/',
    name: 'FoldersPage',
    component: FoldersPage
  },
  {
    path: '/approvals',
    name: 'ApprovalsPage',
    component: ApprovalsPage
  },
  {
    path: '/profile',
    name: 'ProfilePage',
    component: ProfilePage
  },
  {
    path: '/cart',
    name: 'CartPage',
    component: CartPage
  },
  {
    path: '/settings',
    name: 'SettingsPage',
    component: SettingsPage
  },
  {
    path: '/settings/users',
    name: 'UserSettingsPage',
    component: UserSettingsPage
  },
  {
    path: '/settings/user-replacements',
    name: 'UserReplacementsPage',
    component: UserReplacementsPage
  },
  {
    path: '/settings/folders',
    name: 'FolderSettingsPage',
    component: FolderSettingsPage
  },
  {
    path: '/settings/roles',
    name: 'UserFolderSettingsPage',
    component: UserFolderSettingsPage
  },
  {
    path: '/settings/permissions',
    name: 'PermissionSettingsPage',
    component: PermissionSettingsPage
  },
  {
    path: '/settings/usergroups',
    name: 'UserGroupSettingsPage',
    component: UserGroupSettingsPage
  },
  {
    path: '/settings/users-in-usergroups',
    name: 'UsersInUserGroupsSettingsPage',
    component: UsersInUserGroupsSettingsPage
  },
  {
    path: '/settings/flows',
    name: 'FlowsPage',
    component: FlowsPage
  },
  {
    path: '/settings/flowitems',
    name: 'FlowItemsPage',
    component: FlowItemsPage
  },
  {
    path: '/settings/flowitems/:flowitem_id',
    name: 'FlowItemPage',
    component: FlowItemPage
  },
  {
    path: '/settings/server-settings',
    name: 'ServerSettingsPage',
    component: ServerSettingsPage
  },
  {
    path: '/settings/counters',
    name: 'CounterSettingsPage',
    component: CounterSettingsPage
  },
  {
    path: '/settings/mailboxes',
    name: 'MailBoxSettingsPage',
    component: MailBoxSettingsPage
  },
  {
    path: '/settings/mailchannels',
    name: 'MailChannelSettingsPage',
    component: MailChannelSettingsPage
  },
  {
    path: '/settings/mailboxes-in-mailchannels',
    name: 'MailChannelsInMailBoxesSettingsPage',
    component: MailChannelsInMailBoxesSettingsPage
  },
  {
    path: '/settings/mailmessages',
    name: 'MailMessagesPage',
    component: MailMessagesPage
  },
  {
    path: '/service',
    name: 'ServicePage',
    component: ServicePage
  },
  {
    path: '/service/pdfmergetool',
    name: 'PdfMergeToolPage',
    component: PdfMergeToolPage
  },
  {
    path: '/service/sharefiletool',
    name: 'ShareFilePage',
    component: ShareFilePage
  },
  {
    path: '/service/selftesting',
    name: 'SelfTestingPage',
    component: SelfTestingPage
  },
  {
    path: '/service/document-approvals-search',
    name: 'DocumentApprovalSearchPage',
    component: DocumentApprovalSearchPage
  },
  {
    path: '/:pathMatch(.*)*',
    name: 'PageNotFound',
    component: MessagePage,
    props: {text: "  "}
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
