import {
  NegativeResponse,
  GetServerSettingsRequest,
  GetServerSettingsResponse,
  UpdateServerSettingRequest,
  UpdateServerSettingResponse,
} from "../../adapters/api";

import { ApiError, ServerSettingUpdatedSuccess } from "../../domain/event";

import { ServerSetting } from "../../domain/model";

class WrongResponseError extends Error {}

const convert_server_setting_response_obj_to_model = (obj) =>
  new ServerSetting(obj.id, obj.name, obj.value);

const get_server_settings = async (event, uow) => {
  const request = new GetServerSettingsRequest({});
  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof GetServerSettingsResponse) {
    response.data.forEach((obj) => {
      const model = convert_server_setting_response_obj_to_model(obj);
      uow.server_setting_repository.set(model.id, model);
    });
    return;
  }

  throw new WrongResponseError();
};

const update_server_setting = async (event, uow) => {
  const request = new UpdateServerSettingRequest({
    id: event.id,
    value: event.value,
  });
  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof UpdateServerSettingResponse) {
    const model = convert_server_setting_response_obj_to_model(response.data);
    uow.server_setting_repository.set(model.id, model);
    uow.push_message(new ServerSettingUpdatedSuccess());
    return;
  }

  throw new WrongResponseError();
};


export {
  convert_server_setting_response_obj_to_model,
  get_server_settings,
  update_server_setting,
};
