import { VueFlatRepository } from "../adapters/flat_repository";
import { VueBoxRepository } from "../adapters/box_repository";
import { VueUserRepository } from "../adapters/user_repository";
import { VueUserGroupRepository } from "../adapters/usergroup_repository";
import { VueTokenRepository } from "../adapters/token_repository";
import { VueFolderRepository } from "../adapters/folder_repository";
import { VueDocRepository } from "../adapters/doc_repository";
import { VueFileBoxRepository } from "../adapters/file_box_repository";
import { VueFileFlatRepository } from "../adapters/file_flat_repository";
import { VueUserFolderSettingsRepository } from "../adapters/user_folder_settings_repository";
import { VuePermissionRepository } from "../adapters/permission_repository";
import { VueRolePermissionsRepository } from "../adapters/role_permissions_repository";
import { VueFlatCurrentRepository } from "../adapters/flat_current_repository";
import { HttpApiProvider } from "../adapters/api_provider";
import { NotyNotificator } from "../adapters/notificator";
import { UploadProgressTimer, WaitSecondsTimer } from "../adapters/timer";
import { VueFolderSettings } from "../adapters/folder_settings";
import { VueInfoRepository } from "../adapters/info_repository";
import { VueCartRepository } from "../adapters/cart_repository";
import { VueCountableFlatRepository } from "../adapters/countable_flat_repository";

class AbstractUnitOfWork {
  constructor() {
    this.messages = [];
  }

  collect_new_messages = () => {
    const messages = [...this.messages];
    this.messages = [];

    return messages;
  };

  push_message = (message) => {
    // console.log('Push message "' + message.constructor.name + '"');
    this.messages.push(message);
  };
}

class VueUnitOfWork extends AbstractUnitOfWork {
  constructor() {
    super();
    this.api = new HttpApiProvider().get();
    this.notificator = new NotyNotificator();
    this.upload_progress_timer = new UploadProgressTimer(2000); //    2 
    this.wait_seconds_timer = new WaitSecondsTimer(1000); //     

    this.info_repository = new VueInfoRepository("info");
    this.user_repository = new VueUserRepository("users");
    this.user_replacement_repository = new VueBoxRepository("user_replacements");
    this.usergroup_repository = new VueUserGroupRepository("usergroups");
    this.token_repository = new VueTokenRepository("tokens", 30); //  auth_token 30 
    this.folder_repository = new VueFolderRepository("folders");
    this.doc_repository = new VueDocRepository("documents");
    this.waiting_doc_approval_repository = new VueFlatRepository(
      "waiting_document_approvals"
    );
    this.search_doc_approval_repository = new VueCountableFlatRepository(
      "search_document_approvals"
    );
    this.doc_approval_repository = new VueBoxRepository("document_approvals");
    this.doc_approval_flow_repository = new VueFlatRepository("document_approval_flows");
    this.doc_approval_file_repository = new VueFileFlatRepository(
      "files_document_approval"
    );
    this.cart_repository = new VueCartRepository("cart");

    this.file_in_folder_repository = new VueFileBoxRepository(
      "files_in_folders"
    );
    this.file_common_repository = new VueFileFlatRepository("files_common");
    this.user_folder_settings_repository = new VueUserFolderSettingsRepository(
      "user_folder_settings"
    );
    this.role_permissions_repository = new VueRolePermissionsRepository(
      "role_permissions"
    );
    this.permission_repository = new VuePermissionRepository("permissions");
    this.download_progress = new VueFileFlatRepository("download_progress");
    this.folder_settings = new VueFolderSettings("folder_settings");

    this.pdf_merge_file_repository = new VueFileFlatRepository(
      "files_pdf_merge"
    );
    this.share_file_repository = new VueFileFlatRepository("files_share");
    this.self_testing_question_repository = new VueFlatRepository(
      "self_testing_questions"
    );
    this.context_repository = new VueBoxRepository("context");

    this.flow_repository = new VueFlatRepository("flows");
    this.flow_item_repository = new VueFlatCurrentRepository("flow_items");
    this.flow_item_step_repository = new VueFlatRepository("flow_item_steps");

    this.server_setting_repository = new VueFlatRepository("server_settings");

    this.mailbox_repository = new VueFlatRepository("mailboxes");
    this.mail_channel_repository = new VueFlatRepository("mail_channels");
    this.mail_message_repository = new VueFlatRepository("mail_messages");

    this.counter_repository = new VueFlatRepository("counters");

    this.api.set_auth_token(this.token_repository.get_auth_token());
  }
}

export { VueUnitOfWork };
