<template>
  <div class="page">
    <heading-component
      class="page__heading"
      text=" "
      size="middle"
    />
    <div class="page__container">
      <div class="page__editblock">
        <base-form
          class="page__form"
          :attributes="current"
          :settings="form_pages"
          :is_edit_mode="true"
          :is_header_required="false"
          @update="handle_update_attribute($event.name, $event.value)"
        >
        </base-form>

        <div class="page__editrow">
          <btn-component
            class="page__button"
            @click="add_action()"
            caption=""
            type="primary"
            :disabled="!is_edit_block_valid"
            v-if="can_add && !current_id"
          />
          <btn-component
            class="page__button"
            @click="update_action()"
            caption=""
            type="success"
            :disabled="!is_edit_block_valid"
            v-if="can_change && current_id"
          />
          <btn-component
            class="page__button"
            @click="delete_action()"
            caption=""
            type="danger"
            v-if="can_delete && current_id"
          />
        </div>
      </div>
      <search-component
        class="page__searchblock"
        :schema="search_schema"
        :model_objects="model_objects"
        :convert_obj_to_item="convert_model_to_item"
        :current_id="current_id"
        sort_by="name"
        :search_time="search_time"
        :is_searching="is_searching"
        :should_emit_search_event_on_any_keypress="true"
        @choose="current_id = $event"
        @search="search_action($event)"
      />
    </div>
  </div>
</template>

<script>
import { computed, onMounted, ref } from "vue";
import {
  AddMailChannel,
  UpdateMailChannel,
  DeleteMailChannel,
  FetchMailChannel,
} from "../../logic/domain/command";
import { MessageBus } from "../../logic/service_layer/message_bus";
import { VueUnitOfWork } from "../../logic/service_layer/uow";
import {
  convertObject,
  useBeforeEnterPage,
  filterArrayOfObjectsByQuery,
  validate_json,
  useCurrentMixin,
  useGetMailChannels,
} from "../../logic/service_layer/use_modules";
import { MailChannelSettings } from "../../logic/adapters/api_responses/models/mail_channel/mail_channel_settings";

import HeadingComponent from "../../components/HeadingComponent.vue";
import BtnComponent from "../../components/buttons/BtnComponent.vue";
import BaseForm from "../../components/forms/BaseForm.vue";
import SearchComponent from "../../components/SearchComponent.vue";

export default {
  components: {
    HeadingComponent,
    BtnComponent,
    BaseForm,
    SearchComponent,
  },
  name: "MailChannelSettingsPage",

  setup() {
    const uow = new VueUnitOfWork();

    const convert_model_to_item = (obj) => {
      return {
        id: obj.id,
        attributes: {
          id: obj.id.toString(),
          name: obj.name,
          folder: obj.folder,
          storage_path: obj.storage_path,
          settings: obj.settings,
          is_active: obj.is_active === true ? "Y" : "N",
        },
        actions: {},
      };
    };

    const query_for_internal_search = ref({});
    const model_objects = computed(() =>
      filterArrayOfObjectsByQuery(
        uow.mail_channel_repository.values().map((obj) => obj.value),
        query_for_internal_search.value
      )
    );

    const can_add = uow.permission_repository.has("can_add_mailchannel");
    const can_change = uow.permission_repository.has("can_change_mailchannel");
    const can_delete = uow.permission_repository.has("can_delete_mailchannel");
    const can_fetch = uow.permission_repository.has("can_fetch_mailchannel");

    // STATUS
    const search_time = ref(0);
    const is_searching = ref(false);

    const search_action = (query) => (query_for_internal_search.value = query);

    const search_schema = [
      {
        name: "id",
        size: "w50",
        fields: [
          {
            placeholder: "ID",
            rule: "exact",
          },
        ],
      },
      {
        name: "name",
        size: "w200",
        align: "left",
        grow: true,
        fields: [
          {
            placeholder: "",
            rule: "icontains",
          },
        ],
      },
      {
        name: "folder",
        size: "w300",
        fields: [
          {
            placeholder: "",
            rule: "icontains",
          },
        ],
      },
      {
        name: "storage_path",
        size: "w300",
        fields: [
          {
            placeholder: "",
            rule: "icontains",
          },
        ],
      },
      {
        name: "is_active",
        size: "w30",
        fields: [
          {
            type: "bool",
            placeholder: "?",
            rule: "exact",
          },
        ],
      },
    ];

    // FORM
    const init_obj = {
      id: null,
      name: "",
      folder: "",
      storage_path: "",
      settings: "",
      is_active: false,
    };

    const to_repository_convertion = {
      settings: (x) => JSON.parse(x),
    };

    const from_repository_convertion = {
      settings: (x) => JSON.stringify(x, null, 3), //    - 3 
    };

    const { current_id, current, handle_update_attribute } = useCurrentMixin(
      init_obj,
      uow.mail_channel_repository,
      from_repository_convertion
    );

    const is_settings_valid = computed(() =>
      validate_json(current.value.settings, MailChannelSettings)
    );

    const is_edit_block_valid = computed(
      () =>
        current.value.name.length > 0 &&
        current.value.folder.length > 0 &&
        current.value.storage_path.length > 0 &&
        is_settings_valid.value
    );

    const form_pages = computed(() => [
      {
        size: "w500",
        fields: [
          {
            type: "FormTextField",
            value: ":",
          },
          {
            type: "FormTextInput",
            bind: "name",
          },
          {
            type: "FormTextField",
            value: " ",
          },
          {
            type: "FormTextInput",
            bind: "folder",
          },
          {
            type: "FormTextField",
            value: "  /  :",
          },
          {
            type: "FormTextInput",
            bind: "storage_path",
          },
          {
            type: "FormTextField",
            value: " ?",
          },
          {
            type: "FormBooleanInput",
            bind: "is_active",
          },
          {
            type: "FormTextField",
            value: "   JSON",
          },
          {
            type: "FormMultilineTextInput",
            parameters: {
              rows: 15,
              resize: "vertical",
              color: is_settings_valid.value ? "green" : "red",
            },
            bind: "settings",
          },
        ],
      },
    ]);

    const add_action = async () => {
      const message = new AddMailChannel(
        ...["name", "folder", "storage_path", "settings", "is_active"].map(
          (k) => convertObject(current.value, to_repository_convertion)[k]
        )
      );
      await MessageBus.handle(message, uow);
    };

    const update_action = async () => {
      const message = new UpdateMailChannel(
        ...[
          "id",
          "name",
          "folder",
          "storage_path",
          "settings",
          "is_active",
        ].map((k) => convertObject(current.value, to_repository_convertion)[k])
      );
      await MessageBus.handle(message, uow);
    };

    const delete_action = async () => {
      const message = new DeleteMailChannel(current_id.value);
      await MessageBus.handle(message, uow);
      current_id.value = null;
    };

    const fetch_action = async (id) => {
      const message = new FetchMailChannel(id);
      await MessageBus.handle(message, uow);
    };

    onMounted(async () => {
      //   a
      await useBeforeEnterPage(uow, [
        "can_view_mailchannel",
        "can_view_settings",
      ]); 
      await useGetMailChannels(uow);
    });

    return {
      // ACTION
      search_action,
      add_action,
      update_action,
      delete_action,
      fetch_action,

      // PERMISSION
      can_add,
      can_change,
      can_delete,
      can_fetch,

      // EDIT
      current_id,
      current,
      is_settings_valid,
      is_edit_block_valid,
      handle_update_attribute,
      form_pages,

      // SEARCH
      search_schema,
      model_objects,
      convert_model_to_item,
      search_time,
      is_searching,
    };
  },
};
</script>

<style scoped>
.page {
  display: flex;
  flex-direction: column;
  padding-left: 60px;
  padding-right: 60px;
}

.page__heading {
  padding-top: 60px;
  padding-bottom: 15px;
}
.page__container {
  display: flex;
}

.page__editblock {
  width: 550px;
  display: flex;
  flex-direction: column;
  padding-right: 15px;
}

.page__searchblock {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding-left: 15px;
  border-left: 1px solid black;
}

.page__button {
  margin: 5px;
}

.page__editrow {
  display: flex;
  width: 100%;
}
</style>
