import { SimpleParser } from "./parser/simple_parser";

const MailChannelSettings = {
  type: "object",
  properties: {
    version: { type: "string" },
    store_to: {
      type: "array",
      items: {
        type: "object",
        properties: {
          folder_id: { type: "integer" },
          usergroup_id: { type: "integer" },
        },
        required: ["folder_id", "usergroup_id"],
        additionalProperties: false,
      },
    },
    parser: {
      oneOf: [SimpleParser],
    },
    save: {
      type: "object",
      properties: {
        raw: { type: "boolean" },
        text: { type: "boolean" },
        html: { type: "boolean" },
        attachments: { type: "boolean" },
        filename: { type: "string" },
      },
      required: ["raw", "text", "html", "attachments"],
      additionalProperties: false,
    },
  },
  required: ["store_to", "parser", "save"],
  additionalProperties: false,
};

export { MailChannelSettings };
