<template>
  <div class="page">
    <heading-component class="page__heading" text="" size="middle" />
    <div class="page__container">
      <div class="page__editblock">
        <base-form
          class="page__form"
          :attributes="current"
          :settings="form_pages"
          :is_edit_mode="true"
          :is_header_required="false"
          @update="handle_update_attribute($event.name, $event.value)"
        >
        </base-form>

        <div class="page__editrow">
          <btn-component
            class="page__button"
            @click="add_action()"
            caption=""
            type="primary"
            :disabled="!is_edit_block_valid"
            v-if="can_add && !current_id"
          />
          <btn-component
            class="page__button"
            @click="update_action()"
            caption=""
            type="success"
            :disabled="!is_edit_block_valid"
            v-if="can_change && current_id"
          />
          <btn-component
            class="page__button"
            @click="delete_action()"
            caption=""
            type="danger"
            v-if="can_delete && current_id"
          />
        </div>
      </div>
      <search-component
        class="page__searchblock"
        :schema="search_schema"
        :model_objects="model_objects"
        :convert_obj_to_item="convert_model_to_item"
        :current_id="current_id"
        sort_by="name"
        :search_time="search_time"
        :is_searching="is_searching"
        :should_emit_search_event_on_any_keypress="false"
        @choose="current_id = $event"
        @search="search_action($event)"
      />
    </div>
  </div>
</template>

<script>
import { computed, onMounted, ref } from "vue";
import {
  GetFlows,
    AddFlow,
    UpdateFlow,
    DeleteFlow,
} from "../../logic/domain/command";
import { MessageBus } from "../../logic/service_layer/message_bus";
import { VueUnitOfWork } from "../../logic/service_layer/uow";
import {
  convertObject,
  useBeforeEnterPage,
  convertDateToDateString,
  filterArrayOfObjectsByQuery,
  validate_json,
  useExecutionTimeDecorator,
  useCurrentMixin,
} from "../../logic/service_layer/use_modules";
import { FlowProcess } from "../../logic/adapters/api_responses/models/flow_process/flow_process";

import HeadingComponent from "../../components/HeadingComponent.vue";
import BtnComponent from "../../components/buttons/BtnComponent.vue";
import BaseForm from "../../components/forms/BaseForm.vue";
import SearchComponent from "../../components/SearchComponent.vue";

export default {
  components: {
    HeadingComponent,
    BtnComponent,
    BaseForm,
    SearchComponent,
  },
  name: "FlowsPage",

  setup() {
    const uow = new VueUnitOfWork();

    const convert_model_to_item = (obj) => {
      return {
        id: obj.id,
        attributes: {
          id: obj.id.toString(),
          name: obj.name,
          username: obj.username,
          trigger: obj.trigger,
          process: obj.process,
          is_active: obj.is_active === true ? "Y" : "N",
          is_multiple: obj.is_multiple === true ? "Y" : "N",
          created_at: convertDateToDateString(obj.created_at),
          updated_at: convertDateToDateString(obj.updated_at),
        },
        actions: {},
      };
    };

    const query_for_internal_search = ref({});
    const model_objects = computed(() =>
      filterArrayOfObjectsByQuery(
        uow.flow_repository.values().map((obj) => obj.value),
        query_for_internal_search.value
      )
    );

    const can_add = uow.permission_repository.has("can_add_flow");
    const can_change = uow.permission_repository.has("can_change_flow");
    const can_delete = uow.permission_repository.has("can_delete_flow");

    // STATUS
    const search_time = ref(0);
    const is_searching = ref(false);


    const search_action = async (query) => {
      await useExecutionTimeDecorator(
        uow,
        new GetFlows(query),
        search_time,
        is_searching
      );
    };

    const search_schema = [
      {
        name: "id",
        size: "w50",
        align: "center",
        fields: [
          {
            placeholder: "ID",
            rule: "exact",
          },
        ],
      },
      {
        name: "username",
        size: "w150",
        align: "center",
        fields: [
          {
            placeholder: "",
            rule: "icontains",
          },
        ],
      },
      {
        name: "name",
        size: "w200",
        grow: true,
        fields: [
          {
            placeholder: "",
            rule: "icontains",
          },
        ],
      },
      {
        name: "trigger",
        size: "w200",
        grow: true,
        fields: [
          {
            placeholder: "",
            rule: "icontains",
          },
        ],
      },
      {
        name: "is_active",
        size: "w30",
        align: "center",
        fields: [
          {
            type: "bool",
            placeholder: "?",
            rule: "exact",
          },
        ],
      },
      {
        name: "is_multiple",
        size: "w30",
        align: "center",
        fields: [
          {
            type: "bool",
            placeholder: "?",
            rule: "exact",
          },
        ],
      },
      {
        name: "created_at",
        size: "w150",
        align: "center",
        fields: [
          {
            placeholder: " ()",
            rule: "gte",
            re: "^\\d{2}\\.\\d{2}\\.\\d{4}$",
            // converter: "date_to_date_object",
            converter: "date_to_iso_string",
          },
          {
            placeholder: " ()",
            rule: "lte",
            re: "^\\d{2}\\.\\d{2}\\.\\d{4}$",
            converter: "date_to_iso_string",
          },
        ],
      },
      {
        name: "updated_at",
        size: "w150",
        align: "center",
        fields: [
          {
            placeholder: " ()",
            rule: "gte",
            re: "^\\d{2}\\.\\d{2}\\.\\d{4}$",
            converter: "date_to_iso_string",
          },
          {
            placeholder: " ()",
            rule: "lte",
            re: "^\\d{2}\\.\\d{2}\\.\\d{4}$",
            converter: "date_to_iso_string",
          },
        ],
      },
    ];

    // FORM
    const init_obj = {
      id: null,
      name: "",
      trigger: "",
      is_multiple: false,
      is_active: false,
      message_if_fail: true,
      process: "",
    };

    const to_repository_convertion = {
      process: (x) => JSON.parse(x),
    };

    const from_repository_convertion = {
      process: (x) => JSON.stringify(x, null, 3), //    - 3 
    };

    const { current_id, current, handle_update_attribute } = useCurrentMixin(
      init_obj,
      uow.flow_repository,
      from_repository_convertion
    );

    const is_process_valid = computed(() =>
      validate_json(current.value.process, FlowProcess)
    );

    const is_edit_block_valid = computed(
      () => is_process_valid.value && current.value.name.length > 0
    );

    const add_action = async () => {
      const message = new AddFlow(
        ...[
          "name",
          "trigger",
          "is_active",
          "is_multiple",
          "message_if_fail",
          "process",
        ].map((k) => convertObject(current.value, to_repository_convertion)[k])
      );
      await MessageBus.handle(message, uow);
    };

    const update_action = async () => {
      const message = new UpdateFlow(
        ...[
          "id",
          "name",
          "trigger",
          "is_active",
          "is_multiple",
          "message_if_fail",
          "process",
        ].map((k) => convertObject(current.value, to_repository_convertion)[k])
      );
      await MessageBus.handle(message, uow);
    };

    const delete_action = async () => {
      const message = new DeleteFlow(current_id.value);
      await MessageBus.handle(message, uow);
      current_id.value = null;
    };

    const form_pages = computed(() => [
      {
        size: "w450",
        fields: [
          {
            type: "FormTextField",
            bind: "id",
          },
          {
            type: "FormTextField",
            value: ":",
          },
          {
            type: "FormTextInput",
            parameters: { height: 30 },
            bind: "name",
          },
          {
            type: "FormTextField",
            value: " :",
          },
          {
            type: "FormTextInput",
            parameters: { height: 30 },
            bind: "trigger",
          },
          {
            type: "FormTextField",
            value: " :",
          },
          {
            type: "FormBooleanInput",
            parameters: { size: 20 },
            bind: "is_active",
          },
          {
            type: "FormTextField",
            value: "   :",
          },
          {
            type: "FormBooleanInput",
            parameters: { size: 20 },
            bind: "is_multiple",
          },
          {
            type: "FormTextField",
            value: "    ",
          },
          {
            type: "FormBooleanInput",
            parameters: { size: 20 },
            bind: "message_if_fail",
          },
          {
            type: "FormTextField",
            value: "   JSON",
          },
          {
            type: "FormMultilineTextInput",
            parameters: {
              rows: 15,
              resize: "vertical",
              color: is_process_valid.value ? "green" : "red",
            },
            bind: "process",
          },
        ],
      },
    ]);

    onMounted(async () => {
      //   a
      await useBeforeEnterPage(uow, ["can_view_flow"]);
    });

    return {
      // ACTION
      search_action,
      add_action,
      update_action,
      delete_action,

      // PERMISSION
      can_add,
      can_change,
      can_delete,

      // EDIT
      current_id,
      current,
      is_process_valid,
      is_edit_block_valid,
      handle_update_attribute,
      form_pages,

      // SEARCH
      search_schema,
      model_objects,
      convert_model_to_item,
      search_time,
      is_searching,
    };
  },
};
</script>

<style scoped>
.page {
  display: flex;
  flex-direction: column;
  padding-left: 60px;
  padding-right: 60px;
}

.page__heading {
  padding-top: 60px;
  padding-bottom: 15px;
}
.page__container {
  display: flex;
}

.page__editblock {
  width: 450px;
  display: flex;
  flex-direction: column;
  padding-right: 15px;
}

.page__searchblock {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding-left: 15px;
  border-left: 1px solid black;
}

.page__button {
  margin: 5px;
}

.page__editrow {
  display: flex;
  width: 100%;
}
</style>
