<template>
  <div class="page">
    <div class="page__header">
      <heading-component
        :text="` ${
          action_progress < 100
            ? '(  ' + action_progress.toString() + '%)'
            : ''
        }`"
        size="middle"
      />
    </div>
    <div class="page__container" v-show="action_progress === 100">
      <div class="page__searchcontainer">
        <div class="page__searchblock">
          <form-boolean-input
            class="page__checkboxbutton"
            :parameters="{ size: 30 }"
            :value="is_all_selected"
            @click="handle_select_all_items()"
          />
          <form-text-input
            class="page__searchtype"
            :value="query.type"
            :parameters="{
              height: 30,
              placeholder: '',
            }"
            @update="query.type = $event"
          />
          <form-text-input
            class="page__searchdescription"
            :value="query.description"
            :parameters="{
              height: 30,
              placeholder: '',
            }"
            @update="query.description = $event"
          />
        </div>
        <div
          class="page__listrow listrow"
          v-for="item in filtered_items"
          :key="item"
        >
          <div
            class="listrow__delete"
            title=" "
            @click="item.remove_from_cart()"
          />
          <div
            class="listrow__lockicon"
            :class="{
              listrow__lockicon_closed: item.is_blocked === true,
            }"
          />
          <form-boolean-input
            class="listrow__checkbox"
            :value="item.id in selected_item_ids"
            :parameters="{ size: 30 }"
            @click="handle_select_item(item.id)"
          />
          <form-text-field
            class="listrow__type"
            :parameters="{ align: 'center' }"
            :value="item.type"
          />
          <router-link class="listrow__href" :to="item.route_obj">
            <form-text-field
              class="listrow__description"
              :value="item.description"
            />
          </router-link>
        </div>
      </div>
      <div
        v-if="count_of_selected_items == 0"
        class="page__actioncontainer actioncontainer"
      >
        <form-text-field
          class="actioncontainer__header"
          value="       "
          :parameters="{ align: 'left' }"
        />
      </div>
      <div
        v-if="count_of_selected_items > 0"
        class="page__actioncontainer actioncontainer"
      >
        <form-text-field
          class="actioncontainer__header"
          value="   :"
          :parameters="{ align: 'left' }"
        />
        <div class="actioncontainer__row">
          <form-text-field
            class="container__header"
            value="  :"
            :parameters="{ align: 'left' }"
          />
        </div>
        <div class="actioncontainer__row">
          <div class="actioncontainer__cell actioncontainer__cell_left">
            <text-button
              :disabled="seconds_before_document_copy_is_allowed > 0"
              :title="
                seconds_before_document_copy_is_allowed == 0
                  ? '  '
                  : '   ( ' +
                    seconds_before_document_copy_is_allowed.toString() +
                    ' .)'
              "
              @click="handle_copy_documents_to_usergroup()"
            />
          </div>
          <div class="actioncontainer__cell">
            <form-select-input
              class="copycontainer__usergroupselect"
              :value="copy_list_usergroup_id.toString()"
              :parameters="{
                options: copy_list_usergroups,
              }"
              @update="copy_list_usergroup_id = parseInt($event)"
            />
          </div>
        </div>
        <div class="actioncontainer__row">
          <form-text-field
            class="container__header"
            value=":"
            :parameters="{ align: 'left' }"
          />
        </div>
        <div class="actioncontainer__row">
          <div class="actioncontainer__cell actioncontainer__cell_left">
            <text-button
              title=" "
              :disabled="is_approval_action_username_chosen === false"
              @click="perform_action_on_selected_items('add_approval')"
            />
          </div>
          <div class="actioncontainer__cell">
            <form-filtered-select-input
              class="approvalorderblock__input"
              :parameters="{
                options: user_options,
                placeholder: '...',
                size: 3,
              }"
              :value="approval_action_username"
              @update="approval_action_username = $event"
            />
          </div>
        </div>
        <div class="actioncontainer__row">
          <div
            class="actioncontainer__approvalflowcontainer approvalflowcontainer"
          >
            <text-button
              v-if="!approval_flow"
              class="approvalflowcontainer__createbutton"
              title="  "
              @click="handle_create_approval_flow()"
            />
            <approval-flow
              v-if="approval_flow"
              class="approvalflowcontainer__flow"
              :is_active="approval_flow.is_active"
              :is_saved="approval_flow.is_saved"
              :success="approval_flow.success"
              :created_at="approval_flow.created_at"
              :updated_at="approval_flow.updated_at"
              :steps="approval_flow.steps"
              :states="{}"
              :users="users"
              :can_be_modified="true"
              @add:step="handle_add_approval_flow_step()"
              @delete:step="handle_delete_approval_flow_step($event)"
              @attach:user="handle_attach_user_to_approval_flow_step(...$event)"
              @delete:user="
                handle_delete_user_from_approval_flow_step(...$event)
              "
              @save="handle_save_approval_flow()"
              @start="handle_start_approval_flow()"
            />
          </div>
        </div>
        <div class="actioncontainer__row">
          <text-button
            title="  "
            @click="handle_stop_approval_flow()"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { onMounted, computed, reactive, ref, watch } from "vue";
import { VueUnitOfWork } from "../logic/service_layer/uow";
import { MessageBus } from "../logic/service_layer/message_bus";
import {
  filterArrayOfObjects,
  useBeforeEnterPage,
  useGetCart,
  useGetFolders,
  useGetUsers,
  useGetUserGroups,
  useSelectionMixin,
  usePerformActionMixin,
} from "../logic/service_layer/use_modules";
import HeadingComponent from "../components/HeadingComponent.vue";
import TextButton from "../components/buttons/TextButton.vue";
import ApprovalFlow from "../components/approval/ApprovalFlow.vue";
import FormTextField from "../components/fields/FormTextField.vue";
import FormTextInput from "../components/fields/FormTextInput.vue";
import FormBooleanInput from "../components/fields/FormBooleanInput.vue";
import FormSelectInput from "../components/fields/FormSelectInput.vue";
import FormFilteredSelectInput from "../components/fields/FormFilteredSelectInput.vue";

import {
  AddDocumentApproval,
  DuplicateManyDocumentsToUsergroup,
  RemoveDocumentFromCart,
  CreateDocumentApprovalFlow,
  AddDocumentApprovalFlowStep,
  DeleteDocumentApprovalFlowStep,
  AttachUserToDocumentApprovalFlowStep,
  DeleteUserFromDocumentApprovalFlowStep,
  SetDocumentApprovalFlow,
  StartDocumentApprovalFlow,
  StopDocumentApprovalFlow,
} from "../logic/domain/command";

import { ApprovalFlow as ApprovalFlowModel } from "../logic/domain/model";

export default {
  components: {
    HeadingComponent,
    TextButton,
    ApprovalFlow,
    FormTextField,
    FormTextInput,
    FormBooleanInput,
    FormSelectInput,
    FormFilteredSelectInput,
  },
  name: "CartPage",
  setup() {
    const uow = new VueUnitOfWork();
    //   ,  user = undefined  copy_list_usergroups
    const user = uow.user_repository.get_current();

    // USERGROUP
    const copy_list_usergroups = computed(() => {
      let arr = uow.usergroup_repository
        .values()
        .filter(
          (obj) =>
            !obj.value.is_private || obj.value.has_username(user.value.username)
        )
        .map((obj) => {
          return {
            id: obj.value.id,
            name: obj.value.name,
            value: obj.value.id.toString(),
          };
        });

      arr.sort((a, b) => a.name.localeCompare(b.name));
      return arr;
    });

    const copy_list_usergroup_id = ref(0);

    //    groups,  group_id 
    watch(copy_list_usergroups, (newValue) => {
      if (copy_list_usergroup_id.value === 0 && newValue.length > 0) {
        copy_list_usergroup_id.value = newValue[0].id;
      }
    });

    // -      
    const seconds_before_document_copy_is_allowed = ref(0);
    const decrease_seconds_before_document_copy = () => {
      if (seconds_before_document_copy_is_allowed.value > 0)
        seconds_before_document_copy_is_allowed.value--;
    };

    //   
    setInterval(decrease_seconds_before_document_copy, 1000);

    // DOCUMENT
    const generate_doc_route_obj = (doc) => {
      return {
        name: "DocumentPage",
        params: {
          folder_id: doc.folder_id,
          usergroup_id: doc.usergroup_id,
          document_id: doc.id,
        },
      };
    };

    const convert_doc_to_item = (doc) => {
      return {
        type: "",
        id: doc.id, //  ,     id  uuid
        folder_id: doc.folder_id,
        usergroup_id: doc.usergroup_id,
        is_blocked: doc.is_blocked,
        description:
          doc.raw_description == ""
            ? " .  ."
            : doc.raw_description,
        route_obj: generate_doc_route_obj(doc),
        remove_from_cart: async () => {
          await MessageBus.handle(
            new RemoveDocumentFromCart(doc.folder_id, doc.usergroup_id, doc.id),
            uow
          );
        },
        actions: {
          add_approval: async () => {
            await MessageBus.handle(
              new AddDocumentApproval(
                doc.folder_id,
                doc.usergroup_id,
                doc.id,
                approval_action_username.value
              ),
              uow
            );
          },
          create_approval_flow: async () => {
            await MessageBus.handle(
              new CreateDocumentApprovalFlow(doc.id),
              uow
            );
          },
          add_approval_flow_step: async () => {
            await MessageBus.handle(
              new AddDocumentApprovalFlowStep(doc.id),
              uow
            );
          },
          delete_approval_flow_step: async () => {
            await MessageBus.handle(
              new DeleteDocumentApprovalFlowStep(
                doc.id,
                approval_flow_step_index.value
              ),
              uow
            );
          },
          attach_user_to_approval_flow_step: async () => {
            await MessageBus.handle(
              new AttachUserToDocumentApprovalFlowStep(
                doc.id,
                approval_flow_step_index.value,
                approval_flow_step_username.value
              ),
              uow
            );
          },
          delete_user_from_approval_flow_step: async () => {
            await MessageBus.handle(
              new DeleteUserFromDocumentApprovalFlowStep(
                doc.id,
                approval_flow_step_index.value,
                approval_flow_step_username.value
              ),
              uow
            );
          },
          save_approval_flow: async () => {
            await MessageBus.handle(
              new SetDocumentApprovalFlow(
                doc.folder_id,
                doc.usergroup_id,
                doc.id
              ),
              uow
            );
          },
          start_approval_flow: async () => {
            await MessageBus.handle(
              new StartDocumentApprovalFlow(
                doc.folder_id,
                doc.usergroup_id,
                doc.id
              ),
              uow
            );
          },
          stop_approval_flow: async () => {
            await MessageBus.handle(
              new StopDocumentApprovalFlow(
                doc.folder_id,
                doc.usergroup_id,
                doc.id
              ),
              uow
            );
          },
        },
      };
    };

    const docs = computed(() =>
      uow.cart_repository
        .doc_values()
        .map((obj) => convert_doc_to_item(obj.value))
    );

    const query = reactive({
      type: "",
      description: "",
    });

    const filter_objs = (objs, query) =>
      // false -    
      filterArrayOfObjects(objs, query, false).sort((a, b) =>
        a.description.localeCompare(b.description)
      );

    const filtered_items = computed(() => filter_objs(docs.value, query));

    // SELECTION
    const {
      selected_item_ids,
      selected_items,
      is_all_selected,
      count_of_selected_items,
      handle_select_all_items,
      handle_select_item,
      reset_selection,
    } = useSelectionMixin(docs, filtered_items);

    // USERS
    const users = computed(() =>
      uow.user_repository.values().map((e) => e.value)
    );
    const user_options = computed(() =>
      [{ name: " ", value: "NOT_CHOSEN" }].concat(
        users.value
          .filter((e) => e.is_active === true)
          .map((e) => {
            return {
              name: (
                e.last_name +
                " " +
                e.first_name +
                " (" +
                e.username +
                ")"
              ).trim(),
              value: e.username,
            };
          })
          .sort((a, b) => a.name.localeCompare(b.name))
      )
    );

    const approval_action_username = ref("NOT_CHOSEN");
    const is_approval_action_username_chosen = computed(
      () => approval_action_username.value !== "NOT_CHOSEN"
    );

    // ACTION
    const {
      action_progress,
      perform_action_on_items: perform_action_on_selected_items,
    } =
      //   30 
      usePerformActionMixin(selected_items, 30);

    // COPY DOCUMENT TO USERGROUP
    const handle_copy_documents_to_usergroup = async () => {
      seconds_before_document_copy_is_allowed.value = 10;

      //       
      let doc_lists = {};
      selected_items.value.forEach((doc) => {
        const key =
          doc.folder_id.toString() + " " + doc.usergroup_id.toString();

        if (key in doc_lists) {
          doc_lists[key].push(doc.id);
        } else doc_lists[key] = [doc.id];
      });

      //      
      let messages = [];
      Object.keys(doc_lists).forEach((key) => {
        const temp = key.split(" ");
        const folder_id = parseInt(temp[0]);
        const usergroup_id = parseInt(temp[1]);

        messages.push(
          MessageBus.handle(
            new DuplicateManyDocumentsToUsergroup(
              folder_id,
              usergroup_id,
              copy_list_usergroup_id.value,
              doc_lists[key]
            ),
            uow
          )
        );
      });

      await Promise.all(messages);
    };

    // APPROVAL FLOW
    const approval_flow = ref(null);
    const approval_flow_step_index = ref(null);
    const approval_flow_step_username = ref(null);

    const handle_create_approval_flow = () => {
      approval_flow.value = new ApprovalFlowModel(
        null, // id
        false, // is_active
        false, // success,
        [[]], // steps,
        new Date(), // created_at
        new Date() // updated_at
      );
      perform_action_on_selected_items("create_approval_flow");
    };

    const handle_add_approval_flow_step = () => {
      approval_flow.value.add_step();
      perform_action_on_selected_items("add_approval_flow_step");
    };

    const handle_delete_approval_flow_step = (index) => {
      approval_flow.value.delete_step(index);
      approval_flow_step_index.value = index;
      perform_action_on_selected_items("delete_approval_flow_step");
    };

    const handle_attach_user_to_approval_flow_step = (index, username) => {
      approval_flow.value.add_username_to_step(index, username);
      approval_flow_step_index.value = index;
      approval_flow_step_username.value = username;
      perform_action_on_selected_items("attach_user_to_approval_flow_step");
    };

    const handle_delete_user_from_approval_flow_step = (index, username) => {
      approval_flow.value.delete_username_from_step(index, username);
      approval_flow_step_index.value = index;
      approval_flow_step_username.value = username;
      perform_action_on_selected_items("delete_user_from_approval_flow_step");
    };

    const handle_save_approval_flow = () => {
      approval_flow.value.flush_empty_steps();
      approval_flow.value.save();
      perform_action_on_selected_items("save_approval_flow");
    };

    const handle_start_approval_flow = () => {
      perform_action_on_selected_items("start_approval_flow");
    };

    const handle_stop_approval_flow = () => {
      perform_action_on_selected_items("stop_approval_flow");
    };

    onMounted(async () => {
      //   a
      useBeforeEnterPage(uow, ["can_get_folders"]);

      await Promise.all([useGetFolders(uow), useGetCart(uow)]);
      await Promise.all([useGetUserGroups(uow), useGetUsers(uow)]);
    });

    return {
      // DOCUMENT
      docs,
      filtered_items,
      query,

      // SELECTION
      selected_item_ids,
      selected_items,
      count_of_selected_items,
      handle_select_item,
      reset_selection,
      is_all_selected,
      handle_select_all_items,

      // USERS
      user,
      users,
      user_options,
      approval_action_username,
      is_approval_action_username_chosen,

      // USERGROUP
      copy_list_usergroups,
      copy_list_usergroup_id,

      // ACTION
      action_progress,
      seconds_before_document_copy_is_allowed,
      perform_action_on_selected_items,
      handle_copy_documents_to_usergroup,

      // APPROVAL FLOW
      approval_flow,
      approval_flow_step_index,
      handle_create_approval_flow,
      handle_add_approval_flow_step,
      handle_delete_approval_flow_step,
      handle_attach_user_to_approval_flow_step,
      handle_delete_user_from_approval_flow_step,
      handle_save_approval_flow,
      handle_start_approval_flow,
      handle_stop_approval_flow,
    };
  },
};
</script>

<style scoped>
.page {
  display: flex;
  flex-direction: column;
  padding-left: 60px;
  padding-right: 60px;
}

.page__header {
  display: flex;
  padding-top: 60px;
  padding-bottom: 15px;
}

.page__container {
  display: flex;
}

.page__searchcontainer {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  max-width: 600px;
}

.page__actioncontainer {
  min-width: 300px;
  margin-left: 50px;
  display: flex;
  flex-direction: column;
}

.actioncontainer__header {
  font-weight: bold;
  margin-bottom: 10px;
}

.actioncontainer__row {
  display: flex;
  margin-bottom: 5px;
  width: 100%;
}

.actioncontainer__cell {
  display: flex;
  flex-grow: 1;
}

.actioncontainer__cell_left {
  min-width: 200px;
  max-width: 200px;
}

.page__searchblock {
  display: flex;
}

.listrow {
  display: flex;
  width: 100%;
  align-items: center;
  padding-top: 3px;
}

.page__checkboxbutton {
  margin-left: 60px;
}

.page__searchtype,
.listrow__type {
  min-width: 150px;
  width: 150px;
  padding-left: 5px
}

.page__searchdescription,
.listrow__description {
  flex-grow: 1;
  padding-left: 5px
}

.listrow__lockicon {
  min-width: 30px;
  min-height: 30px;
  max-width: 30px;
  max-height: 30px;
}

.listrow__lockicon_closed {
  background: url("../../public/lock.png") center center/26px 26px no-repeat;
}

.listrow__delete:hover {
  transform: rotate(30deg);
}

.listrow__delete {
  min-width: 30px;
  min-height: 30px;
  max-width: 30px;
  max-height: 30px;
  transition: all 0.25s;
  cursor: pointer;
  background: url("../../public/delete.png") center center/26px 26px no-repeat;
}

/* .listrow__checkbox {
  width: 30px;
  height: 30px;
  padding: 3px;
} */

.container__header {
  font-weight: 500;
  padding: 3px;
}

.approvalflowcontainer {
  flex-grow: 1;
}
</style>
            