<template>
  <div class="page">
    <div class="page__caption">
      <heading-component class="page__heading" text="" size="middle" />
      <div
        class="page__state"
        :class="{
          page__state_running: flowitem_state === 'RUNNING',
          page__state_finished_fail: flowitem_state === 'FINISHED_FAIL',
          page__state_finished_success: flowitem_state === 'FINISHED_SUCCESS',
        }"
      />
      <text-button
        class="page__restartbutton"
        title=""
        @click="handle_restart_flowitem()"
      />
    </div>
    <div class="page__container">
      <div class="page__infoblock">
        <div class="page__inforow">
          <form-text-field class="infoitem__label" value=":" />
          <form-text-field class="infoitem__value" :value="flowitem_name" />
        </div>
        <div class="page__inforow">
          <form-text-field class="infoitem__label" value=":" />
          <form-text-field class="infoitem__value" :value="flowitem_trigger" />
        </div>
        <div class="page__inforow">
          <form-text-field class="infoitem__label" value=" :" />
          <form-text-field
            class="infoitem__value"
            :value="
              flowitem_started_at === null
                ? '-'
                : convertDateToDateTimeString(flowitem_started_at)
            "
          />
        </div>
        <div class="page__inforow">
          <form-text-field class="infoitem__label" value=" :" />
          <form-text-field
            class="infoitem__value"
            :value="
              flowitem_finished_at === null
                ? '-'
                : convertDateToDateTimeString(flowitem_finished_at)
            "
          />
        </div>
      </div>
      <div class="page__stepblock stepblock">
        <flow-item-step
          class="stepblock__item stepblock__item_trigger"
          name="input"
          :description="flowitem_name"
          :action="flowitem_trigger"
          :input="flowitem_input"
          :started_at="flowitem_started_at"
          :finished_at="flowitem_started_at"
          :success="true"
          ref="trigger_el"
          @updated="handle_updated()"
        />
        <flow-item-step
          class="stepblock__item stepblock__item_regular"
          v-for="(x, index) in steps"
          :key="x.name"
          :name="x.name"
          :variable_name="x.variable_name"
          :description="x.description"
          :action="x.action"
          :attributes="x.attributes"
          :input="x.input"
          :output="x.output"
          :started_at="x.started_at"
          :finished_at="x.finished_at"
          :success="x.success"
          :ref="
            (el) => {
              step_el_arr[index] = el;
            }
          "
          @updated="handle_updated()"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { MessageBus } from "../../logic/service_layer/message_bus";
import LeaderLine from "leader-line-vue";
import {
  ref,
  onMounted,
  onBeforeUpdate,
  onBeforeUnmount,
  computed,
  nextTick,
} from "vue";
import { useRoute } from "vue-router";
import { VueUnitOfWork } from "../../logic/service_layer/uow";
import HeadingComponent from "../../components/HeadingComponent.vue";
import {
  useValidateIntOr404,
  useBeforeEnterPage,
  useGetFlowItem,
  useGetFlowItemSteps,
  convertDateToDateTimeString,
} from "../../logic/service_layer/use_modules";
import { RestartFlowItem } from "../../logic/domain/command";
import FormTextField from "../../components/fields/FormTextField.vue";
import FlowItemStep from "../../components/flow/FlowItemStep.vue";
import TextButton from "../../components/buttons/TextButton.vue";

export default {
  components: {
    HeadingComponent,
    FormTextField,
    TextButton,
    FlowItemStep,
  },
  name: "FlowItemPage",

  setup() {
    const route = useRoute();
    const uow = new VueUnitOfWork();

    // FLOW ITEM

    const flowitem_id = useValidateIntOr404(route.params.flowitem_id);
    const flowitem = uow.flow_item_repository.get_current(); // Ref

    const flowitem_name = computed(() =>
      flowitem.value === null ? "-" : flowitem.value.name
    );

    const flowitem_trigger = computed(() =>
      flowitem.value === null ? "-" : flowitem.value.trigger
    );

    const flowitem_started_at = computed(() =>
      flowitem.value === null ? null : flowitem.value.started_at
    );

    const flowitem_finished_at = computed(() =>
      flowitem.value === null ? null : flowitem.value.finished_at
    );

    const flowitem_input = computed(() =>
      flowitem.value === null ? {} : flowitem.value.raw_input
    );

    const flowitem_state = computed(() => {
      if (flowitem.value === null) return "-";
      if (flowitem.value.is_active) return "RUNNING";
      return "FINISHED_" + (flowitem.value.success ? "SUCCESS" : "FAIL");
    });

    const process = computed(() =>
      flowitem.value === null ? null : flowitem.value.raw_process
    );

    const process_steps = computed(() => {
      return process.value === null ? null : process.value["steps"];
    });

    const steps = computed(() => {
      if (process_steps.value === null) return null;
      return process_steps.value.map((x) => {
        const name = x["name"];
        let input = {};
        let output = {};
        let started_at = null;
        let finished_at = null;
        let success = false;

        if (
          Object.prototype.hasOwnProperty.call(real_latest_steps.value, name)
        ) {
          input = real_latest_steps.value[name].input;
          output = real_latest_steps.value[name].output;
          started_at = real_latest_steps.value[name].started_at;
          finished_at = real_latest_steps.value[name].finished_at;
          success = real_latest_steps.value[name].success;
        }

        return {
          name: name,
          description: x["description"],
          action: x["action"]["name"],
          attributes: x["action"]["input"],
          input: input,
          output: output,
          started_at: started_at,
          finished_at: finished_at,
          success: success,
        };
      });
    });

    const handle_restart_flowitem = async () => {
      const message = new RestartFlowItem(flowitem_id);
      await MessageBus.handle(message, uow);
    };

    // STEPS

    const real_latest_steps = computed(() => {
      let result = {};
      //    
      uow.flow_item_step_repository.values().forEach((obj) => {
        if (Object.prototype.hasOwnProperty.call(result, obj.value.name)) {
          if (result[obj.value.name].started_at > obj.value.started_at) return;
        }
        result[obj.value.name] = obj.value;
      });
      return result;
    });

    // 

    const trigger_el = ref(null);
    const step_el_arr = ref([]);

    //    DOM  
    const make_dom_elem_obj = () => {
      let obj = { trigger: trigger_el.value.$el };
      step_el_arr.value.forEach((x) => (obj[x.name] = x.$el));
      return obj;
    };

    //  
    const lines_arr = ref([]);

    //   
    const draw = () => {
      //   
      lines_arr.value = [];

      //  DOM    
      const dom_steps = make_dom_elem_obj();

      process_steps.value.forEach((step) => {
        // console.log(step);

        const name = step["name"];
        // console.log(name);
        const endElem = dom_steps[name];

        //   ,      
        //  ,         
        const must = (step["to_be_finished_before"] || []).map((x) =>
          Object.assign(x, { type: "must" })
        );
        const should = (step["at_least_one_to_be_finished_before"] || []).map((x) =>
          Object.assign(x, { type: "should" })
        );
        const combined_to_be_finished_before = [].concat(must).concat(should);

        let line = null;

        if (combined_to_be_finished_before) {
          combined_to_be_finished_before.forEach((before) => {
            const before_name = before["name"];
            const before_success = before["success"];
            const before_result_key = before["result_key"];
            const dash = before['type'] === 'should'

            const startElem = dom_steps[before_name];
            const position = before_success ? "right" : "left";
            // 
            //  1.   
            //  2. success 
            //  3. result_key 
            //  
            //    - fail
            //    - success
            //    - 
            const color =
              Object.prototype.hasOwnProperty.call(
                real_latest_steps.value,
                before_name
              ) &&
              real_latest_steps.value[before_name].success === before_success &&
              real_latest_steps.value[before_name].result_key ===
                before_result_key
                ? before["success"]
                  ? "green"
                  : "red"
                : "gray";


            line = LeaderLine.setLine(startElem, endElem, {
              startPlug: "disc",
              startSocket: position,
              endSocket: position,
              middleLabel: before_result_key,
              color: color,
              dash: dash
            });
          });
        } else {
          line = LeaderLine.setLine(dom_steps["trigger"], endElem, {
            startPlug: "disc",
            startSocket: "right",
            endSocket: "right",
            color: "green",
          });
        }
        lines_arr.value.push(line);
      });
    };

    onMounted(async () => {
      await useBeforeEnterPage(uow, ["can_view_flowitem"]); //   a
      await Promise.all([
        useGetFlowItem(uow, flowitem_id),
        useGetFlowItemSteps(uow, flowitem_id),
      ]);

      //   
      draw();
    });

    //     refs  ,    
    onBeforeUpdate(() => {
      step_el_arr.value = [];
    });

    //    
    onBeforeUnmount(() => {
      lines_arr.value.forEach((line) => line.remove());
    });

    //      
    //      
    const handle_updated = () =>
      void nextTick(() => lines_arr.value.forEach((x) => x.position()));

    return {
      convertDateToDateTimeString,

      // FLOW iTEM
      flowitem,
      flowitem_id,
      flowitem_name,
      flowitem_trigger,
      flowitem_started_at,
      flowitem_finished_at,
      flowitem_state,
      flowitem_input,
      handle_restart_flowitem,

      // STEPS
      process,
      process_steps,
      steps,
      real_latest_steps,

      //  DRAW
      trigger_el,
      step_el_arr,
      make_dom_elem_obj,
      draw,
      lines_arr,
      handle_updated,
    };
  },
};
</script>
<style scoped>
.page {
  display: flex;
  flex-direction: column;
  padding-left: 60px;
  padding-right: 60px;
}

.page__caption {
  display: flex;
  padding-top: 60px;
  padding-bottom: 15px;
  align-items: flex-end;
}

.page__restartbutton {
  margin-left: 10px;
}

.page__container {
  display: flex;
}

.page__state {
  margin-left: 10px;
  width: 30px;
  height: 30px;
}

.page__state_running {
  background: url("../../../public/pie-chart.png") top left/30px 30px no-repeat;
}

.page__state_finished_success {
  background: url("../../../public/check.png") top left/30px 30px no-repeat;
}

.page__state_finished_fail {
  background: url("../../../public/cancel.png") top left/30px 30px no-repeat;
}

.page__infoblock {
  width: 500px;
  display: flex;
  flex-direction: column;
  padding-right: 15px;
}

.page__inforow {
  display: flex;
  width: 100%;
}

.infoitem {
  display: flex;
  align-items: center;
  flex-grow: 1;
  padding: 5px;
}

.infoitem__label {
  min-width: 150px;
  max-width: 150px;
  font-weight: 500;
}

.infoitem__value {
  flex-grow: 1;
}

.page__stepblock {
  width: 800px;
  display: flex;
  flex-direction: column;
  margin-left: 100px;
  margin-right: 100px;
}

.stepblock__item {
  padding: 10px;
  border: 1px solid black;
  border-radius: 10px;
  margin-bottom: 20px;
}

.stepblock__item_trigger {
  background-color: burlywood;
}

.stepblock__item_regular {
  background-color: lightblue;
}
</style>
