<template>
  <div class="page">
    <heading-component
      class="page__heading"
      text=" "
      size="middle"
    />
    <div class="page__container">
      <div class="page__editblock">
        <base-form
          class="page__form"
          :attributes="current"
          :settings="form_pages"
          :is_edit_mode="true"
          :is_header_required="false"
          @update="handle_update_attribute($event.name, $event.value)"
        >
        </base-form>

        <div class="page__editrow">
          <btn-component
            class="page__button"
            @click="add_action()"
            caption=""
            type="primary"
            :disabled="!is_edit_block_valid"
            v-if="can_add && !current_id"
          />
          <btn-component
            class="page__button"
            @click="update_action()"
            caption=""
            type="success"
            :disabled="!is_edit_block_valid"
            v-if="can_change && current_id"
          />
          <btn-component
            class="page__button"
            @click="delete_action()"
            caption=""
            type="danger"
            v-if="can_delete && current_id"
          />
        </div>
      </div>
      <search-component
        class="page__searchblock"
        :schema="search_schema"
        :model_objects="model_objects"
        :convert_obj_to_item="convert_model_to_item"
        :current_id="current_id"
        sort_by="name"
        :search_time="search_time"
        :is_searching="is_searching"
        :should_emit_search_event_on_any_keypress="true"
        @choose="current_id = $event"
        @search="search_action($event)"
      />
    </div>
  </div>
</template>

<script>
import { computed, onMounted, ref } from "vue";
import {
  AddMailBox,
  UpdateMailBox,
  DeleteMailBox,
} from "../../logic/domain/command";
import { MessageBus } from "../../logic/service_layer/message_bus";
import { VueUnitOfWork } from "../../logic/service_layer/uow";
import {
  convertObject,
  useBeforeEnterPage,
  filterArrayOfObjectsByQuery,
  useCurrentMixin,
  useGetMailBoxes,
} from "../../logic/service_layer/use_modules";

import HeadingComponent from "../../components/HeadingComponent.vue";
import BtnComponent from "../../components/buttons/BtnComponent.vue";
import BaseForm from "../../components/forms/BaseForm.vue";
import SearchComponent from "../../components/SearchComponent.vue";

export default {
  components: {
    HeadingComponent,
    BtnComponent,
    BaseForm,
    SearchComponent,
  },
  name: "MailBoxSettingsPage",

  setup() {
    const uow = new VueUnitOfWork();

    const convert_model_to_item = (obj) => {
      return {
        id: obj.id,
        attributes: {
          id: obj.id.toString(),
          name: obj.name,
          username: obj.username,
          password: obj.password,
          out_protocol: obj.out_protocol,
          out_encryption: obj.out_encryption,
          out_port: obj.out_port,
          out_server: obj.out_server,
          in_protocol: obj.in_protocol,
          in_encryption: obj.in_encryption,
          in_port: obj.in_port,
          in_server: obj.in_server,
        },
        actions: {},
      };
    };

    const query_for_internal_search = ref({});
    const model_objects = computed(() =>
      filterArrayOfObjectsByQuery(
        uow.mailbox_repository.values().map((obj) => obj.value),
        query_for_internal_search.value
      )
    );

    const can_add = uow.permission_repository.has("can_add_mailbox");
    const can_change = uow.permission_repository.has("can_change_mailbox");
    const can_delete = uow.permission_repository.has("can_delete_mailbox");

    // STATUS
    const search_time = ref(0);
    const is_searching = ref(false);

    const search_action = (query) => (query_for_internal_search.value = query);

    const search_schema = [
      {
        name: "id",
        size: "w50",
        fields: [
          {
            placeholder: "ID",
            rule: "exact",
          },
        ],
      },
      {
        name: "name",
        size: "w300",
        grow: true,
        fields: [
          {
            placeholder: "",
            rule: "icontains",
          },
        ],
      },
      {
        name: "username",
        size: "w300",
        grow: true,
        align: "left",
        fields: [
          {
            placeholder: " ",
            rule: "exact",
          },
        ],
      },
    ];

    // FORM
    const init_obj = {
      id: null,
      name: "",
      username: "",
      password: "",
      out_protocol: "SMTP",
      in_protocol: "IMAP",
      out_encryption: "NONE",
      in_encryption: "NONE",
      out_port: 25,
      in_port: 143,
      out_server: "",
      in_server: "",
    };

    const to_repository_convertion = { in_port: parseInt, out_port: parseInt };
    const from_repository_convertion = {
      in_port: (x) => x.toString(),
      out_port: (x) => x.toString(),
    };

    const { current_id, current, handle_update_attribute } = useCurrentMixin(
      init_obj,
      uow.mailbox_repository,
      from_repository_convertion
    );

    const is_edit_block_valid = computed(
      () =>
        current.value.name.length > 0 &&
        current.value.username.length > 0 &&
        current.value.password.length > 0 &&
        current.value.out_protocol.length > 0 &&
        current.value.in_protocol.length > 0 &&
        current.value.out_encryption.length > 0 &&
        current.value.in_encryption.length > 0 &&
        current.value.out_port > 0 &&
        current.value.out_port < 1000 &&
        current.value.in_port > 0 &&
        current.value.in_port < 1000 &&
        current.value.out_server.length > 0 &&
        current.value.in_server.length > 0
    );

    const add_action = async () => {
      const message = new AddMailBox(
        ...[
          "out_protocol",
          "out_encryption",
          "out_port",
          "out_server",
          "in_protocol",
          "in_encryption",
          "in_port",
          "in_server",
          "username",
          "password",
          "name",
        ].map((k) => convertObject(current.value, to_repository_convertion)[k])
      );
      await MessageBus.handle(message, uow);
    };

    const update_action = async () => {
      const message = new UpdateMailBox(
        ...[
          "id",
          "out_protocol",
          "out_encryption",
          "out_port",
          "out_server",
          "in_protocol",
          "in_encryption",
          "in_port",
          "in_server",
          "username",
          "password",
          "name",
        ].map((k) => convertObject(current.value, to_repository_convertion)[k])
      );
      await MessageBus.handle(message, uow);
    };

    const delete_action = async () => {
      const message = new DeleteMailBox(current_id.value);
      await MessageBus.handle(message, uow);
      current_id.value = null;
    };

    const encryption_options = [
      { name: "NONE", value: "NONE" },
      { name: "STARTTLS", value: "STARTTLS" },
      { name: "SSL/TLS", value: "SSL/TLS" },
    ];

    const form_pages = [
      {
        size: "w600",
        fields: [
          {
            type: "FormTextField",
            value: ":",
          },
          {
            type: "FormTextInput",
            bind: "name",
          },
          {
            type: "FormTextField",
            value: " :",
          },
          {
            type: "FormTextInput",
            bind: "username",
          },
          {
            type: "FormTextField",
            value: ":",
          },
          {
            type: "FormTextInput",
            parameters: { secure: true },
            bind: "password",
          },
          {
            type: "FormTextField",
            value: "  :",
          },
          {
            type: "FormSelectInput",
            parameters: { options: [{ name: "SMTP", value: "SMTP" }] },
            bind: "out_protocol",
          },
          {
            type: "FormTextField",
            value: "  :",
          },
          {
            type: "FormSelectInput",
            parameters: { options: encryption_options },
            bind: "out_encryption",
          },
          {
            type: "FormTextField",
            value: "  :",
          },
          {
            type: "FormTextInput",
            bind: "out_port",
          },
          {
            type: "FormTextField",
            value: "  :",
          },
          {
            type: "FormTextInput",
            bind: "out_server",
          },
          {
            type: "FormTextField",
            value: "  :",
          },
          {
            type: "FormSelectInput",
            parameters: { options: [{ name: "IMAP", value: "IMAP" }] },
            bind: "in_protocol",
          },
          {
            type: "FormTextField",
            value: "  :",
          },
          {
            type: "FormSelectInput",
            parameters: { options: encryption_options },
            bind: "in_encryption",
          },
          {
            type: "FormTextField",
            value: "  :",
          },
          {
            type: "FormTextInput",
            bind: "in_port",
          },
          {
            type: "FormTextField",
            value: "  :",
          },
          {
            type: "FormTextInput",
            bind: "in_server",
          },
        ],
      },
    ];

    onMounted(async () => {
      //   a
      await useBeforeEnterPage(uow, ["can_view_mailbox", "can_view_settings"]);
      await useGetMailBoxes(uow);
    });

    return {
      // ACTION
      search_action,
      add_action,
      update_action,
      delete_action,

      // PERMISSION
      can_add,
      can_change,
      can_delete,

      // EDIT
      current_id,
      current,
      is_edit_block_valid,
      handle_update_attribute,
      form_pages,

      // SEARCH
      search_schema,
      model_objects,
      convert_model_to_item,
      search_time,
      is_searching,
    };
  },
};
</script>

<style scoped>
.page {
  display: flex;
  flex-direction: column;
  padding-left: 60px;
  padding-right: 60px;
}

.page__heading {
  padding-top: 60px;
  padding-bottom: 15px;
}
.page__container {
  display: flex;
}

.page__editblock {
  width: 420px;
  display: flex;
  flex-direction: column;
  padding-right: 15px;
}

.page__searchblock {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding-left: 15px;
  border-left: 1px solid black;
}

.page__button {
  margin: 5px;
}

.page__editrow {
  display: flex;
  width: 100%;
}
</style>
