import {
  NegativeResponse,
  GetCountersRequest,
  GetCountersResponse,
  AddCounterRequest,
  AddCounterResponse,
  UpdateCounterRequest,
  UpdateCounterResponse,
  DeleteCounterRequest,
  DeleteCounterResponse,
} from "../../adapters/api";

import {
  ApiError,
  CounterAddedSuccess,
  CounterUpdatedSuccess,
  CounterDeletedSuccess,
} from "../../domain/event";

import {
  Counter,
} from "../../domain/model";

class WrongResponseError extends Error {}


const convert_counter_response_obj_to_model = (obj) => {
  const item = new Counter(
    obj.id,
    obj.name,
    obj.description,
    obj.value,
    obj.increment,
  );

  return item;
};

const get_counters = async (event, uow) => {
  uow.counter_repository.reset_keeping_refs();

  const request = new GetCountersRequest({});
  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return false;
  }

  if (response instanceof GetCountersResponse) {
    response.data.forEach((obj) => {
      const counter = convert_counter_response_obj_to_model(obj);
      uow.counter_repository.set(counter.id, counter);
    });
    return true;
  }

  throw new WrongResponseError();
};

const add_counter = async (event, uow) => {
  const request = new AddCounterRequest({
    name: event.name,
    description: event.description,
    value: event.value,
    increment: event.increment,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return false;
  }

  if (response instanceof AddCounterResponse) {
    const counter = convert_counter_response_obj_to_model(response.data);
    uow.counter_repository.set(counter.id, counter);
    uow.push_message(new CounterAddedSuccess());
    return true;
  }

  throw new WrongResponseError();
};

const update_counter = async (event, uow) => {
  const request = new UpdateCounterRequest({
    id: event.id,
    name: event.name,
    description: event.description,
    value: event.value,
    increment: event.increment,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return false;
  }

  if (response instanceof UpdateCounterResponse) {
    const counter = convert_counter_response_obj_to_model(response.data);
    uow.counter_repository.set(counter.id, counter);
    uow.push_message(new CounterUpdatedSuccess());
    return true;
  }

  throw new WrongResponseError();
};

const delete_counter = async (event, uow) => {
  const request = new DeleteCounterRequest({
    id: event.id,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return false;
  }

  if (response instanceof DeleteCounterResponse) {
    uow.counter_repository.delete(event.id);
    uow.push_message(new CounterDeletedSuccess());
    return true;
  }

  throw new WrongResponseError();
};

export {
  convert_counter_response_obj_to_model,
  get_counters,
  add_counter,
  update_counter,
  delete_counter,
};
