import {
  NegativeResponse,
  EmptyCartRequest,
  EmptyCartResponse,
  GetCartDocumentsRequest,
  GetCartDocumentsResponse,
  AddDocumentToCartRequest,
  AddDocumentToCartResponse,
  AddManyDocumentsToCartRequest,
  AddManyDocumentsToCartResponse,
  RemoveDocumentFromCartRequest,
  RemoveDocumentFromCartResponse,
} from "../../adapters/api";

import {
  ApiError,
  DocumentAddedToCartSuccess,
  DocumentRemovedFromCartSuccess,
  ManyDocumentsAddedToCartSuccess,
} from "../../domain/event";

import { convert_doc_response_obj_to_model } from "./document_handlers";

class WrongResponseError extends Error {}

const empty_cart = async (event, uow) => {
  const request = new EmptyCartRequest();
  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return false;
  }

  if (response instanceof EmptyCartResponse) {
    uow.cart_repository.reset_keeping_refs_for_docs();
    return true;
  }

  throw new WrongResponseError();
};

const get_cart_documents = async (event, uow) => {
  const request = new GetCartDocumentsRequest();
  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return false;
  }

  if (response instanceof GetCartDocumentsResponse) {
    response.data.forEach((obj) => {
      const doc = convert_doc_response_obj_to_model(obj);
      uow.cart_repository.set_doc(doc.id, doc);
    });
    return true;
  }

  throw new WrongResponseError();
};

const add_document_to_cart = async (event, uow) => {
  const request = new AddDocumentToCartRequest({
    folder_id: event.folder_id,
    usergroup_id: event.usergroup_id,
    document_id: event.document_id,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return false;
  }

  if (response instanceof AddDocumentToCartResponse) {
    const doc = convert_doc_response_obj_to_model(response.data);
    uow.cart_repository.set_doc(doc.id, doc);

    uow.push_message(new DocumentAddedToCartSuccess());
    return true;
  }

  throw new WrongResponseError();
};

const add_many_documents_to_cart = async (event, uow) => {
  const request = new AddManyDocumentsToCartRequest({
    folder_id: event.folder_id,
    usergroup_id: event.usergroup_id,
    document_ids: event.document_ids,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return false;
  }

  if (response instanceof AddManyDocumentsToCartResponse) {
    response.data.forEach((obj) => {
      const doc = convert_doc_response_obj_to_model(obj);
      uow.cart_repository.set_doc(doc.id, doc);
    });
    uow.push_message(new ManyDocumentsAddedToCartSuccess());
    return true;
  }

  throw new WrongResponseError();
};

const remove_document_from_cart = async (event, uow) => {
  const request = new RemoveDocumentFromCartRequest({
    folder_id: event.folder_id,
    usergroup_id: event.usergroup_id,
    document_id: event.document_id,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return false;
  }

  if (response instanceof RemoveDocumentFromCartResponse) {
    uow.cart_repository.delete_doc(event.document_id);
    uow.push_message(new DocumentRemovedFromCartSuccess());
    return true;
  }

  throw new WrongResponseError();
};

export {
  empty_cart,
  get_cart_documents,
  add_document_to_cart,
  add_many_documents_to_cart,
  remove_document_from_cart,
};
