import { List as FileList } from "./file";

const Single = {
  type: "object",
  properties: {
    id: {
      type: "string",
    },
    union: { type: ["null", "string"] },
    folder_id: { type: "integer" },
    usergroup_id: { type: "integer" },
    user_id: { type: ["null", "integer"] },
    data: {
      type: "object",
      patternProperties: {
        "^[a-zA-Z0-9_]+$": {
          type: ["string", "number", "boolean", "object", "array"],
        },
      },
    },
    description: {
      type: "string",
    },
    files: FileList,
    is_blocked: {
      type: "boolean",
    },
    created_at: {
      type: "string",
    },
    updated_at: {
      type: "string",
    },
  },
  required: [
    "id",
    "union",
    "folder_id",
    "usergroup_id",
    "user_id",
    "data",
    "description",
    "files",
    "is_blocked",
    "created_at",
    "updated_at",
  ],
  additionalProperties: false,
};

const List = {
  type: "array",
  items: Single,
};

export { Single, List };
