import { VueFlatRepository } from "./flat_repository";
import { CurrentMixin } from "./mixins/current";

const AuthenticatedMixin = {
  is_authenticated() {
    return this.is_current_settled();
  },
};

const RoleMixin = {
  get_role(username) {
    const user = this.get(username).value;
    return user.role;
  },

  set_role(username, role) {
    const user = this.get(username).value;
    user.role = role;
  },

  get_role_inside_group(username) {
    const user = this.get(username).value;
    return user.role_inside_group;
  },

  set_role_inside_group(username, role) {
    const user = this.get(username).value;
    user.role_inside_group = role;
  },

  get_role_outside_group(username) {
    const user = this.get(username).value;
    return user.role_outside_group;
  },

  set_role_outside_group(username, role) {
    const user = this.get(username).value;
    user.role_outside_group = role;
  },
};

const ActiveMixin = {
  get_isactive(username) {
    const user = this.get(username).value;
    return user.is_active;
  },

  set_isactive(username, is_active) {
    const user = this.get(username).value;
    user.is_active = is_active;
  },
};

const OnBehalfMixin = {
  set_on_behalf_users(obj) {
    this._set_box("__BEHALF__", "__BEHALF__", obj);
  },

  get_on_behalf_users() {
    return this._get_box("__BEHALF__", "__BEHALF__");
  },

};

class VueUserRepository extends VueFlatRepository {}
Object.assign(
  VueUserRepository.prototype,
  CurrentMixin,
  AuthenticatedMixin,
  RoleMixin,
  ActiveMixin,
  OnBehalfMixin,
);

export { VueUserRepository };
