import { VueFlatRepository } from "./flat_repository";
import { CurrentMixin } from "./mixins/current";

const SlugMixin = {
  get_id_by_slug(slug) {
    // console.log('USERGROUPS', this.values())
    const usergroup = this.values().find(
      (usergroup) => usergroup.value.slug === slug
    );
    return usergroup === undefined ? null : usergroup.value.id;
  },
};

class VueUserGroupRepository extends VueFlatRepository {}
Object.assign(VueUserGroupRepository.prototype, CurrentMixin, SlugMixin);

export { VueUserGroupRepository };
