const Type = { const: "FormTextInput" };

const Parameters = {
  type: "object",
  properties: {
    placeholder: { type: "string" },
    secure: { type: "boolean" },
    is_disabled: { type: "boolean" },
    color: { type: "string" },
    default: { type: "string" },
  },
  additionalProperties: false,
};

const FormTextInput = {
  oneOf: [
    {
      type: "object",
      properties: {
        type: Type,
        parameters: Parameters,
        value: { type: "string" },
      },
      required: ["type", "value"],
      additionalProperties: false,
    },
    {
      type: "object",
      properties: {
        type: Type,
        parameters: Parameters,
        bind: { type: "string" },
      },
      required: ["type", "bind"],
      additionalProperties: false,
    },
  ],
};

export { FormTextInput };
