const FlowProcess001 = {
  type: "object",
  properties: {
    version: {
      type: "integer",
      minimum: 1,
      maximum: 1,
    },
    start_condition: { type: "string" },
    steps: {
      type: "array",
      items: {
        type: "object",
        required: ["name", "description", "action"],
        properties: {
          name: {
            type: "string",
            not: { enum: ["input"] },
          },
          variable_name: {
            type: "string",
            not: { enum: ["input"] },
          },
          description: { type: "string" },

          to_be_finished_before: {
            type: "array",
            items: FinishedBeforeItem,
          },

          at_least_one_to_be_finished_before: {
            type: "array",
            items: FinishedBeforeItem,
          },

          action: {
            oneOf: [
              DivisionByZeroAction,
              IfAction,
              CreateVariableAction,
              SendEmailAction,
              SendEmailUserCreatedAction,
              SendEmailDocumentCreatedOrUpdatedAction,
              SendEmailDocumentApprovalCreatedAction,
              SendEmailDocumentApprovalUpdatedAction,
              SendEmailDocumentApprovalDeletedAction,
              SendEmailDocumentApprovalFlowStartedAction,
              SendEmailDocumentApprovalFlowStoppedAction,
              SendEmailDocumentApprovalFlowExecutedAction,
              SendEmailDocumentCreatedFromMailMessageAction,
              DispatchEmailsAction,
              GetUserByIdAction,
              GetUserByUsernameAction,
              GetUsergroupByIdAction,
              GetFolderByIdAction,
              GetDocumentByIdAction,
              GetDocumentByApprovalAction,
              UpdateDocumentAttributesAction,
              UpdateDocumentFilesAction,
              GetMailBoxByIdAction,
              GetMailChannelByIdAction,
              // FetchMailMessageAction,
              FetchMailMessagesAction,
              SendMailMessageAction,
              FetchMailChannelAction,
              FetchAllMailChannelsAction,
              CreateDocumentFromMailMessageAction,
              ExtractHtmlPortionAction,
              FetchCounter,
              ConfirmCounter,
              CopyDocumentToUsergroupAction,
            ],
          },
        },
      },
    },
  },
  required: ["version", "steps"],
  additionalProperties: false,
};

export { FlowProcess001 };
