const Single = {
  type: "object",
  properties: {
    id: { type: "integer" },
    name: { type: "string" },
    description: { type: "string" },
    flowitem_id: { type: "integer" },
    success: { type: "boolean" },
    result_key: { type: "string" },
    action: { type: "string" },
    input: { type: "object" },
    output: { type: ["string", "object"] },
    started_at: {
      type: "string",
    },
    finished_at: {
      type: ["null", "string"],
    },
  },
  required: [
    "id",
    "name",
    "description",
    "flowitem_id",
    "success",
    "result_key",
    "action",
    "input",
    "output",
    "started_at",
    "finished_at",
  ],
  additionalProperties: false,
};

const List = {
  type: "array",
  items: Single,
};

export { Single, List };
