import { Single as FileSingle } from "./file";
import { Single as UserSingle } from "./user";
import { Single as DocumentSingle } from "./document";

const Single = {
  type: "object",
  properties: {
    id: {
      type: "integer",
    },
    user: UserSingle,
    created_by: UserSingle,
    delegated_from: {
      type: ["null", "integer"],
    },
    state: {
      enum: ["APPROVED", "REJECTED", "DELEGATED", "NONE"],
    },
    created_at: {
      type: "string",
    },
    updated_at: {
      type: "string",
    },
    files: {
      type: "array",
      items: FileSingle,
    },
    document: DocumentSingle,
    active_approvals_count_of_document: {
      type: "integer",
    },
  },
  required: ["id", "user", "state", "created_at", "updated_at", "files"],
  additionalProperties: false,
};

const List = {
  type: "array",
  items: Single,
};

const SearchList = {
  type: "object",
  required: ["approvals", "count"],
  additionalProperties: false,
  properties: {
    approvals: List,
    count: { type: "integer" },
  },
};

export { Single, List, SearchList };
