import {
  NegativeResponse,
  GetDocumentApprovalFlowRequest,
  GetDocumentApprovalFlowResponse,
  SetDocumentApprovalFlowRequest,
  SetDocumentApprovalFlowResponse,
  StartDocumentApprovalFlowRequest,
  StartDocumentApprovalFlowResponse,
  StopDocumentApprovalFlowRequest,
  StopDocumentApprovalFlowResponse,
} from "../../adapters/api";

import {
  ApiError,
  EmptyApprovalFlowSaved,
  ApprovalFlowStarted,
  ApprovalFlowStopped,
} from "../../domain/event";

import { ApprovalFlow } from "../../domain/model";

class WrongResponseError extends Error {}

const convert_approval_flow_response_obj_to_model = (obj) => {
  const flow = new ApprovalFlow(
    obj.id,
    obj.is_active,
    obj.success,
    obj.steps,
    new Date(obj.created_at),
    new Date(obj.updated_at)
  );

  return flow;
};

const create_document_approval_flow = (event, uow) => {
  const dt = new Date();
  uow.doc_approval_flow_repository.set(
    event.document_id,
    new ApprovalFlow(
      null, // id
      false, // is_active
      false, // success,
      [[]], //steps,
      dt, // created_at
      dt // updated_at
    )
  );
};

const add_document_approval_flow_step = (event, uow) => {
  const flow = uow.doc_approval_flow_repository.get(event.document_id);
  flow.value.add_step();
};

const delete_document_approval_flow_step = (event, uow) => {
  const flow = uow.doc_approval_flow_repository.get(event.document_id);
  flow.value.delete_step(event.index);
};

const attach_user_to_document_approval_flow_step = (event, uow) => {
  const flow = uow.doc_approval_flow_repository.get(event.document_id);
  flow.value.add_username_to_step(event.index, event.username);
};

const delete_user_from_document_approval_flow_step = (event, uow) => {
  const flow = uow.doc_approval_flow_repository.get(event.document_id);
  flow.value.delete_username_from_step(event.index, event.username);
};

const get_document_approval_flow = async (event, uow) => {
  const request = new GetDocumentApprovalFlowRequest({
    folder_id: event.folder_id,
    usergroup_id: event.usergroup_id,
    document_id: event.document_id,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return false;
  }

  if (response instanceof GetDocumentApprovalFlowResponse) {
    const flow =
      response.data === null
        ? null
        : convert_approval_flow_response_obj_to_model(response.data);
    uow.doc_approval_flow_repository.set(event.document_id, flow);
    return true;
  }

  throw new WrongResponseError();
};

const set_document_approval_flow = async (event, uow) => {
  const flow = uow.doc_approval_flow_repository.get(event.document_id);
  flow.value.flush_empty_steps();

  if (flow.value.steps.length === 0) {
    uow.push_message(new EmptyApprovalFlowSaved());
    return;
  }

  const request = new SetDocumentApprovalFlowRequest({
    folder_id: event.folder_id,
    usergroup_id: event.usergroup_id,
    document_id: event.document_id,
    steps: flow.value.steps,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return false;
  }

  if (response instanceof SetDocumentApprovalFlowResponse) {
    const flow =
      response.data === null
        ? null
        : convert_approval_flow_response_obj_to_model(response.data);
    uow.doc_approval_flow_repository.set(event.document_id, flow);
    return true;
  }

  throw new WrongResponseError();
};

const start_document_approval_flow = async (event, uow) => {
  const request = new StartDocumentApprovalFlowRequest({
    folder_id: event.folder_id,
    usergroup_id: event.usergroup_id,
    document_id: event.document_id,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return false;
  }

  if (response instanceof StartDocumentApprovalFlowResponse) {
    const flow =
      response.data === null
        ? null
        : convert_approval_flow_response_obj_to_model(response.data);
    uow.doc_approval_flow_repository.set(event.document_id, flow);

    uow.push_message(new ApprovalFlowStarted());
    return true;
  }

  throw new WrongResponseError();
};

const stop_document_approval_flow = async (event, uow) => {
  const request = new StopDocumentApprovalFlowRequest({
    folder_id: event.folder_id,
    usergroup_id: event.usergroup_id,
    document_id: event.document_id,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return false;
  }

  if (response instanceof StopDocumentApprovalFlowResponse) {
    const flow =
      response.data === null
        ? null
        : convert_approval_flow_response_obj_to_model(response.data);
    uow.doc_approval_flow_repository.set(event.document_id, flow);

    uow.push_message(new ApprovalFlowStopped());
    return true;
  }

  throw new WrongResponseError();
};

export {
  convert_approval_flow_response_obj_to_model,
  create_document_approval_flow,
  add_document_approval_flow_step,
  delete_document_approval_flow_step,
  attach_user_to_document_approval_flow_step,
  delete_user_from_document_approval_flow_step,
  get_document_approval_flow,
  set_document_approval_flow,
  start_document_approval_flow,
  stop_document_approval_flow,
};
