<template>
  <div class="nav">
    <div v-for="item in links" v-bind:key="item.route_name" class="nav__links">
      <router-link class="nav__item" :to="{ name: item.route_name }">{{
        item.title
      }}</router-link>
    </div>

    <div class="nav__usergroup" v-if="show_usergroup">
      <form-select-input
        class="nav__usergroupselect"
        :parameters="{
          options: groups,
          is_disabled: !can_change_usergroup,
        }"
        :value="group_id.toString()"
        @update="handle_change_current($event)"
      />
    </div>

    <div
      v-if="approval_actions_count !== null && approval_orders_count !== null"
      class="nav__approvalinfo"
    >
      <router-link class="nav__approvaltext" :to="{ name: 'ApprovalsPage' }">
        :
      </router-link>

      <span
        class="nav__approvalcount"
        :class="{
          nav__approvalcount_warning:
            approval_actions_count === 0 && approval_orders_count > 0,
          nav__approvalcount_alarm: approval_actions_count > 0,
        }"
        >{{ approval_actions_count }}/{{ approval_orders_count }}</span
      >
    </div>
    <div class="nav__cartinfo">
      <router-link :to="{ name: 'CartPage' }">
        <div class="nav__cartimg" title="  "></div>
      </router-link>
      <span
        class="nav__cartcount"
        :class="{
          nav__cartcount_increased: cart_obj_count_increased_flag,
          nav__cartcount_decreased: cart_obj_count_decreased_flag,
        }"
        >{{ cart_obj_count }}</span
      >
      <div
        class="nav__cleancartimg"
        title=" "
        @click="handle_empty_cart()"
      ></div>
    </div>
    <div class="nav__empty"></div>

    <div class="nav__welcome">
      <span v-if="user"
        > ,
        <router-link :to="{ name: 'ProfilePage' }">
          {{ user.last_name }} {{ user.first_name }}
        </router-link>
        .
      </span>
    </div>

    <div class="nav__version">v.{{ version }}</div>
    <div
      class="nav__behalfuserimg"
      title="   "
      @click="handle_delete_on_behalf_user()"
    ></div>
  </div>
</template>

<script>
import { onMounted, computed, watch, ref } from "vue";
import {
  SetCurrentUserGroup,
  EmptyCart,
  DeleteOnBehalfUser,
} from "../../logic/domain/command";
import { MessageBus } from "../../logic/service_layer/message_bus";
import { VueUnitOfWork } from "../../logic/service_layer/uow";
import {
  useBeforeEnterPage,
  useGetUserGroups,
  useGetInfo,
  useGetCart,
} from "../../logic/service_layer/use_modules";
import FormSelectInput from "../fields/FormSelectInput.vue";

export default {
  name: "NavigationComponent",
  components: { FormSelectInput },
  props: {
    version: {
      type: String,
      required: true,
    },
    links: {
      type: Array,
      required: true,
    },
    show_usergroup: {
      type: Boolean,
      required: true,
    },
    can_change_usergroup: {
      type: Boolean,
      required: true,
    },
  },
  setup(props) {
    const uow = new VueUnitOfWork();
    const user = uow.user_repository.get_current(); // Ref

    const groups = computed(() => {
      if (user.value === null) return [];
      let arr = uow.usergroup_repository
        .values()
        .filter(
          (obj) =>
            //    ,     
            !obj.value.is_private || obj.value.has_username(user.value.username)
        )
        .map((obj) => {
          return {
            id: obj.value.id,
            name: obj.value.name,
            value: obj.value.id.toString(),
          };
        });

      arr.sort((a, b) => a.name.localeCompare(b.name));
      return arr;
    });

    const group_id = computed(() => {
      const current_group = uow.usergroup_repository.get_current();
      return current_group.value === null ? 0 : current_group.value.id;
    });

    //    groups,  group_id 
    watch(groups, (newValue) => {
      if (group_id.value === 0 && newValue.length > 0) {
        handle_change_current(newValue[0].id);
      }
    });

    const handle_change_current = (id) => {
      const group = uow.usergroup_repository.get(id);
      MessageBus.handle(new SetCurrentUserGroup(group.value), uow);
    };

    // INFO
    const approval_actions_count =
      uow.info_repository.get_approval_actions_count();

    const approval_orders_count =
      uow.info_repository.get_approval_orders_count();

    // CART
    const cart_obj_count = computed(
      () => uow.cart_repository.doc_values().length
    );

    const cart_obj_count_increased_flag = ref(false);
    const cart_obj_count_decreased_flag = ref(false);
    
    watch(
      () => cart_obj_count.value,
      (newValue, oldValue) => {
        if (newValue > oldValue) cart_obj_count_increased_flag.value = true;
        if (newValue < oldValue) cart_obj_count_decreased_flag.value = true;
        setTimeout(() => {
          cart_obj_count_decreased_flag.value = false;
          cart_obj_count_increased_flag.value = false;
        }, 1000);
      }
    );

    const handle_empty_cart = () => {
      MessageBus.handle(new EmptyCart(), uow);
    };

    const handle_delete_on_behalf_user = (username) =>
      MessageBus.handle(new DeleteOnBehalfUser(username), uow);

    onMounted(async () => {
      await useBeforeEnterPage(uow, []); //   a
      if (props.show_usergroup === true) {
        await useGetUserGroups(uow);
      }
      await Promise.all([useGetInfo(uow), useGetCart(uow)]);
    });

    return {
      user,
      groups,
      group_id,
      handle_change_current,
      // INFO
      approval_actions_count,
      approval_orders_count,
      // CART
      cart_obj_count,
      cart_obj_count_increased_flag,
      cart_obj_count_decreased_flag,
      handle_empty_cart,
      // BEHALF USERS
      handle_delete_on_behalf_user,
    };
  },
};
</script>

<style>
.nav {
  display: flex;
  justify-content: right;
  align-items: center;
  background-color: #f8f9fa;
  border-bottom-width: 1px;
  border-bottom-style: solid;
  border-bottom-color: rgb(222, 226, 230);
}

.nav__welcome {
  text-align: right;
  padding: 10px;
  padding-right: 10px;
}

.nav__empty {
  flex-grow: 1;
}

.nav__item {
  display: block;
  box-sizing: border-box;
  text-decoration: none;
  border: solid transparent;
  border-width: 1px 1px 0 1px;
  border-top-left-radius: 0.25rem;
  border-top-right-radius: 0.25rem;
  padding: 10px;
  text-align: left;
  font-size: 16px;
  font-weight: 400;
  color: #007bff;
}

.nav__item:link,
.nav__item:visited {
  color: #007bff;
}

.nav__item:focus,
.nav__item:hover {
  color: darkblue;
  border-color: rgb(222, 226, 230);
}

.nav__version {
  padding-left: 10px;
  padding-right: 10px;
}

.nav__usergroup {
  display: flex;
  margin-left: 10px;
}

.nav__usergroupselect {
  flex-grow: 1;
}

.nav__approvalinfo {
  display: flex;
  align-items: center;
  margin-left: 10px;
}

.nav__approvaltext {
  user-select: none;
  text-decoration: none;
}

.nav__approvalcount {
  background-color: green;
  font-weight: 500;
  padding: 5px;
  border-radius: 7px;
  margin-left: 5px;
  user-select: none;
}

.nav__approvalcount_warning {
  background-color: yellow;
}

.nav__approvalcount_alarm {
  background-color: red;
}

.nav__cartinfo {
  display: flex;
  align-items: center;
  margin-left: 50px;
}

.nav__cartimg {
  cursor: pointer;
  min-width: 36px;
  min-height: 36px;
  background: url("../../../public/shopping-cart.png") top left/36px 36px no-repeat;
}

.nav__cartcount {
  user-select: none;
  margin-left: 10px;
  font-weight: bold;
  transition: transform 1s;
}

.nav__cartcount_decreased {
  color: red;
  transform: scale(1.5);
}

.nav__cartcount_increased {
  color: green;
  transform: scale(1.5);
}

.nav__cleancartimg {
  cursor: pointer;
  margin-left: 10px;
  min-width: 30px;
  min-height: 30px;
  background: url("../../../public/clean.png") top left/30px 30px no-repeat;
  transition: transform 0.3s;
}

.nav__cleancartimg:hover {
  transform: rotate(30deg);
}

.nav__behalfuserimg {
  cursor: pointer;
  margin-left: 10px;
  min-width: 30px;
  min-height: 30px;
  background: url("../../../public/log-out.png") top left/30px 30px no-repeat;
  transition: transform 0.3s;
  margin-right: 10px;
}
</style>