<template>
  <div>
    <div class="page" v-if="folder_name">
      <heading-component
        class="page__heading"
        :text="`${folder_name} ${
          validation_progress < 100
            ? '( ' + validation_progress.toString() + '%)'
            : ''
        }`"
        size="middle"
      />
      <div class="page__container">
        <div v-if="is_list_container_required" class="page__listcontainer">
          <document-page-download-progress-row
            class="page__downloadprogress"
            :files="downloading_files"
          />

          <div class="page__buttonblock">
            <document-page-buttons-row
              class="page__primarybuttonblock"
              :is_edit_mode="is_edit_mode"
              :is_files_shown="is_files_shown"
              :is_folder_saved="is_folder_saved"
              :can_add_doc="can_add_doc"
              :can_change_doc="can_change_doc"
              :can_view_file="can_view_file"
              :can_add_file="can_add_file"
              :can_delete_file="can_delete_file"
              :can_delete_doc="can_delete_doc"
              :is_tuning_mode="is_tuning_mode"
              :is_group_by_mode="is_group_by_mode"
              :group_by_settings="group_by_settings"
              @reset:search="is_search_clean_required = true"
              @toggle:shownfiles="is_files_shown = !is_files_shown"
              @toggle:editmode="
                is_edit_mode = !is_edit_mode;
                is_tuning_mode = is_edit_mode === true ? false : is_tuning_mode;
              "
              @toggle:tuningmode="is_tuning_mode = !is_tuning_mode"
              @toggle:groupbymode="is_group_by_mode = !is_group_by_mode"
              @save:docs="handle_save_documents()"
              @flush:filecontainer="handle_flush_file_container()"
              @new:doc="add_empty_document()"
              @allocate:files="handle_allocate_files()"
              @reset:tuning="handle_delete_user_search_schema()"
              @delete:docs="mark_selected_documents_as_deleted()"
              @duplicate:docs="duplicate_selected_documents()"
              @download:excel="handle_download_excel_file()"
              @download:json="handle_download_json_file()"
              @download:files_archive="handle_download_files_in_archive()"
              @to:cart="handle_add_to_cart()"
            />
            <div v-if="!is_edit_mode" class="page__additionalbuttonblock">
              <text-button
                v-for="(button, index) in action_buttons"
                :key="index"
                class="page__actionbutton"
                :title="button.caption"
                @click="handle_push_action_button(button.handler)"
              />
            </div>
          </div>

          <document-page-search-row
            class="page__searchrow"
            :search_schema="search_schema"
            :is_disabled="!is_folder_saved"
            :is_clean_required="is_search_clean_required"
            :is_tuning_mode="is_tuning_mode"
            :is_selection_required="true"
            :is_all_selected="is_all_selected"
            :required_space_for_link="true"
            @search:docs="
              get_documents_action({ search_by_user: true, query: $event })
            "
            @clean:query="is_search_clean_required = false"
            @change:size="
              handle_tuning_change_field('size', $event.name, $event.value)
            "
            @change:grow="
              handle_tuning_change_field('grow', $event.name, $event.value)
            "
            @delete:field="handle_tuning_delete_field($event)"
            @selectall:docs="handle_select_all_documents()"
          />
          <document-page-sort-row
            class="page__sortrow"
            :search_schema="search_schema"
            :sort_asc="sort_asc"
            :sort_by="sort_by"
            @toggle:direction="sort_asc = !sort_asc"
            @sort:by="[sort_by, sort_rule] = $event"
          />
          <document-pagination-row
            class="page__paginationrow"
            v-if="search_results.length > 0"
            :total_count="Math.max(search_count, search_results.length)"
            :count_per_page="pagination_count_per_page"
            :current_page="pagination_current_page"
            :variants="pagination_variants"
            :is_intermediate_part_shown="is_intermediate_pages_shown"
            @set:page_range="handle_set_page_range($event.min, $event.max)"
            @select:page="pagination_current_page = $event"
            @set:count_per_page="pagination_count_per_page = $event"
            @show:pages="is_intermediate_pages_shown = true"
          />
          <div class="page__statusrow">
            <form-text-field :value="search_status" />
          </div>
          <div class="page__resultblock">
            <div
              class="page__documentblock"
              v-for="doc in sorted_results"
              :key="doc.id"
            >
              <document-row
                class="page__documentrow"
                :doc="doc"
                :search_schema="search_schema"
                :is_selected="doc.id in selected_document_ids"
                :is_edit_mode="is_edit_mode"
                :is_files_shown="is_files_shown"
                :is_dragging="is_uploaded_file_dragging"
                :has_link="true"
                :link_route_obj="generate_doc_link(doc.id)"
                @recover:doc="mark_document_as_not_deleted(doc.id)"
                @update:doc="
                  handle_update_doc_attribute(
                    $event.id,
                    $event.name,
                    $event.value
                  )
                "
                @delete:file="detach_file_from_document(doc, $event)"
                @attach:file="attach_file_to_document(doc, $event)"
                @download:file="handle_download_file(...$event)"
                @copy_link_to:file="
                  handle_copy_link_to_file_into_clipboard($event)
                "
                @select:doc="handle_select_document(doc.id)"
              />
            </div>
          </div>
        </div>

        <div
          class="page__servicecontainer"
          :class="{ page__servicecontainer_alone: !is_list_container_required }"
          v-if="is_edit_mode && (can_add_doc || can_add_file)"
        >
          <json-file-drop-zone
            class="page__docdropzone"
            v-if="can_add_doc && is_doc_container_required"
            :class="{ page__docdropzone_dragging: is_drag_above_doc_zone }"
            @update:dragging="is_drag_above_doc_zone = $event"
            @update:json="handle_new_docs_drop($event)"
            :default="[]"
            :parsers="doc_container_parsers"
          >
            <span v-if="!is_drag_above_doc_zone" class="page__dropzonetitle"
              >&gt;  (json) &lt;</span
            >
          </json-file-drop-zone>
          <file-drop-zone
            class="page__filedropzone"
            v-if="can_add_file"
            :class="{ page__filedropzone_dragging: is_drag_above_file_zone }"
            @add:file="handle_new_file_drop($event)"
            @update:dragging="is_drag_above_file_zone = $event"
          >
            <span v-if="!is_drag_above_file_zone" class="page__dropzonetitle">
              &lt; {{ max_file_size }} </span
            >
          </file-drop-zone>
          <div class="page__filelistlabel">
               ,   'C '.
          </div>
          <file-list
            class="page__filelist"
            v-if="can_add_file"
            :files="spare_files"
            :can_be_dragged="is_edit_mode"
            :can_be_deleted="true"
            @dragstart:file="is_uploaded_file_dragging = true"
            @dragend:file="is_uploaded_file_dragging = false"
            @download:file="handle_download_file(...$event)"
            @delete:file="handle_delete_file($event)"
            @copy_link_to:file="handle_copy_link_to_file_into_clipboard($event)"
          />
        </div>
      </div>
    </div>
    <div class="page" v-else>
      <heading-component
        class="page__heading"
        text="..  .."
        size="small"
      />
    </div>
  </div>
</template>

<script>
import { v4 as uuidv4 } from "uuid";
import { onMounted, ref, computed, watch, onBeforeUnmount } from "vue";
import { useRoute, useRouter } from "vue-router";
import { VueUnitOfWork } from "../logic/service_layer/uow";
import { MessageBus } from "../logic/service_layer/message_bus";
import {
  useBeforeEnterPage,
  useGetFolders,
  useGetUserSearchSchema,
  useGetSpareFiles,
  useValidateIntOr404,
  useGetUserGroups,
} from "../logic/service_layer/use_modules";
import HeadingComponent from "../components/HeadingComponent.vue";
import {
  GetManyDocuments,
  SetDocumentAttribute,
  SaveDocumentsInFolder,
  MarkDocumentAsDeleted,
  MarkDocumentAsNotDeleted,
  AttachFileToDocument,
  DetachFileFromDocument,
  UploadFileToFolder,
  DownloadFileFromFolder,
  DeleteFileFromFolder,
  FlushFileContainer,
  AddDocument,
  AllocateFilesToDocumentsUsingPattern,
  SetUserSearchSchema,
  DeleteUserSearchSchema,
  UpdateUploadProgress,
  DownloadExcelFile,
  DownloadJsonFile,
  SetCurrentUserGroup,
  ValidateDocument,
  AddManyDocumentsToCart,
  DownloadManyDocumentsFilesAsArchive,
  PerformActionOnDocuments,
} from "../logic/domain/command";
import {
  UploadFileTooBigError,
  UploadEmptyFileError,
  FileLinkCopied,
} from "../logic/domain/event";
import DocumentRow from "../components/document/DocumentRow.vue";
import DocumentPageButtonsRow from "../components/document/DocumentPageButtonsRow.vue";
import DocumentPageSearchRow from "../components/document/DocumentPageSearchRow.vue";
import DocumentPageSortRow from "../components/document/DocumentPageSortRow.vue";
import FileList from "../components/file/FileList.vue";
import JsonFileDropZone from "../components/file/JsonFileDropZone.vue";
import FileDropZone from "../components/file/FileDropZone.vue";
import DocumentPageDownloadProgressRow from "../components/document/DocumentPageDownloadProgressRow.vue";
import DocumentPaginationRow from "../components/document/DocumentPaginationRow.vue";
import FormTextField from "../components/fields/FormTextField.vue";
import TextButton from "../components/buttons/TextButton.vue";

export default {
  components: {
    HeadingComponent,
    DocumentRow,
    DocumentPageButtonsRow,
    DocumentPageSearchRow,
    DocumentPageSortRow,
    FileList,
    JsonFileDropZone,
    FileDropZone,
    DocumentPageDownloadProgressRow,
    DocumentPaginationRow,
    FormTextField,
    TextButton,
  },
  name: "SearchDocumentPage",
  setup() {
    const route = useRoute();
    const router = useRouter();
    const uow = new VueUnitOfWork();

    // USER
    const user = uow.user_repository.get_current();

    // USERGROUP
    const usergroup_id = useValidateIntOr404(route.params.usergroup_id);

    // DOCUMENT
    const generate_doc_link = (doc_id) => {
      return {
        name: "DocumentPage",
        params: {
          folder_id: folder_id,
          usergroup_id: usergroup_id,
          document_id: doc_id,
        },
      };
    };

    // FOLDER
    const folder_id = useValidateIntOr404(route.params.folder_id);
    const folder = uow.folder_repository.get(folder_id); // Ref

    const folder_name = computed(() =>
      folder.value === null ? "" : folder.value.name
    );
    const search_schema = computed(() => {
      if (folder.value === null) return [];
      return is_edit_mode.value === true
        ? folder.value.search_schema
        : folder.value.user_search_schema;
    });

    const is_empty_request_allowed = computed(() => {
      return folder.value === null
        ? false
        : folder.value.is_empty_search_allowed;
    });

    const is_list_container_required = computed(() => {
      return folder.value === null
        ? true
        : folder.value.is_list_container_required;
    });

    const is_doc_container_required = computed(() => {
      return folder.value === null
        ? true
        : folder.value.is_doc_container_required;
    });

    const folder_default_mode = computed(() => {
      return folder.value === null ? "view" : folder.value.default_mode;
    });

    const doc_container_parsers = computed(() => {
      return folder.value === null ? [] : folder.value.doc_container_parsers;
    });

    // PERMISSIONS
    const can_view_doc = uow.permission_repository.has_for_folder(
      folder_id,
      "can_view_document"
    );
    const can_add_doc = uow.permission_repository.has_for_folder(
      folder_id,
      "can_add_document"
    );
    const can_change_doc = uow.permission_repository.has_for_folder(
      folder_id,
      "can_change_document"
    );
    const can_delete_doc = uow.permission_repository.has_for_folder(
      folder_id,
      "can_delete_document"
    );
    const can_view_file = uow.permission_repository.has_for_folder(
      folder_id,
      "can_view_file"
    );
    const can_add_file = uow.permission_repository.has_for_folder(
      folder_id,
      "can_add_file"
    );
    const can_delete_file = uow.permission_repository.has_for_folder(
      folder_id,
      "can_delete_file"
    );

    // QUERY
    const is_search_clean_required = ref(false);
    const search_query = ref({});

    // EDIT MODE
    const is_edit_mode = uow.folder_settings.get(folder_id, "is_edit_mode");
    if (is_edit_mode.value === null)
      is_edit_mode.value = folder_default_mode.value === "edit";

    const handle_update_doc_attribute = (doc_id, name, value) => {
      //     ,   ,    
      //   = '' -    ,   
      //     ,    

      const message = new SetDocumentAttribute(
        folder_id,
        usergroup_id,
        doc_id,
        name,
        value
      );
      MessageBus.handle(message, uow);
    };

    const is_folder_saved = computed(() => {
      let result = true;
      search_results.value.forEach((doc) => {
        if (doc.is_saved === false || doc.is_deleted === true) result = false;
      });
      return result;
    });

    const handle_save_documents = () => {
      MessageBus.handle(
        new SaveDocumentsInFolder(folder_id, usergroup_id),
        uow
      );
    };

    const mark_selected_documents_as_deleted = () => {
      selected_document_ids_flat_array.value.forEach((doc_id) =>
        MessageBus.handle(new MarkDocumentAsDeleted(folder_id, doc_id), uow)
      );
    };

    const mark_document_as_not_deleted = (doc_id) => {
      MessageBus.handle(new MarkDocumentAsNotDeleted(folder_id, doc_id), uow);
    };

    const handle_new_docs_drop = (obj_array) => {
      if (!Array.isArray(obj_array)) obj_array = [obj_array];

      obj_array.forEach((obj) => {
        MessageBus.handle(
          new AddDocument(
            folder_id, // folder_id
            usergroup_id, // usergroup_id
            uuidv4(), // document_id
            null, // union_id
            obj, // attributes
            [], // files
            true, // mark_as_modified
            true, // is_created_first_time
            false // is blocked
          ),
          uow
        );
      });
    };

    const add_empty_document = () => {
      let init_obj = {};

      MessageBus.handle(
        new AddDocument(
          folder_id, // folder_id
          usergroup_id, // usergroup_id
          uuidv4(), // document_id
          null, // union_id
          init_obj, // attributes
          [], // files
          true, // mark_as_modified
          true, // is_created_first_time
          false // is blocked
        ),
        uow
      );
    };

    const duplicate_selected_documents = () =>
      selected_document_ids_flat_array.value.forEach((doc_id) => {
        const init_doc = uow.doc_repository.get(folder_id, doc_id); // Ref
        MessageBus.handle(
          new AddDocument(
            folder_id, // folder_id
            usergroup_id, // usergroup_id
            uuidv4(), // document_id
            null, // union_id
            init_doc.value.raw_attributes, // attributes
            [], // files
            true, // mark_as_modified
            true, // is_created_first_time
            false // is blocked
          ),
          uow
        );
      });

    // FILES
    const is_files_shown = uow.folder_settings.get(folder_id, "is_files_shown");
    if (is_files_shown.value === null) is_files_shown.value = false;

    const spare_files = computed(() =>
      uow.file_in_folder_repository.values(folder_id).map((obj) => obj.value)
    );
    const downloading_files = computed(() =>
      uow.download_progress.values().map((obj) => obj.value)
    );

    const detach_file_from_document = (doc, file_id) => {
      if (!doc.is_blocked) {
        MessageBus.handle(
          new DetachFileFromDocument(folder_id, usergroup_id, doc.id, file_id),
          uow
        );
      }
    };

    const attach_file_to_document = (doc, file_id) => {
      if (!doc.is_blocked) {
        MessageBus.handle(
          new AttachFileToDocument(folder_id, usergroup_id, doc.id, file_id),
          uow
        );
      }
    };

    const max_file_size = computed(() =>
      folder.value === null ? 0 : folder.value.max_file_size
    );

    const handle_new_file_drop = async (file) => {
      let message = null;
      if (file.size === 0) {
        message = new UploadEmptyFileError(file.name);
      } else if (file.size > max_file_size.value * 1024 * 1024) {
        message = new UploadFileTooBigError(file.name);
      } else {
        message = new UploadFileToFolder(
          folder_id,
          usergroup_id,
          uuidv4(),
          file
        );
      }
      await MessageBus.handle(message, uow);
    };

    const handle_download_file = async (file, inline) => {
      const message = new DownloadFileFromFolder(
        folder_id,
        usergroup_id,
        file.id,
        file.name,
        file.size,
        inline
      );
      await MessageBus.handle(message, uow);
    };

    const handle_flush_file_container = () => {
      MessageBus.handle(new FlushFileContainer(folder_id, usergroup_id), uow);
    };

    const handle_delete_file = async (file_id) => {
      await MessageBus.handle(
        new DeleteFileFromFolder(folder_id, usergroup_id, file_id),
        uow
      );
    };

    const handle_allocate_files = () => {
      MessageBus.handle(
        new AllocateFilesToDocumentsUsingPattern(folder_id, usergroup_id),
        uow
      );
    };

    const handle_copy_link_to_file_into_clipboard = (file) => {
      const props = router.resolve({
        name: "DownloadFileFromFolderPage",
        params: {
          folder_id: folder_id,
          usergroup_id: usergroup_id,
          file_id: file.id,
        },
      });

      navigator.clipboard
        .writeText(window.location.origin + props.href)
        .then(() => MessageBus.handle(new FileLinkCopied(), uow));
    };

    const handle_download_files_in_archive = async () => {
      await MessageBus.handle(
        new DownloadManyDocumentsFilesAsArchive(
          folder_id,
          usergroup_id,
          selected_document_ids_flat_array.value,
          //        
          selected_documents.value
            .map((doc) =>
              doc.files.map((x) => x.size).reduce((a, b) => a + b, 0)
            )
            .reduce((a, b) => a + b, 0)
        ),
        uow
      );
    };

    // DRAGGING
    const is_uploaded_file_dragging = ref(false);
    const is_drag_above_file_zone = ref(false);
    const is_drag_above_doc_zone = ref(false);

    // SEARCH
    const is_empty_request = ref(false);
    uow.doc_repository.set_count(0);
    const search_status = computed(() => {
      if (is_searching.value === true) return "...";
      if (
        is_empty_request.value === true &&
        is_empty_request_allowed.value === false
      )
        return "     .  -...";

      return (
        "   ENTER   .  " +
        search_count.value.toString() +
        ". (" +
        search_time.value.toString() +
        " .)  " +
        (pagination_min_doc_index.value + 1).toString() +
        "-" +
        (pagination_max_doc_index.value + 1).toString() +
        ".  " +
        selected_document_count.value.toString() +
        "."
      );
    });

    const search_results = computed(() =>
      uow.doc_repository.values(folder_id).map((obj) => obj.value)
    );
    const search_count = uow.doc_repository.get_count(); // Ref
    const is_searching = ref(false);
    const search_time = ref(0);
    const is_first_search_completed = ref(false);

    const get_documents_action = async ({
      search_by_user = false,
      query = search_query.value,
      without_limits = false,
    }) => {
      //        
      if (!(search_by_user || is_first_search_completed.value)) return;

      //   ,   
      if (query !== search_query.value) reset_pagination();

      search_query.value = query;

      is_empty_request.value = false;

      //   
      if (Object.values(query).filter((e) => e !== "").length === 0) {
        is_empty_request.value = true;

        //    
        if (is_empty_request_allowed.value === false) return;
      }

      if (is_searching.value === true) return;
      const t0 = performance.now();

      is_searching.value = true;

      const limit =
        without_limits || is_group_by_mode.value
          ? null
          : pagination_count_per_page.value;

      const offset =
        without_limits || is_group_by_mode.value
          ? null
          : pagination_count_per_page.value *
            (pagination_current_page.value - 1);

      const message = new GetManyDocuments(
        folder_id,
        usergroup_id,
        query,
        offset,
        limit,
        sort_by.value,
        sort_asc.value,
        sort_rule.value
      );

      await MessageBus.handle(message, uow);

      const t1 = performance.now();
      search_time.value = Math.round(t1 - t0) / 1000;
      is_searching.value = false;
      is_first_search_completed.value = true;
    };

    // PAGINATION
    const default_count_per_page = 20;
    const default_current_page = 1;
    const pagination_variants = ref([20, 50, 100]);

    const pagination_count_per_page = ref(0);
    const pagination_current_page = ref(0);
    const pagination_min_doc_index = ref(0);
    const pagination_max_doc_index = ref(0);
    const is_intermediate_pages_shown = ref(false);

    const reset_pagination = () => {
      pagination_count_per_page.value = default_count_per_page;
      pagination_current_page.value = default_current_page;
      pagination_min_doc_index.value =
        (default_current_page - 1) * default_count_per_page;
      pagination_max_doc_index.value =
        default_current_page * default_count_per_page;
    };

    reset_pagination();

    const handle_set_page_range = async (min, max) => {
      pagination_min_doc_index.value = min;
      pagination_max_doc_index.value = max;
    };

    watch(
      pagination_count_per_page,
      async () => await get_documents_action({})
    );
    watch(pagination_current_page, async () => await get_documents_action({}));

    // SORTING
    //     
    const default_sort_attribute = computed(() => {
      return folder.value === null ? null : folder.value.default_sort_attribute;
    });

    const default_sort_rule = computed(() => {
      if (default_sort_attribute.value === null) return "DEFAULT";
      //    search_schema
      //     name = default_sort_attribute
      const block = search_schema.value.find(
        (x) => x["name"] === default_sort_attribute.value
      );
      if (block) return block["sort_rule"];
      return "DEFAULT";
    });

    const default_sort_order = computed(() => {
      return folder.value === null ? null : folder.value.default_sort_order;
    });

    const sort_by = ref(default_sort_attribute.value);
    const sort_rule = ref(default_sort_rule.value);
    const sort_asc = ref(default_sort_order.value);

    watch(sort_by, async () => await get_documents_action({}));
    watch(sort_asc, async () => await get_documents_action({}));
    watch(sort_rule, async () => await get_documents_action({}));

    const sorted_results = computed(() => grouped_results.value || []);

    // GROUPS
    const aggregate_functions = {
      MAX: (a, b) => (Number.parseInt(a) > Number.parseInt(b) ? true : false),
    };

    const is_group_by_mode = ref(false);

    watch(is_group_by_mode, async (newValue) => {
      if (newValue) await get_documents_action({});
    });

    const group_by_settings = computed(() =>
      folder.value ? folder.value.group_by : null
    );

    const grouped_results = computed(() => {
      if (
        group_by_settings.value === null ||
        !is_group_by_mode.value ||
        search_results.value === null
      )
        return search_results.value;

      //    
      const order_by = group_by_settings.value.order_by;
      const aggregate_function =
        aggregate_functions[group_by_settings.value.aggregate_function];

      //          
      const make_key_from_fields = (doc) => {
        let key = "";
        group_by_settings.value.fields.forEach((field) => {
          let field_value = doc.get_raw_attribute(field);
          if (!field_value) field_value = "";
          key = key + field + "|" + field_value + "|";
        });
        return key;
      };

      //     
      let aggregate_value = {};
      search_results.value.forEach((e) => {
        const key = make_key_from_fields(e);

        if (Object.prototype.hasOwnProperty.call(aggregate_value, key)) {
          const a = aggregate_value[key];
          const b = e.get_raw_attribute(order_by);
          aggregate_value[key] = aggregate_function(a, b) ? a : b;
        } else {
          aggregate_value[key] = e.get_raw_attribute(order_by);
        }
      });

      //      ,     
      return search_results.value.filter(
        (e) =>
          e.get_raw_attribute(order_by) ===
          aggregate_value[make_key_from_fields(e)]
      );
    });

    // TUNING
    const is_tuning_mode = ref(false);

    const handle_tuning_change_field = (property, field, value) => {
      let schema = search_schema.value;
      const idx = schema.findIndex((e) => e.name === field);
      schema[idx][property] = value;
      uow.folder_repository.set_user_search_schema(folder_id, schema);
    };

    const handle_tuning_delete_field = (field) => {
      uow.folder_repository.set_user_search_schema(
        folder_id,
        search_schema.value.filter((e) => e.name !== field)
      );
    };

    const handle_save_user_search_schema = () => {
      MessageBus.handle(
        new SetUserSearchSchema(folder_id, usergroup_id, search_schema.value),
        uow
      );
    };

    const handle_delete_user_search_schema = () => {
      MessageBus.handle(
        new DeleteUserSearchSchema(folder_id, usergroup_id),
        uow
      );
    };

    // TIMERS
    const update_upload_progress = () => {
      MessageBus.handle(new UpdateUploadProgress(), uow);
    };

    uow.upload_progress_timer.set_callback(update_upload_progress);

    // EXCEL

    const handle_download_excel_file = async () => {
      //   
      const names = search_schema.value.map((e) => e.name);
      const descriptions = search_schema.value.map((e) => e.description);

      await get_documents_action({ without_limits: true });

      //    
      const rows = grouped_results.value.map((doc) => {
        return names.map((key) => doc.get_attribute(key));
      });

      //  XLSX    
      MessageBus.handle(
        new DownloadExcelFile(
          "doc_list.xlsx",
          [names, descriptions].concat(rows)
        ),
        uow
      );
    };

    // JSON

    const handle_download_json_file = async () => {
      await get_documents_action({ without_limits: true });

      //    
      const rows = grouped_results.value.map((doc) => {
        const doc_copy = structuredClone(doc.raw_attributes);
        delete doc_copy["_CREATED_AT_DATE"];
        delete doc_copy["_UPDATED_AT_DATE"];
        return doc_copy;
      });

      MessageBus.handle(new DownloadJsonFile("doc_list.json", rows), uow);
    };

    // SELECTION
    const selected_document_ids = ref({});
    const selected_document_ids_flat_array = computed(() =>
      Object.entries(selected_document_ids.value)
        .filter((x) => x[1] === true)
        .map((x) => x[0])
    );

    const selected_documents = computed(() =>
      selected_document_ids_flat_array.value.map(
        (document_id) => uow.doc_repository.get(folder_id, document_id).value
      )
    );

    const selected_document_count = computed(
      () =>
        Object.values(selected_document_ids.value).filter((x) => x === true)
          .length
    );

    const handle_select_document = (id) => {
      if (id in selected_document_ids.value)
        selected_document_ids.value[id] = !selected_document_ids.value[id];
      else selected_document_ids.value[id] = true;
    };

    const reset_selection = () => {
      selected_document_ids.value = {};
      is_all_selected.value = false;
    };

    const is_all_selected = ref(false);

    const handle_select_all_documents = () => {
      if (is_all_selected.value === true) {
        reset_selection();
        is_all_selected.value = false;
      } else {
        is_all_selected.value = true;
        sorted_results.value.forEach(
          (x) => (selected_document_ids.value[x.id] = true)
        );
      }
    };

    // CART
    const handle_add_to_cart = () => {
      MessageBus.handle(
        new AddManyDocumentsToCart(
          folder_id,
          usergroup_id,
          selected_document_ids_flat_array.value
        ),
        uow
      );
    };

    // VALIDATION
    const validation_progress = computed(() =>
      uow.doc_repository.validation_progress(folder_id)
    );

    const validate_documents = () => {
      sorted_results.value
        .filter((doc) => !doc.once_validated)
        .forEach((doc) =>
          MessageBus.handle(
            new ValidateDocument(folder_id, usergroup_id, doc.id),
            uow
          )
        );
    };

    // ACTION BUTTONS
    const action_buttons = computed(() => {
      return folder.value === null
        ? []
        : folder.value.search_action_buttons.filter((obj) =>
            is_action_button_shown(obj)
          );
    });

    const is_action_button_shown = (button_obj) =>
      button_obj.allowed_usernames
        ? button_obj.allowed_usernames.includes(user.value.username)
        : true;

    const handle_push_action_button = async (handler) => {
      await MessageBus.handle(
        new PerformActionOnDocuments(
          handler,
          user.value.username,
          folder_id,
          usergroup_id,
          selected_document_ids_flat_array.value
        ),
        uow
      );
    };

    // WATCH

    //      ,
    //      
    watch(
      () => sorted_results.value,
      () => {
        reset_selection();
        if (is_edit_mode.value === true) validate_documents();
      }
    );

    //    ,  
    watch(
      folder_default_mode,
      (newValue) => (is_edit_mode.value = newValue === "edit")
    );

    watch(is_edit_mode, (newValue) => {
      //       
      folder.value.is_necessary_validate = newValue;

      //     
      if (newValue === true) {
        //     
        validate_documents();
        //   
        is_group_by_mode.value = false;
      }
    });

    //     
    // ,       
    watch(default_sort_attribute, (newValue) => {
      if (newValue) {
        sort_by.value = newValue;
      }
    });

    watch(default_sort_rule, (newValue) => {
      if (newValue) {
        sort_rule.value = newValue;
      }
    });

    watch(default_sort_order, (newValue) => {
      if (newValue !== null && newValue !== undefined)
        sort_asc.value = newValue;
    });

    //     ,
    watch(group_by_settings, (newValue) => {
      if (newValue) is_group_by_mode.value = newValue.is_active === true;
    });

    watch(is_tuning_mode, (newValue) => {
      if (newValue === false) handle_save_user_search_schema();
    });

    onMounted(async () => {
      await useBeforeEnterPage(
        uow,
        ["can_get_folders", "can_view_document"],
        folder_id,
        usergroup_id
      ); //   a
      await Promise.all([useGetFolders(uow), useGetUserGroups(uow)]);

      //   
      const usergroup = uow.usergroup_repository.get(usergroup_id);
      MessageBus.handle(new SetCurrentUserGroup(usergroup.value), uow);

      //      
      await Promise.all([
        useGetUserSearchSchema(uow, folder_id, usergroup_id),
        useGetSpareFiles(uow, folder_id, usergroup_id),
      ]);
    });

    onBeforeUnmount(async () => uow.upload_progress_timer.stop());

    return {
      router,

      // USER
      user,

      // PERMISSIONS
      can_view_doc,
      can_add_doc,
      can_change_doc,
      can_delete_doc,
      can_view_file,
      can_add_file,
      can_delete_file,

      // QUERY
      is_search_clean_required,

      // USERGROUP
      usergroup_id,

      // FOLDER
      folder_id,
      folder_name,
      search_schema,
      is_list_container_required,
      is_doc_container_required,
      doc_container_parsers,

      // DOCUMENT
      generate_doc_link,

      // EDIT
      is_edit_mode,
      is_folder_saved,
      handle_update_doc_attribute,
      handle_save_documents,
      handle_new_docs_drop,
      mark_document_as_not_deleted,
      mark_selected_documents_as_deleted,
      add_empty_document,
      duplicate_selected_documents,

      // FILES
      max_file_size,
      is_files_shown,
      spare_files,
      downloading_files,
      attach_file_to_document,
      detach_file_from_document,
      handle_new_file_drop,
      handle_download_file,
      handle_flush_file_container,
      handle_delete_file,
      handle_allocate_files,
      handle_copy_link_to_file_into_clipboard,
      handle_download_files_in_archive,

      // DRAGGING
      is_uploaded_file_dragging,
      is_drag_above_file_zone,
      is_drag_above_doc_zone,

      // SEARCH
      get_documents_action,
      is_searching,
      search_time,
      search_status,
      is_empty_request_allowed,
      search_count,

      // SORTING
      sort_by,
      sort_asc,
      sort_rule,
      search_results,
      sorted_results,

      // GROUPING
      is_group_by_mode,
      group_by_settings,
      grouped_results,

      // PAGINATION
      handle_set_page_range,
      pagination_variants,
      pagination_count_per_page,
      pagination_current_page,
      pagination_min_doc_index,
      pagination_max_doc_index,
      is_intermediate_pages_shown,

      // TUNING
      is_tuning_mode,
      handle_tuning_change_field,
      handle_save_user_search_schema,
      handle_tuning_delete_field,
      handle_delete_user_search_schema,

      // EXCEL
      handle_download_excel_file,

      // JSON
      handle_download_json_file,

      // SELECTION
      selected_document_ids,
      selected_documents,
      selected_document_count,
      handle_select_document,
      is_all_selected,
      handle_select_all_documents,

      // CART
      handle_add_to_cart,

      // VALIDATION
      validation_progress,
      validate_documents,

      // ACTION BUTTONS
      action_buttons,
      handle_push_action_button,
    };
  },
};
</script>
<style scoped>
.page {
  display: flex;
  flex-direction: column;
  padding-left: 60px;
  padding-right: 60px;
}

.page__heading {
  padding-top: 60px;
  padding-bottom: 15px;
}

.page__container {
  display: flex;
}

.page__listcontainer {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  padding: 3px;
}

.page__servicecontainer {
  display: flex;
  flex-direction: column;
  min-width: 300px;
  max-width: 300px;
  padding: 3px;
}

.page__servicecontainer_alone {
  min-width: 500px;
  max-width: 500px;
}

.page__documentblock {
  cursor: pointer;
}

.page__documentblock:nth-child(even) {
  background-color: rgb(215, 242, 255);
}

.page__filedropzone,
.page__docdropzone {
  height: 80px;
  border: 2px dashed grey;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px;
}

.page__filedropzone_dragging,
.page__docdropzone_dragging {
  background: repeating-linear-gradient(
    45deg,
    #606dbc,
    #606dbc 10px,
    #465298 10px,
    #465298 20px
  );
}

.page__dropzonetitle {
  font-size: 24px;
}

.page__downloadprogress {
  min-height: 20px;
}

.page__buttonblock {
  display: flex;
  margin-bottom: 10px;
}

.page__primarybuttonblock,
.page__additionalbuttonblock {
  display: flex;
}


.page__statusrow {
  display: flex;
  justify-content: center;
}

.page__sortrow {
  margin-left: 60px;
}

.page__filelistlabel {
  margin: 10px;
}
</style>
