import { VueBoxRepository } from "./box_repository";

const VariantMixin = {
  get_variants() {
    return this._get("__VARIANTS__");
  },

  set_variants(value) {
    this._set("__VARIANTS__", value);
  },
};

class VueRolePermissionsRepository extends VueBoxRepository {}
Object.assign(VueRolePermissionsRepository.prototype, VariantMixin);

export { VueRolePermissionsRepository };
