import { Single as UserSingle } from "./user";

const Single = {
  type: "object",
  properties: {
    id: {
      type: "integer",
    },
    name: {
      type: "string",
    },
    slug: {
      type: "string",
    },
    is_private: {
      type: "boolean",
    },
    users: {
      type: "array",
      items: UserSingle,
    },
  },
  required: ["id", "name", "slug", "is_private", "users"],
  additionalProperties: false,
};

const List = {
  type: "array",
  items: Single,
};

export { Single, List };