import {
  NegativeResponse,
  PushDocumentButtonRequest,
  PushDocumentButtonResponse,
  DownloadTransmittalCoverSheetRequest,
  DownloadTransmittalCoverSheetResponse,
} from "../../adapters/api";

import { ApiError, DocumentButtonPushed } from "../../domain/event";

import { js_download_file } from "../utils/file_download";

class WrongResponseError extends Error {}

const push_document_button = async (event, uow) => {
  const request = new PushDocumentButtonRequest({
    folder_id: event.folder_id,
    usergroup_id: event.usergroup_id,
    document_id: event.document_id,
    action: event.action,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof PushDocumentButtonResponse) {
    uow.push_message(new DocumentButtonPushed());
    return;
  }

  throw new WrongResponseError();
};

const handler_1882_dlenc_transmittal = async (
  uow,
  username,
  folder_id,
  usergroup_id,
  document_ids
) => {

  const request = new DownloadTransmittalCoverSheetRequest({
    folder_id: folder_id,
    usergroup_id: usergroup_id,
    document_ids: document_ids,
    template: "1882_dlenc",
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof DownloadTransmittalCoverSheetResponse) {
    js_download_file({
      data: response.data.file,
      filename: response.data.name,
    });
    return;
  }

  throw new WrongResponseError();
};

const perform_action_on_documents = async (event, uow) => {
  const handlers = {
    "1882_dlenc_transmittal": handler_1882_dlenc_transmittal,
  };

  const handler = handlers[event.handler];

  await handler(
    uow,
    event.username,
    event.folder_id,
    event.usergroup_id,
    event.document_ids
  );
};

export { push_document_button, perform_action_on_documents };
