import {
  NegativeResponse,
  GetFlowsRequest,
  GetFlowsResponse,
  AddFlowRequest,
  AddFlowResponse,
  UpdateFlowRequest,
  UpdateFlowResponse,
  DeleteFlowRequest,
  DeleteFlowResponse,
  GetFlowItemRequest,
  GetFlowItemResponse,
  GetManyFlowItemsRequest,
  GetManyFlowItemsResponse,
  GetFlowItemStepsRequest,
  GetFlowItemStepsResponse,
  RestartFlowItemRequest,
  RestartFlowItemResponse,
} from "../../adapters/api";

import {
  ApiError,
  AddFlowSuccess,
  UpdateFlowSuccess,
  DeleteFlowSuccess,
  RestartFlowItemSuccess,
} from "../../domain/event";

import { Flow, FlowItem, FlowItemStep } from "../../domain/model";

class WrongResponseError extends Error {}

const convert_flow_response_obj_to_model = (obj) =>
  new Flow(
    obj.id,
    obj.name,
    obj.user,
    obj.trigger,
    obj.is_multiple,
    obj.is_active,
    obj.message_if_fail,
    obj.process,
    new Date(obj.created_at),
    new Date(obj.updated_at)
  );

const get_flows = async (event, uow) => {
  const request = new GetFlowsRequest({ query: event.query });

  const response = await uow.api.call(request);
  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof GetFlowsResponse) {
    uow.flow_repository.reset_keeping_refs();
    response.data.forEach((obj) =>
      uow.flow_repository.set(obj.id, convert_flow_response_obj_to_model(obj))
    );
    return;
  }

  throw new WrongResponseError();
};

const add_flow = async (event, uow) => {
  const request = new AddFlowRequest({
    name: event.name,
    trigger: event.trigger,
    is_active: event.is_active,
    is_multiple: event.is_multiple,
    message_if_fail: event.message_if_fail,
    process: event.process,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof AddFlowResponse) {
    uow.flow_repository.set(
      response.data.id,
      convert_flow_response_obj_to_model(response.data)
    );
    uow.push_message(new AddFlowSuccess());
    return;
  }

  throw new WrongResponseError();
};

const update_flow = async (event, uow) => {
  const request = new UpdateFlowRequest({
    id: event.id,
    name: event.name,
    trigger: event.trigger,
    is_active: event.is_active,
    is_multiple: event.is_multiple,
    message_if_fail: event.message_if_fail,
    process: event.process,
  });

  const response = await uow.api.call(request);
  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof UpdateFlowResponse) {
    uow.flow_repository.set(
      response.data.id,
      convert_flow_response_obj_to_model(response.data)
    );
    uow.push_message(new UpdateFlowSuccess());
    return;
  }

  throw new WrongResponseError();
};

const delete_flow = async (event, uow) => {
  const request = new DeleteFlowRequest({
    id: event.id,
  });

  const response = await uow.api.call(request);
  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof DeleteFlowResponse) {
    uow.flow_repository.delete(event.id);
    uow.push_message(new DeleteFlowSuccess());
    return;
  }

  throw new WrongResponseError();
};

const convert_flow_item_response_obj_to_model = (obj) =>
  new FlowItem(
    obj.id,
    obj.flow.name,
    obj.flow.user,
    obj.flow.trigger,
    obj.success,
    obj.is_active,
    obj.message_if_fail,
    obj.input,
    obj.process,
    new Date(obj.started_at),
    obj.finished_at === null ? null : new Date(obj.finished_at)
  );

const get_many_flow_items = async (event, uow) => {
  const request = new GetManyFlowItemsRequest({ query: event.query });

  const response = await uow.api.call(request);
  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof GetManyFlowItemsResponse) {
    uow.flow_item_repository.reset_keeping_refs();
    response.data.forEach((obj) =>
      uow.flow_item_repository.set(
        obj.id,
        convert_flow_item_response_obj_to_model(obj)
      )
    );
    return;
  }

  throw new WrongResponseError();
};

const get_flow_item = async (event, uow) => {
  const request = new GetFlowItemRequest({ id: event.id });

  const response = await uow.api.call(request);
  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof GetFlowItemResponse) {
    uow.flow_item_repository.set_current(
      convert_flow_item_response_obj_to_model(response.data)
    );
    return;
  }

  throw new WrongResponseError();
};

const convert_flow_item_step_response_obj_to_model = (obj) =>
  new FlowItemStep(
    obj.id,
    obj.name,
    obj.description,
    obj.flowitem_id,
    obj.success,
    obj.result_key,
    obj.action,
    obj.input,
    obj.output,
    new Date(obj.started_at),
    obj.finished_at === null ? null : new Date(obj.finished_at)
  );

const get_flow_item_steps = async (event, uow) => {
  const request = new GetFlowItemStepsRequest({
    flowitem_id: event.flowitem_id,
  });

  const response = await uow.api.call(request);
  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof GetFlowItemStepsResponse) {
    uow.flow_item_step_repository.reset_keeping_refs();
    response.data.forEach((obj) => {
      uow.flow_item_step_repository.set(
        obj.id,
        convert_flow_item_step_response_obj_to_model(obj)
      );
    });
    return;
  }

  throw new WrongResponseError();
};

const restart_flow_item = async (event, uow) => {
  const request = new RestartFlowItemRequest({ id: event.id });

  const response = await uow.api.call(request);
  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof RestartFlowItemResponse) {
    uow.push_message(new RestartFlowItemSuccess());
    return;
  }

  throw new WrongResponseError();
};

export {
  get_flows,
  add_flow,
  update_flow,
  delete_flow,
  get_flow_item,
  restart_flow_item,
  get_many_flow_items,
  get_flow_item_steps,
};
