import { Single as FileSingle } from "./file";

const Single = {
  type: "object",
  properties: {
    id: {
      type: "string",
    },
    order: {
      type: "integer",
    },
    page_count: {
      type: "integer",
    },
    page_from: {
      type: "integer",
    },
    page_to: {
      type: "integer",
    },
    angle: {
      type: "integer",
    },
    file: FileSingle,
  },
  required: [
    "id",
    "order",
    "page_count",
    "page_from",
    "page_to",
    "angle",
    "file",
  ],
  additionalProperties: false,
};

const List = {
  type: "array",
  items: Single,
};

export { Single, List };
