import { CheckMandatoryAttributeProcessor } from "./attribute_processors/check_mandatory_attribute_processor";
import { DatetimeToTimestampAttributeProcessor } from "./attribute_processors/datetime_to_timestamp_attribute_processor";
import { SetIfNoneAttributeProcessor } from "./attribute_processors/set_if_none_attribute_processor";

const SimpleParser = {
  type: "object",
  properties: {
    name: { enum: ["SIMPLE"] },
    parameters: {
      type: "object",
      properties: {
        subject: {
          type: "object",
          properties: {
            regex: { type: "array", items: { type: "string" } },
            flags: { type: "string" },
            row_count: { type: "integer", minimum: 1 },
          },
          required: ["regex"],
          additionalProperties: false,
        },
        body: {
          type: "object",
          properties: {
            regex: { type: "array", items: { type: "string" } },
            flags: { type: "string" },
            row_count: { type: "integer", minimum: 1 },
          },
          required: ["regex"],
          additionalProperties: false,
        },
        post_processors: {
          type: "array",
          items: {
            type: "object",
            properties: {
              attribute: { type: "string" },
              processor: {
                oneOf: [
                  DatetimeToTimestampAttributeProcessor,
                  SetIfNoneAttributeProcessor,
                  CheckMandatoryAttributeProcessor,
                ],
              },
            },
            required: ["attribute", "processor"],
            additionalProperties: false,
          },
        },
      },
      required: ["subject", "body"],
      additionalProperties: false,
    },
  },
  required: ["name", "parameters"],
  additionalProperties: false,
};

export { SimpleParser };
