<template>
  <div class="page">
    <heading-component
      class="page__heading"
      text=" "
      size="middle"
    />
    <div class="page__container">
      <div class="page__editblock">
        <base-form
          class="page__form"
          :attributes="current"
          :settings="form_pages"
          :is_edit_mode="true"
          :is_header_required="false"
          @update="handle_update_attribute($event.name, $event.value)"
        >
        </base-form>

        <div class="page__editrow">
          <btn-component
            class="page__button"
            @click="add_action()"
            caption=""
            type="primary"
            :disabled="!is_edit_block_valid"
            v-if="can_add && !current_id"
          />
          <btn-component
            class="page__button"
            @click="update_action()"
            caption=""
            type="success"
            :disabled="!is_edit_block_valid"
            v-if="can_change && current_id"
          />
          <btn-component
            class="page__button"
            @click="delete_action()"
            caption=""
            type="danger"
            v-if="can_delete && current_id"
          />
        </div>
      </div>
      <search-component
        class="page__searchblock"
        :schema="search_schema"
        :model_objects="model_objects"
        :convert_obj_to_item="convert_model_to_item"
        :current_id="current_id"
        sort_by="name"
        :search_time="search_time"
        :is_searching="is_searching"
        :should_emit_search_event_on_any_keypress="true"
        @choose="current_id = $event"
        @search="search_action($event)"
      />
    </div>
  </div>
</template>

<script>
import { computed, onMounted, ref } from "vue";
import {
  AddUserGroup,
  UpdateUserGroup,
  DeleteUserGroup,
} from "../../logic/domain/command";
import { MessageBus } from "../../logic/service_layer/message_bus";
import { VueUnitOfWork } from "../../logic/service_layer/uow";
import {
  convertObject,
  useBeforeEnterPage,
  filterArrayOfObjectsByQuery,
  useCurrentMixin,
  useGetUserGroups,
} from "../../logic/service_layer/use_modules";

import HeadingComponent from "../../components/HeadingComponent.vue";
import BtnComponent from "../../components/buttons/BtnComponent.vue";
import BaseForm from "../../components/forms/BaseForm.vue";
import SearchComponent from "../../components/SearchComponent.vue";

export default {
  components: {
    HeadingComponent,
    BtnComponent,
    BaseForm,
    SearchComponent,
  },
  name: "UserGroupSettingsPage",

  setup() {
    const uow = new VueUnitOfWork();

    const convert_model_to_item = (obj) => {
      return {
        id: obj.id,
        attributes: {
          id: obj.id.toString(),
          name: obj.name,
          slug: obj.slug,
          is_private: obj.is_private === true ? "Y" : "N",
        },
        actions: {},
      };
    };

    const query_for_internal_search = ref({});
    const model_objects = computed(() =>
      filterArrayOfObjectsByQuery(
        uow.usergroup_repository.values().map((obj) => obj.value),
        query_for_internal_search.value
      )
    );

    const can_add = uow.permission_repository.has("can_add_usergroup");
    const can_change = uow.permission_repository.has("can_change_usergroup");
    const can_delete = uow.permission_repository.has("can_delete_usergroup");

    // STATUS
    const search_time = ref(0);
    const is_searching = ref(false);

    const search_action = (query) => (query_for_internal_search.value = query);

    const search_schema = [
      {
        name: "id",
        size: "w50",
        fields: [
          {
            placeholder: "ID",
            rule: "exact",
          },
        ],
      },
      {
        name: "name",
        grow: true,
        size: "w300",
        fields: [
          {
            placeholder: "",
            rule: "icontains",
          },
        ],
      },
      {
        name: "slug",
        grow: true,
        size: "w300",
        fields: [
          {
            placeholder: " ",
            rule: "icontains",
          },
        ],
      },
      {
        name: "is_private",
        size: "w30",
        align: "center",
        fields: [
          {
            type: "bool",
            placeholder: "?",
            rule: "exact",
          },
        ],
      },
    ];

    // FORM
    const init_obj = { id: null, name: "", slug: "", is_private: true };

    const to_repository_convertion = {};
    const from_repository_convertion = {};

    const { current_id, current, handle_update_attribute } = useCurrentMixin(
      init_obj,
      uow.usergroup_repository,
      from_repository_convertion
    );

    const is_edit_block_valid = computed(() => current.value.name.length > 0);

    const add_action = async () => {
      const message = new AddUserGroup(
        ...["name", "slug", "is_private"].map(
          (k) => convertObject(current.value, to_repository_convertion)[k]
        )
      );
      await MessageBus.handle(message, uow);
    };

    const update_action = async () => {
      const message = new UpdateUserGroup(
        ...["id", "name", "slug", "is_private"].map(
          (k) => convertObject(current.value, to_repository_convertion)[k]
        )
      );
      await MessageBus.handle(message, uow);
    };

    const delete_action = async () => {
      const message = new DeleteUserGroup(current_id.value);
      await MessageBus.handle(message, uow);
      current_id.value = null;
    };

    const form_pages = computed(() => [
      {
        size: "w450",
        fields: [
          {
            type: "FormTextField",
            bind: "id",
          },
          {
            type: "FormTextField",
            parameters: { font_weight: "bold" },
            value: ":",
          },
          {
            type: "FormTextInput",
            bind: "name",
          },
          {
            type: "FormTextField",
            parameters: { font_weight: "bold" },
            value: "  (slug):",
          },
          {
            type: "FormTextInput",
            bind: "slug",
          },
          {
            type: "FormTextField",
            parameters: { font_weight: "bold" },
            value: "  ?",
          },
          {
            type: "FormBooleanInput",
            parameters: { size: 20 },
            bind: "is_private",
          },
        ],
      },
    ]);
    onMounted(async () => {
      //   a
      await useBeforeEnterPage(uow, [
        "can_view_usergroup",
        "can_view_settings",
      ]);
      await useGetUserGroups(uow);
    });

    return {
      // ACTION
      search_action,
      add_action,
      update_action,
      delete_action,

      // PERMISSION
      can_add,
      can_change,
      can_delete,

      // EDIT
      current_id,
      current,
      is_edit_block_valid,
      handle_update_attribute,
      form_pages,

      // SEARCH
      search_schema,
      model_objects,
      convert_model_to_item,
      search_time,
      is_searching,
    };
  },
};
</script>

<style scoped>
.page {
  display: flex;
  flex-direction: column;
  padding-left: 60px;
  padding-right: 60px;
}

.page__heading {
  padding-top: 60px;
  padding-bottom: 15px;
}
.page__container {
  display: flex;
}

.page__editblock {
  width: 600px;
  display: flex;
  flex-direction: column;
  padding-right: 15px;
}

.page__searchblock {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding-left: 15px;
  border-left: 1px solid black;
}

.page__button {
  margin: 5px;
}

.page__editrow {
  display: flex;
  width: 100%;
}
</style>
