<template>
  <div>
    <div class="page" v-if="folder_name">
      <div class="page__header">
        <span
          class="page__linkbutton"
          @click="handle_copy_link_to_document_into_clipboard"
          >&#128279;
        </span>
        <heading-component
          :text="usergroup_name + ' > ' + folder_name"
          size="middle"
        />
      </div>
      <div class="page__container" v-if="doc">
        <div class="page__doccontainer">
          <div class="page__docbuttonblock docbuttonblock">
            <toggle-text-button
              v-if="
                !doc.is_blocked &&
                doc.is_saved &&
                are_files_saved &&
                can_change_doc
              "
              class="docbuttonblock__button"
              activeTitle=" "
              passiveTitle=" "
              :isActive="is_edit_mode"
              @click="is_edit_mode = !is_edit_mode"
            />
            <div v-if="doc.is_blocked" class="docbuttonblock__lockicon" />
            <form-text-field
              v-if="doc.is_blocked"
              class="docbuttonblock__locktext"
              value=" "
            />
            <text-button
              class="docbuttonblock__button docbuttonblock__button_red"
              v-if="!doc.is_saved && can_change_doc"
              title=" "
              @click="handle_save_document()"
            />
            <text-button
              class="docbuttonblock__button"
              v-if="doc.files.length > 0"
              title="  "
              @click="handle_download_all_files()"
            />
            <div class="docbuttonblock__grow"></div>
            <div
              v-if="form_schema"
              class="docbuttonblock__linktoform"
              title=" "
              @click="is_form_opened = true"
            >
              &#9998;
            </div>
          </div>
          <document-page-download-progress-row
            class="page__filedownloadblock"
            :files="downloading_files"
          />
          <document-page-download-progress-row
            class="page__filedownloadblock"
            :files="uploading_files"
          />
          <div class="page__docattributeblock">
            <document-attribute-block
              :id="doc.id"
              :search_schema="search_schema"
              :attributes="
                is_edit_mode === true ? doc.raw_attributes : doc.attributes
              "
              :is_edit_mode="is_edit_mode"
              :is_highlighted="!doc.is_saved"
              :is_disabled="doc.is_blocked"
              @update:doc="
                handle_update_doc_attribute(
                  $event.id,
                  $event.name,
                  $event.value
                )
              "
            />
          </div>
          <file-drop-zone
            v-if="is_edit_mode && can_change_doc"
            class="page__docfiledropzone"
            :class="{
              page__docfiledropzone_dragging: is_drag_above_doc_file_zone,
            }"
            @add:file="handle_document_file_drop($event)"
            @update:dragging="is_drag_above_doc_file_zone = $event"
          >
            <span
              v-if="!is_drag_above_doc_file_zone"
              class="page__docfiledropzonetitle"
            >
              &lt; {{ doc_max_file_size }} </span
            >
          </file-drop-zone>
          <file-list
            v-if="can_view_file"
            class="page__docfileblock"
            :files="doc.files"
            :can_be_dragged="false"
            :can_be_deleted="is_edit_mode"
            :can_be_renamed="is_edit_mode"
            @download:file="handle_download_file(...$event)"
            @rename:file="handle_rename_file(...$event)"
            @rename:activated="handle_rename_file_activated($event)"
            @delete:file="detach_file_from_document(doc, $event)"
            @copy_link_to:file="
              handle_copy_link_to_document_file_into_clipboard($event)
            "
          />
          <div class="page__docerrorblock docerrorblock">
            <form-text-field
              class="container__header"
              value="   :"
            />
            <document-errors-row
              v-if="doc.errors.length > 0"
              class="docerrorblock__errors"
              :errors="doc.errors"
            />
            <form-text-field
              v-else
              class="docerrorblock__noerrors"
              value="OK"
            />
          </div>
          <div
            class="page__copycontainer copycontainer"
            v-if="copy_list_usergroups.length > 0"
          >
            <form-text-field
              class="container__header"
              value="  :"
            />
            <div class="copycontainer__row" v-if="!doc.is_blocked">
              <btn-component
                class="copycontainer__button"
                type="warning"
                :disabled="
                  get_seconds_before_button_copy_to_usergroup_activated() > 0
                "
                :caption="
                  get_text_of_action_button(
                    '  ',
                    COPY_TO_USERGROUP_ACTION
                  )
                "
                @click="handle_copy_list_change_usergroup()"
              />
              <form-select-input
                class="copycontainer__usergroupselect"
                :parameters="{
                  options: copy_list_usergroups,
                }"
                :value="copy_list_usergroup_id.toString()"
                @update="copy_list_usergroup_id = parseInt($event)"
              />
            </div>
            <div class="copycontainer__row" v-else>
              <form-text-field class="copycontainer__text" value="" />
            </div>
            <div class="copycontainer__row">
              <div class="copycontainer__links">
                <router-link
                  v-for="cloned_doc in doc.clones"
                  :key="cloned_doc.id"
                  class="copycontainer__cloneref"
                  :to="generate_doc_link(cloned_doc.id)"
                  >  
                </router-link>
              </div>
            </div>
          </div>

          <div
            class="page__actioncontainer actioncontainer"
            v-if="action_buttons.length > 0"
          >
            <btn-component
              class="actioncontainer__button"
              v-for="(button, index) in action_buttons"
              :key="index"
              :type="button.type || 'primary'"
              :disabled="
                is_action_button_disabled(button) ||
                get_seconds_before_button_activated(button.action) > 0
              "
              :caption="
                get_text_of_action_button(button.caption, button.action)
              "
              @click="handle_push_document_button(button.action)"
            />
          </div>
        </div>

        <div
          class="page__approvalcontainer"
          v-if="can_view_approval && !is_edit_mode"
        >
          <form-text-field class="container__header" value=":" />
          <div class="page__approvalorderblock approvalorderblock">
            <form-filtered-select-input
              v-if="can_add_approval || can_delegate_approval"
              class="approvalorderblock__input"
              :parameters="{
                placeholder: '...',
                size: 3,
                options: user_options,
              }"
              :value="approval_action_username"
              @update="approval_action_username = $event"
            />
            <btn-component
              v-if="can_add_approval"
              class="approvalorderblock__button"
              type="primary"
              caption=" "
              :disabled="is_approval_action_username_chosen === false"
              @click="handle_add_document_approval()"
            />
            <btn-component
              v-if="
                can_delegate_approval &&
                approvals_to_be_closed_ids &&
                approvals_to_be_closed_ids.length == 1 &&
                has_right_to_delegate_with_return
              "
              class="approvalorderblock__button"
              type="success"
              caption="  "
              :disabled="is_approval_action_username_chosen === false"
              title="       "
              @click="handle_delegate_document_approval(true)"
            />
            <btn-component
              v-if="
                can_delegate_approval &&
                approvals_to_be_closed_ids &&
                approvals_to_be_closed_ids.length == 1 &&
                has_right_to_delegate_without_return
              "
              class="approvalorderblock__button"
              type="warning"
              caption="  a"
              :disabled="is_approval_action_username_chosen === false"
              title="   ,   "
              @click="handle_delegate_document_approval(false)"
            />
          </div>
          <div
            class="page__approvalactionblock"
            v-if="can_change_approval && approvals_to_be_closed_ids !== null"
          >
            <div class="page__approvalbuttonblock approvalbuttonblock">
              <btn-component
                v-if="has_right_to_approve"
                class="approvalbuttonblock__button"
                type="success"
                caption=""
                @click="handle_approve_document()"
              />
              <btn-component
                v-if="has_right_to_reject"
                class="approvalbuttonblock__button"
                type="danger"
                caption=""
                @click="handle_reject_document()"
              />
            </div>
            <div class="page__approvaldropblock approvaldropblock">
              <form-text-field
                class="approvaldropblock__label"
                value="  ( ,  )     .      .        ,    ,  .     ."
                :parameters="{ align: 'justify' }"
              />
              <file-drop-zone
                class="approvaldropblock__filedropzone"
                :class="{
                  approvaldropblock__filedropzone_dragging:
                    is_drag_above_approval_file_zone,
                }"
                :filename_pattern="approval_filename_pattern"
                @add:file="handle_document_approval_file_drop($event)"
                @update:dragging="is_drag_above_approval_file_zone = $event"
                @wrong:filename="
                  handle_document_approval_filename_wrong($event)
                "
              >
                <div
                  v-show="!is_drag_above_approval_file_zone"
                  id="approvaldropblock__textzone"
                  class="approvaldropblock__dropzonetitle"
                  contenteditable="true"
                  @input="text_comments = $event.target.innerHTML"
                  @paste="handle_paste($event)"
                />
              </file-drop-zone>
            </div>
            <div class="page__approvalfileblock approvalfileblock">
              <file-block
                class="approvalfileblock__file"
                v-for="file in document_approval_files"
                :key="file.id"
                :file="file"
                :can_be_deleted="true"
                :can_be_dragged="false"
                @download:file="handle_download_file(file, $event)"
                @delete:file="handle_delete_document_approval_file(file.id)"
                @copy_link_to:file="
                  handle_copy_link_to_document_file_into_clipboard(file)
                "
              />
            </div>
          </div>
          <div
            v-if="
              approvals_after_last_update.length > 0 ||
              approvals_before_last_update.length > 0
            "
            class="page__approvalitemsblock"
          >
            <approval-row
              class="page__approvalitem"
              v-for="obj in approvals_after_last_update"
              :key="obj.id"
              :obj="obj"
              :can_be_deleted="
                can_delete_approval &&
                approval_ids_can_be_deleted.includes(obj.id)
              "
              @delete:approval="handle_delete_document_approval(obj.id)"
              @download:file="handle_download_file(...$event)"
              @delete:file="handle_delete_document_approval_file($event)"
              @copy_link_to:file="
                handle_copy_link_to_document_file_into_clipboard($event)
              "
            />
            <form-text-field
              class="page__approvallastupdatetext"
              :value="
                '   ' +
                convertDateToDateTimeString(doc.updated_at)
              "
            />
            <approval-row
              class="page__approvalitem"
              v-for="obj in approvals_before_last_update"
              :key="obj.id"
              :obj="obj"
              :can_be_deleted="
                can_delete_approval &&
                approval_ids_can_be_deleted.includes(obj.id)
              "
              @delete:approval="handle_delete_document_approval(obj.id)"
              @download:file="handle_download_file(...$event)"
              @delete:file="handle_delete_document_approval_file($event)"
              @copy_link_to:file="
                handle_copy_link_to_document_file_into_clipboard($event)
              "
            />
          </div>
        </div>
        <div
          v-if="can_view_approvalflow && !is_edit_mode"
          class="page__approvalflowcontainer"
        >
          <form-text-field
            class="container__header"
            value=" :"
          />
          <text-button
            class="approvalflowcontainer__createbutton"
            v-if="!approval_flow && can_change_approvalflow"
            title=" "
            @click="handle_create_approval_flow()"
          />
          <approval-flow
            v-if="approval_flow"
            class="approvalflowcontainer__flow"
            :is_active="approval_flow.is_active"
            :is_saved="approval_flow.is_saved"
            :success="approval_flow.success"
            :created_at="approval_flow.created_at"
            :updated_at="approval_flow.updated_at"
            :steps="approval_flow.steps"
            :states="approval_flow_states"
            :users="users"
            :can_be_modified="can_change_approvalflow"
            @add:step="handle_add_approval_flow_step()"
            @delete:step="handle_delete_approval_flow_step($event)"
            @attach:user="handle_attach_user_to_approval_flow_step(...$event)"
            @delete:user="handle_delete_user_from_approval_flow_step(...$event)"
            @save="handle_save_approval_flow()"
            @start="handle_start_approval_flow()"
            @stop="handle_stop_approval_flow()"
          />
        </div>
      </div>
      <div
        class="page__form-backdrop"
        v-if="doc && is_form_opened && form_pages"
      >
        <base-form
          class="page__form"
          :attributes="
            is_edit_mode === true ? doc.raw_attributes : doc.attributes
          "
          :settings="form_pages"
          :context="form_context"
          @update="
            handle_update_doc_attribute(doc.id, $event.name, $event.value)
          "
          @close="is_form_opened = false"
        >
        </base-form>
      </div>
    </div>
    <div class="page" v-else>
      <div class="page__header">
        <heading-component
          text="..  .."
          size="small"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { v4 as uuidv4 } from "uuid";
import { onMounted, computed, onBeforeUnmount, ref, watch } from "vue";
import { useRoute, useRouter } from "vue-router";
import { VueUnitOfWork } from "../logic/service_layer/uow";
import { MessageBus } from "../logic/service_layer/message_bus";
import {
  useBeforeEnterPage,
  useGetUsers,
  useGetFolders,
  useGetUserSearchSchema,
  useGetDocument,
  useGetContext,
  useValidateDocument,
  useValidateIntOr404,
  useGetUserGroups,
  useGetDocumentApprovalsWithSpareFiles,
  useGetDocumentApprovalFlow,
  convertDateToDateTimeString,
  generate_states_obj_from_approvals,
} from "../logic/service_layer/use_modules";
import BtnComponent from "../components/buttons/BtnComponent.vue";
import HeadingComponent from "../components/HeadingComponent.vue";
import TextButton from "../components/buttons/TextButton.vue";
import ToggleTextButton from "../components/buttons/ToggleTextButton.vue";
import DocumentErrorsRow from "../components/document/DocumentErrorsRow.vue";
import DocumentAttributeBlock from "../components/document/DocumentAttributeBlock.vue";
import FileList from "../components/file/FileList.vue";
import FileBlock from "../components/file/FileBlock.vue";
import FileDropZone from "../components/file/FileDropZone.vue";
import DocumentPageDownloadProgressRow from "../components/document/DocumentPageDownloadProgressRow.vue";
import ApprovalRow from "../components/approval/ApprovalRow.vue";
import ApprovalFlow from "../components/approval/ApprovalFlow.vue";
import FormTextField from "../components/fields/FormTextField.vue";
import FormSelectInput from "../components/fields/FormSelectInput.vue";
import FormFilteredSelectInput from "../components/fields/FormFilteredSelectInput.vue";
import BaseForm from "../components/forms/BaseForm.vue";
import {
  SetDocumentAttribute,
  SaveDocumentsInFolder,
  DownloadFileFromFolder,
  AddDocumentApproval,
  UpdateDocumentApproval,
  DeleteDocumentApproval,
  DelegateDocumentApproval,
  UploadDocumentApprovalFile,
  DeleteDocumentApprovalFile,
  DuplicateDocumentToUsergroup,
  DownloadDocumentFilesAsArchive,
  UploadFileToFolder,
  AttachFileToDocument,
  DetachFileFromDocument,
  PushDocumentButton,
  UpdateFileFromFolder,
  CreateDocumentApprovalFlow,
  AddDocumentApprovalFlowStep,
  DeleteDocumentApprovalFlowStep,
  AttachUserToDocumentApprovalFlowStep,
  DeleteUserFromDocumentApprovalFlowStep,
  SetDocumentApprovalFlow,
  StartDocumentApprovalFlow,
  StopDocumentApprovalFlow,
  UpdateUploadProgress,
} from "../logic/domain/command";

import {
  DocumentLinkCopied,
  FileLinkCopied,
  UploadEmptyFileError,
  UploadFileTooBigError,
  UploadFileWrongFilenameError,
} from "../logic/domain/event";

export default {
  components: {
    BtnComponent,
    HeadingComponent,
    TextButton,
    ToggleTextButton,
    DocumentErrorsRow,
    DocumentAttributeBlock,
    FileList,
    FileBlock,
    FileDropZone,
    DocumentPageDownloadProgressRow,
    ApprovalRow,
    ApprovalFlow,
    FormTextField,
    FormSelectInput,
    FormFilteredSelectInput,
    BaseForm,
  },
  name: "DocumentPage",
  setup() {
    const route = useRoute();
    const router = useRouter();
    const uow = new VueUnitOfWork();

    const route_full_path = route.fullPath;

    // PARAMS
    const usergroup_id = useValidateIntOr404(route.params.usergroup_id);
    const folder_id = useValidateIntOr404(route.params.folder_id);
    const document_id = route.params.document_id;

    // USER
    const user = uow.user_repository.get_current();
    const users = computed(() =>
      uow.user_repository
        .values()
        .map((e) => e.value)
        .filter((e) => e.is_active === true)
    );
    const user_options = computed(() =>
      [{ name: " ", value: "NOT_CHOSEN" }].concat(
        users.value
          .map((e) => {
            return {
              name: (
                e.last_name +
                " " +
                e.first_name +
                " (" +
                e.username +
                ")"
              ).trim(),
              value: e.username,
            };
          })
          .sort((a, b) => a.name.localeCompare(b.name))
      )
    );

    const approval_action_username = ref("NOT_CHOSEN");
    const is_approval_action_username_chosen = computed(
      () => approval_action_username.value !== "NOT_CHOSEN"
    );

    // FOLDER
    const folder = uow.folder_repository.get(folder_id); // Ref

    const folder_name = computed(() =>
      folder.value === null ? "" : folder.value.name
    );

    const search_schema = computed(() =>
      folder.value === null ? [] : folder.value.search_schema
    );

    const form_schema = computed(() => folder.value.form_schema);
    const form_pages = computed(() => {
      if (!form_schema.value) return {};

      return form_schema.value.pages.map((e) => {
        return {
          size: e.size,
          name: e.name,
          fields: e.fields[is_edit_mode.value ? "edit_mode" : "read_mode"],
        };
      });
    });

    // USERGROUP
    const usergroup = uow.usergroup_repository.get(usergroup_id);

    const usergroup_name = computed(() =>
      usergroup.value === null ? "" : usergroup.value.name
    );

    const copy_list_usergroups = computed(() => {
      let arr = uow.usergroup_repository
        .values()
        .filter(
          (obj) =>
            obj.value.id != usergroup.value.id &&
            (!obj.value.is_private ||
              obj.value.has_username(user.value.username))
        )
        .map((obj) => {
          return {
            id: obj.value.id,
            name: obj.value.name,
            value: obj.value.id.toString(),
          };
        });

      arr.sort((a, b) => a.name.localeCompare(b.name));
      return arr;
    });

    const copy_list_usergroup_id = ref(0);

    //    groups,  group_id 
    watch(copy_list_usergroups, (newValue) => {
      if (copy_list_usergroup_id.value === 0 && newValue.length > 0) {
        copy_list_usergroup_id.value = newValue[0].id;
      }
    });

    const handle_copy_list_change_usergroup = () => {
      MessageBus.handle(
        new DuplicateDocumentToUsergroup(
          folder_id,
          usergroup_id,
          copy_list_usergroup_id.value,
          document_id
        ),
        uow
      );
      set_seconds_before_button_copy_to_usergroup_activated(10);
    };

    // DOCUMENT
    const doc = uow.doc_repository.get(folder_id, document_id); // Ref
    const is_edit_mode = ref(false);

    watch(doc, (newValue) => {
      if (!newValue || newValue.is_blocked === true) is_edit_mode.value = false;
    });

    const handle_update_doc_attribute = (doc_id, name, value) => {
      //     ,   ,    
      //   = '' -    ,   
      //     ,    
      MessageBus.handle(
        new SetDocumentAttribute(folder_id, usergroup_id, doc_id, name, value),
        uow
      );
    };

    const handle_save_document = async () => {
      await MessageBus.handle(
        new SaveDocumentsInFolder(folder_id, usergroup_id),
        uow
      );
      await useValidateDocument(uow, folder_id, usergroup_id, document_id);
    };

    const handle_download_file = async (file, inline) => {
      const message = new DownloadFileFromFolder(
        folder_id,
        usergroup_id,
        file.id,
        file.name,
        file.size,
        inline
      );
      await MessageBus.handle(message, uow);
    };

    const handle_rename_file = async (file_id, filename) => {
      const message = new UpdateFileFromFolder(
        folder_id,
        usergroup_id,
        file_id,
        filename
      );
      await MessageBus.handle(message, uow);
      delete file_ids_are_being_renamed.value[file_id];
      await useValidateDocument(uow, folder_id, usergroup_id, document_id);
    };

    const file_ids_are_being_renamed = ref({});
    const are_files_saved = computed(
      () => Object.keys(file_ids_are_being_renamed.value).length == 0
    );

    const handle_rename_file_activated = (file_id) => {
      file_ids_are_being_renamed.value[file_id] = true;
    };

    const handle_copy_link_to_document_into_clipboard = () => {
      navigator.clipboard
        .writeText(window.location.href)
        .then(() => MessageBus.handle(new DocumentLinkCopied(), uow));
    };

    const handle_copy_link_to_document_file_into_clipboard = (file) => {
      const props = router.resolve({
        name: "DownloadFileFromFolderPage",
        params: {
          folder_id: folder_id,
          usergroup_id: usergroup_id,
          file_id: file.id,
        },
      });

      navigator.clipboard
        .writeText(window.location.origin + props.href)
        .then(() => MessageBus.handle(new FileLinkCopied(), uow));
    };

    const generate_doc_link = (doc_id) => {
      return {
        name: "DocumentPage",
        params: {
          folder_id: folder_id,
          usergroup_id: copy_list_usergroup_id.value,
          document_id: doc_id,
        },
      };
    };

    // DRAGGING
    const is_drag_above_doc_file_zone = ref(false);
    const is_drag_above_approval_file_zone = ref(false);

    // FILES
    const approval_max_file_size = 64; // MB

    const uploading_files = computed(() =>
      uow.file_in_folder_repository
        .values(folder_id)
        .map((obj) => obj.value)
        .filter((file) => !file.uploaded)
    );

    const downloading_files = computed(() =>
      uow.download_progress.values().map((obj) => obj.value)
    );

    const handle_document_approval_file_drop = async (file) => {
      let message = null;
      if (file.size === 0) {
        message = new UploadEmptyFileError(file.name);
      } else if (file.size > approval_max_file_size * 1024 * 1024) {
        message = new UploadFileTooBigError(file.name);
      } else {
        message = new UploadDocumentApprovalFile(
          folder_id,
          usergroup_id,
          document_id,
          uuidv4(),
          file
        );
      }
      await MessageBus.handle(message, uow);
    };

    const handle_document_approval_filename_wrong = async (filename) =>
      await MessageBus.handle(new UploadFileWrongFilenameError(filename), uow);

    const handle_delete_document_approval_file = async (file_id) => {
      await MessageBus.handle(
        new DeleteDocumentApprovalFile(
          folder_id,
          usergroup_id,
          document_id,
          file_id
        ),
        uow
      );
    };

    const document_approval_files = computed(() =>
      uow.doc_approval_file_repository.values().map((obj) => obj.value)
    );

    const handle_download_all_files = async () => {
      await MessageBus.handle(
        new DownloadDocumentFilesAsArchive(
          folder_id,
          usergroup_id,
          document_id,
          doc.value.files.map((x) => x.size).reduce((a, b) => a + b, 0)
        ),
        uow
      );
    };

    const doc_max_file_size = computed(() =>
      folder.value === null ? 0 : folder.value.max_file_size
    );

    const handle_document_file_drop = async (file) => {
      if (!doc.is_blocked) {
        if (file.size === 0) {
          MessageBus.handle(new UploadEmptyFileError(file.name), uow);
          return;
        }
        if (file.size > doc_max_file_size.value * 1024 * 1024) {
          MessageBus.handle(new UploadFileTooBigError(file.name), uow);
          return;
        }

        const file_id = uuidv4();

        const message = new UploadFileToFolder(
          folder_id,
          usergroup_id,
          file_id,
          file
        );

        await MessageBus.handle(message, uow);

        await MessageBus.handle(
          new AttachFileToDocument(
            folder_id,
            usergroup_id,
            document_id,
            file_id
          ),
          uow
        );
      }
    };

    const detach_file_from_document = (doc, file_id) => {
      if (!doc.is_blocked) {
        MessageBus.handle(
          new DetachFileFromDocument(folder_id, usergroup_id, doc.id, file_id),
          uow
        );
      }
    };

    // APPROVAL
    const text_comments = ref("");

    const approvals = computed(() => {
      let arr = uow.doc_approval_repository
        .values(doc.value.id)
        .map((obj) => obj.value);
      arr.sort((a, b) => b.updated_at - a.updated_at);
      return arr;
    });

    const approvals_before_last_update = computed(() =>
      approvals.value
        .filter((obj) => obj.updated_at <= doc.value.updated_at)
        .sort((a, b) => b.updated_at - a.updated_at)
    );

    const approvals_after_last_update = computed(() =>
      approvals.value
        .filter((obj) => obj.updated_at > doc.value.updated_at)
        .sort((a, b) => b.updated_at - a.updated_at)
    );

    const approval_ids_can_be_deleted = computed(() => {
      return approvals.value
        .filter((obj) => {
          //    
          //        
          if (
            obj.state === "NONE" &&
            (user.value.username === obj.from_username ||
              user.value.username === obj.to_username)
          )
            return true;
          return false;
        })
        .map((obj) => obj.id);
    });

    const approvals_to_be_closed_ids = computed(() => {
      const to_be_closed = approvals.value
        .filter(
          (obj) =>
            obj.state === "NONE" && obj.to_username === user.value.username
        )
        .map((obj) => obj.id);
      return to_be_closed.length === 0 ? null : to_be_closed;
    });

    const handle_paste = (e) => {
      e.stopPropagation();
      e.preventDefault();

      const clipboardData = e.clipboardData || window.clipboardData;
      insert_text_in_editable(clipboardData.getData("Text"));
    };

    const insert_text_in_editable = (text) => {
      if (window.getSelection) {
        const sel = window.getSelection();
        if (sel.getRangeAt && sel.rangeCount) {
          const range = sel.getRangeAt(0);
          range.deleteContents();
          range.insertNode(document.createTextNode(text));
        }
      } else if (document.selection && document.selection.createRange) {
        document.selection.createRange().text = text;
      }

      const DOM_elem_with_text = document.getElementById(
        "approvaldropblock__textzone"
      );
      text_comments.value = DOM_elem_with_text.textContent;
    };

    const handle_add_document_approval = async () => {
      await MessageBus.handle(
        new AddDocumentApproval(
          folder_id,
          usergroup_id,
          document_id,
          approval_action_username.value
        ),
        uow
      );
      await useGetDocument(uow, folder_id, usergroup_id, document_id);
      await useValidateDocument(uow, folder_id, usergroup_id, document_id);
    };

    const handle_delete_document_approval = async (approval_id) => {
      await MessageBus.handle(
        new DeleteDocumentApproval(
          folder_id,
          usergroup_id,
          document_id,
          approval_id
        ),
        uow
      );
      await useGetDocument(uow, folder_id, usergroup_id, document_id);
      await useValidateDocument(uow, folder_id, usergroup_id, document_id);
      await useGetDocumentApprovalsWithSpareFiles(
        uow,
        folder_id,
        usergroup_id,
        document_id
      );
    };

    const handle_delegate_document_approval = async (with_return) => {
      await MessageBus.handle(
        new DelegateDocumentApproval(
          folder_id,
          usergroup_id,
          document_id,
          approvals_to_be_closed_ids.value[0],
          approval_action_username.value,
          with_return
        ),
        uow
      );
      await useGetDocument(uow, folder_id, usergroup_id, document_id);
      await useValidateDocument(uow, folder_id, usergroup_id, document_id);
      await useGetDocumentApprovalsWithSpareFiles(
        uow,
        folder_id,
        usergroup_id,
        document_id
      );
    };

    const handle_approve_document = () =>
      handle_update_document_approval("APPROVED");
    const handle_reject_document = () =>
      handle_update_document_approval("REJECTED");

    const handle_update_document_approval = async (state) => {
      approvals_to_be_closed_ids.value.forEach(
        async (approval_to_be_closed_id) =>
          await MessageBus.handle(
            new UpdateDocumentApproval(
              folder_id,
              usergroup_id,
              doc.value.id,
              approval_to_be_closed_id,
              state,
              text_comments.value
            ),
            uow
          )
      );
      await useGetDocument(uow, folder_id, usergroup_id, document_id);
      await useValidateDocument(uow, folder_id, usergroup_id, document_id);

      await Promise.all([
        await useGetDocumentApprovalsWithSpareFiles(
          uow,
          folder_id,
          usergroup_id,
          document_id
        ),
        await useGetDocumentApprovalFlow(
          uow,
          folder_id,
          usergroup_id,
          document_id
        ),
      ]);
    };

    const approval_rules = computed(() =>
      folder.value === null ? {} : folder.value.approval_rules
    );

    const approval_rules_filename = computed(() =>
      approval_rules.value === null ? {} : approval_rules.value.filename
    );

    const approval_filename_pattern = computed(() => {
      if (!approval_rules_filename.value) return undefined;

      const flags =
        approval_rules_filename.value.case_insensitive || false ? "i" : "";

      return new RegExp(approval_rules_filename.value.pattern, flags);
    });

    const approval_rules_can_approve = computed(() =>
      approval_rules.value === null ? null : approval_rules.value.can_approve
    );

    const approval_rules_can_reject = computed(() =>
      approval_rules.value === null ? null : approval_rules.value.can_reject
    );

    const approval_rules_can_delegate_with_return = computed(() =>
      approval_rules.value === null
        ? null
        : approval_rules.value.can_delegate_with_return
    );

    const approval_rules_can_delegate_without_return = computed(() =>
      approval_rules.value === null
        ? null
        : approval_rules.value.can_delegate_without_return
    );

    const is_user_included_rights_list = (list) =>
      Array.isArray(list) ? list.includes(user.value.username) : true;

    const has_right_to_approve = computed(() =>
      is_user_included_rights_list(approval_rules_can_approve.value)
    );

    const has_right_to_reject = computed(() =>
      is_user_included_rights_list(approval_rules_can_reject.value)
    );

    const has_right_to_delegate_with_return = computed(() =>
      is_user_included_rights_list(
        approval_rules_can_delegate_with_return.value
      )
    );

    const has_right_to_delegate_without_return = computed(() =>
      is_user_included_rights_list(
        approval_rules_can_delegate_without_return.value
      )
    );

    // APPROVAL FLOW
    const approval_flow = uow.doc_approval_flow_repository.get(document_id); // ref

    const approval_flow_states = computed(() =>
      generate_states_obj_from_approvals(approvals_after_last_update.value)
    );

    const handle_create_approval_flow = () => {
      MessageBus.handle(new CreateDocumentApprovalFlow(document_id), uow);
    };

    const handle_add_approval_flow_step = () => {
      MessageBus.handle(new AddDocumentApprovalFlowStep(document_id), uow);
    };

    const handle_delete_approval_flow_step = (index) => {
      MessageBus.handle(
        new DeleteDocumentApprovalFlowStep(document_id, index),
        uow
      );
    };

    const handle_attach_user_to_approval_flow_step = (index, username) => {
      MessageBus.handle(
        new AttachUserToDocumentApprovalFlowStep(document_id, index, username),
        uow
      );
    };

    const handle_delete_user_from_approval_flow_step = (index, username) => {
      MessageBus.handle(
        new DeleteUserFromDocumentApprovalFlowStep(
          document_id,
          index,
          username
        ),
        uow
      );
    };

    const handle_save_approval_flow = () => {
      MessageBus.handle(
        new SetDocumentApprovalFlow(folder_id, usergroup_id, document_id),
        uow
      );
    };

    const handle_start_approval_flow = async () => {
      await MessageBus.handle(
        new StartDocumentApprovalFlow(folder_id, usergroup_id, document_id),
        uow
      );

      await useGetDocumentApprovalsWithSpareFiles(
        uow,
        folder_id,
        usergroup_id,
        document_id
      );
      await useGetDocumentApprovalFlow(
        uow,
        folder_id,
        usergroup_id,
        document_id
      );
      await useGetDocument(uow, folder_id, usergroup_id, document_id);
      await useValidateDocument(uow, folder_id, usergroup_id, document_id);
    };

    const handle_stop_approval_flow = async () => {
      await MessageBus.handle(
        new StopDocumentApprovalFlow(folder_id, usergroup_id, document_id),
        uow
      );
      await useGetDocumentApprovalsWithSpareFiles(
        uow,
        folder_id,
        usergroup_id,
        document_id
      );
    };

    // FORM
    const form_uid = uuidv4();
    const is_form_opened = ref(false);

    //    
    const form_context = computed(() => {
      if (!form_schema.value || !form_schema.value["context"]) return {};
      let result = {};
      form_schema.value["context"].forEach((e) => {
        const ctx = uow.context_repository.get(form_uid, e.name);
        result[e.name] = ctx.value;
      });
      return result;
    });

    // PERMISSIONS
    const calculate_strict_permission = (perm_name) => {
      return (
        uow.permission_repository.has_for_folder(folder_id, perm_name) &&
        !(
          uow.folder_repository.get_strict_validation(folder_id, perm_name) &&
          uow.doc_repository.has_errors(folder_id, document_id)
        )
      );
    };

    const can_change_doc = uow.permission_repository.has_for_folder(
      folder_id,
      "can_change_document"
    );
    const can_view_file = uow.permission_repository.has_for_folder(
      folder_id,
      "can_view_file"
    );

    const can_view_approval = computed(() =>
      calculate_strict_permission("can_view_documentapproval")
    );
    const can_add_approval = computed(() =>
      calculate_strict_permission("can_add_documentapproval")
    );
    const can_change_approval = computed(() =>
      calculate_strict_permission("can_change_documentapproval")
    );
    const can_delete_approval = computed(() =>
      calculate_strict_permission("can_delete_documentapproval")
    );
    const can_delegate_approval = computed(() =>
      calculate_strict_permission("can_delegate_documentapproval")
    );

    const can_view_approvalflow = computed(() =>
      calculate_strict_permission("can_view_documentapprovalflow")
    );
    const can_change_approvalflow = computed(() =>
      calculate_strict_permission("can_change_documentapprovalflow")
    );

    onMounted(async () => {
      await useBeforeEnterPage(
        uow,
        ["can_get_folders", "can_view_document"],
        folder_id,
        usergroup_id
      ); //   a
      await Promise.all([useGetFolders(uow), useGetUserGroups(uow)]);

      if (!folder.value) return;

      //   
      //        
      // ,    ,   
      // const usergroup = uow.usergroup_repository.get(usergroup_id);
      // MessageBus.handle(new SetCurrentUserGroup(usergroup.value), uow);

      //   
      await Promise.all([
        useGetUserSearchSchema(uow, folder_id, usergroup_id),
        useGetDocument(uow, folder_id, usergroup_id, document_id),
      ]);

      folder.value.is_necessary_validate = true;
      await useValidateDocument(uow, folder_id, usergroup_id, document_id);

      await useGetDocumentApprovalsWithSpareFiles(
        uow,
        folder_id,
        usergroup_id,
        document_id
      );
      await useGetUsers(uow);
      await useGetDocumentApprovalFlow(
        uow,
        folder_id,
        usergroup_id,
        document_id
      );

      //      
      if (form_schema.value && form_schema.value.context) {
        await useGetContext(
          uow,
          form_uid,
          form_schema.value.context,
          // REQUIRED PARAMETERS FOR DOCUMENT CONTEXT
          folder_id,
          usergroup_id,
          document_id
        );
      }
    });

    // TIMERS
    const update_upload_progress = () => {
      MessageBus.handle(new UpdateUploadProgress(), uow);
    };

    uow.upload_progress_timer.set_callback(update_upload_progress);

    onBeforeUnmount(async () => uow.upload_progress_timer.stop());

    // ACTION BUTTONS
    const action_buttons = computed(() =>
      folder.value === null
        ? []
        : folder.value.action_buttons.filter((obj) =>
            is_action_button_shown(obj)
          )
    );

    const APPROVED_after_last_update_usernames = computed(() =>
      approvals_after_last_update.value
        .filter((obj) => obj.state == "APPROVED")
        .map((obj) => obj.to_username)
    );

    const is_action_button_shown = (button_obj) =>
      button_obj.allowed_usernames
        ? button_obj.allowed_usernames.includes(user.value.username)
        : true;

    const is_action_button_disabled = (button_obj) => {
      let result = false;

      if (!button_obj.required_approvals) return result;

      if (button_obj.required_approvals.all)
        result =
          button_obj.required_approvals.all.filter(
            (username) =>
              !APPROVED_after_last_update_usernames.value.includes(username)
          ).length > 0;

      if (button_obj.required_approvals.anyOf)
        result =
          result ||
          button_obj.required_approvals.anyOf.filter(
            (username) =>
              !APPROVED_after_last_update_usernames.value.includes(username)
          ).length == button_obj.required_approvals.anyOf.length;

      return result;
    };

    const handle_push_document_button = async (action) => {
      await MessageBus.handle(
        new PushDocumentButton(folder_id, usergroup_id, document_id, action),
        uow
      );
      set_seconds_before_button_activated(action, 10);
    };

    // -    

    const COPY_TO_USERGROUP_ACTION = "__COPY_TO_USERGROUP__";

    const get_text_of_action_button = (caption, action) =>
      get_seconds_before_button_activated(action) === 0
        ? caption
        : caption +
          " ( " +
          get_seconds_before_button_activated(action).toString() +
          " .)";

    const get_seconds_before_button_activated = (action) =>
      uow.wait_seconds_timer.get(action);

    const set_seconds_before_button_activated = (action, value) =>
      uow.wait_seconds_timer.set(action, value);

    const get_seconds_before_button_copy_to_usergroup_activated = () =>
      uow.wait_seconds_timer.get(COPY_TO_USERGROUP_ACTION);

    const set_seconds_before_button_copy_to_usergroup_activated = (value) =>
      uow.wait_seconds_timer.set(COPY_TO_USERGROUP_ACTION, value);

    return {
      route_full_path,
      router,
      convertDateToDateTimeString,

      // USERS
      users,
      user_options,
      approval_action_username,
      is_approval_action_username_chosen,

      // PERMISSIONS
      can_change_doc,
      can_view_file,
      can_view_approval,
      can_add_approval,
      can_change_approval,
      can_delete_approval,
      can_delegate_approval,

      // PARAMS
      folder_id,
      usergroup_id,
      document_id,

      // FOLDER
      folder,
      folder_name,
      search_schema,

      // USERGROUP
      usergroup_name,
      copy_list_usergroups,
      copy_list_usergroup_id,
      handle_copy_list_change_usergroup,

      // DOCUMENT
      doc,
      is_edit_mode,
      handle_update_doc_attribute,
      handle_save_document,
      handle_copy_link_to_document_into_clipboard,
      generate_doc_link,
      handle_push_document_button,

      // APPROVAL
      text_comments,
      approvals,
      approvals_before_last_update,
      approvals_after_last_update,
      approval_ids_can_be_deleted,
      approvals_to_be_closed_ids,
      handle_paste,
      insert_text_in_editable,
      handle_add_document_approval,
      handle_delete_document_approval,
      handle_update_document_approval,
      handle_delegate_document_approval,
      handle_approve_document,
      handle_reject_document,
      approval_filename_pattern,
      has_right_to_approve,
      has_right_to_reject,
      has_right_to_delegate_with_return,
      has_right_to_delegate_without_return,

      // APPROVAL FLOW
      can_view_approvalflow,
      can_change_approvalflow,
      approval_flow,
      approval_flow_states,
      handle_create_approval_flow,
      handle_add_approval_flow_step,
      handle_delete_approval_flow_step,
      handle_attach_user_to_approval_flow_step,
      handle_delete_user_from_approval_flow_step,
      handle_save_approval_flow,
      handle_start_approval_flow,
      handle_stop_approval_flow,

      // DRAGGING
      is_drag_above_doc_file_zone,
      is_drag_above_approval_file_zone,

      // FILE
      are_files_saved,
      approval_max_file_size,
      doc_max_file_size,
      downloading_files,
      uploading_files,
      document_approval_files,
      handle_download_file,
      handle_rename_file,
      handle_rename_file_activated,
      handle_copy_link_to_document_file_into_clipboard,
      handle_document_approval_file_drop,
      handle_document_approval_filename_wrong,
      handle_delete_document_approval_file,
      handle_download_all_files,
      handle_document_file_drop,
      detach_file_from_document,

      // FORM
      is_form_opened,
      form_uid,
      form_schema,
      form_pages,
      form_context,

      // ACTION BUTTONS
      action_buttons,
      is_action_button_shown,
      is_action_button_disabled,
      APPROVED_after_last_update_usernames,
      get_seconds_before_button_activated,
      set_seconds_before_button_activated,
      get_seconds_before_button_copy_to_usergroup_activated,
      set_seconds_before_button_copy_to_usergroup_activated,
      get_text_of_action_button,
      COPY_TO_USERGROUP_ACTION,
    };
  },
};
</script>

<style scoped>
.page {
  display: flex;
  flex-direction: column;
  padding-left: 60px;
  padding-right: 60px;
}

.page__header {
  display: flex;
  padding-top: 60px;
  padding-bottom: 15px;
}

.page__linkbutton {
  font-size: 28px;
  user-select: none;
  cursor: pointer;
}

.page__container {
  display: flex;
}

.page__doccontainer {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  min-width: 550px;
  max-width: 550px;
  padding-right: 15px;
}
.page__docbuttonblock {
  display: flex;
  margin-bottom: 10px;
}

