  display: flex;
  flex-direction: column;
  padding: 5px;
  border: 1px solid black;
  border-radius: 10px;
}

.page__docfileblock {
  display: flex;
  flex-direction: column;
}

.page__docerrorblock {
  display: flex;
  flex-direction: column;
  width: 100%;
  overflow-wrap: break-word;
}

.page__copycontainer {
  display: flex;
  flex-direction: column;
}

.container__header {
  font-weight: 500;
  padding: 3px;
}

.copycontainer__text {
  padding: 3px;
  margin-left: 10px;
}

.copycontainer__row {
  display: flex;
}

.copycontainer__button {
  min-width: 200px;
  margin-right: 10px;
}

.copycontainer__usergroupselect {
  flex-grow: 1;
}

.page__approvalcontainer {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  align-items: stretch;
}

.page__approvalorderblock {
  display: flex;
  align-items: flex-start;
  padding: 10px;
}

.approvalorderblock__button {
  margin-left: 10px;
  cursor: pointer;
}

.page__approvalactionblock {
  display: flex;
  flex-direction: column;
  padding: 10px;
}

.page__approvalbuttonblock {
  display: flex;
  width: 150px;
  padding: 5px;
}
.approvalbuttonblock__button {
  margin: 3px;
  cursor: pointer;
}

.page__approvaldropblock {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex-wrap: wrap;
}

.page__docfiledropzone {
  min-height: 80px;
  max-height: 80px;
  border: 2px dashed grey;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 3px;
  margin-top: 10px;
}

.approvaldropblock__filedropzone {
  min-height: 80px;
  max-height: 80px;
  overflow-y: auto;
  border: 2px dashed grey;
  border-radius: 5px;
  display: flex;
  width: 100%;
  padding: 3px;
}

.page__docfiledropzone_dragging,
.approvaldropblock__filedropzone_dragging {
  background: repeating-linear-gradient(
    45deg,
    #606dbc,
    #606dbc 10px,
    #465298 10px,
    #465298 20px
  );
}

.page__docfiledropzonetitle {
  font-size: 24px;
}

.approvaldropblock__dropzonetitle {
  width: 100%;
  display: block;
}

.page__approvalfileblock {
  display: flex;
  flex-wrap: wrap;
  flex-grow: 1;
}

.approvalfileblock__file {
  padding: 3px;
}

.page__approvaltextblock {
  display: inline-block;
  border: solid 1px #000;
  min-height: 30px;
}

.page__approvalitemsblock {
  display: flex;
  flex-direction: column;
  padding: 10px;
}

.page__approvalitem {
  margin-bottom: 5px;
}

.page__approvallastupdatetext {
  color: red;
  margin-top: 10px;
  margin-bottom: 10px;
}

.docbuttonblock__button {
  user-select: none;
}

.docbuttonblock__grow {
  flex-grow: 1;
}

.docbuttonblock__linktoform {
  cursor: pointer;
  user-select: none;
}

.docbuttonblock__button_red {
  color: red;
}

.docerrorblock__noerrors {
  color: green;
}

.page__approvalflowcontainer {
  display: flex;
  flex-direction: column;
  min-width: 500px;
}

.approvalflowcontainer__createbutton {
  margin-left: 7px;
}

.page__form-backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
