import { Single as FlowSingle } from "./flow";

const Single = {
  type: "object",
  properties: {
    id: {
      type: "integer",
    },
    flow: FlowSingle,
    success: {
      type: "boolean",
    },
    is_active: {
      type: "boolean",
    },
    input: { type: "object" },
    process: { type: "object" },
    message_if_fail: {type: "boolean"},
    started_at: {
      type: "string",
    },
    finished_at: {
      type: ["null", "string"],
    },
  },
  required: [
    "id",
    "flow",
    "success",
    "is_active",
    "input",
    "process",
    "message_if_fail",
    "started_at",
    "finished_at",
  ],
  additionalProperties: false,
};

const List = {
  type: "array",
  items: Single,
};

export { Single, List };
