import {
  DeleteManyDocuments,
  SaveManyDocuments,
  AddDocumentModel,
} from "../../domain/command";

import {
  NegativeResponse,
  GetDocumentRequest,
  GetDocumentResponse,
  GetManyDocumentsRequest,
  GetManyDocumentsResponse,
  SaveManyDocumentsRequest,
  SaveManyDocumentsResponse,
  DeleteManyDocumentsRequest,
  DeleteManyDocumentsResponse,
  DuplicateDocumentToUsergroupRequest,
  DuplicateDocumentToUsergroupResponse,
  DuplicateManyDocumentsToUsergroupRequest,
  DuplicateManyDocumentsToUsergroupResponse,
  GetPreviousDocumentsRequest,
  GetPreviousDocumentsResponse,
} from "../../adapters/api";

import {
  ApiError,
  SaveManyDocumentsSuccess,
  DeleteManyDocumentsSuccess,
  DocumentAttributesChanged,
  DocumentFilesAreUploading,
  DuplicateDocumentToUsergroupSuccess,
  DuplicateManyDocumentsToUsergroupSuccess,
} from "../../domain/event";

import { Document, BaseFile } from "../../domain/model";

class WrongResponseError extends Error {}

const convert_doc_response_obj_to_model = (obj, is_created_first_time) => {
  const base_files = obj.files.map((f) => {
    const base_file = new BaseFile(f.id, f.name, f.size);
    base_file.set_size(f.size);
    return base_file;
  });

  const doc = new Document(
    obj.id,
    obj.folder_id,
    obj.usergroup_id,
    is_created_first_time,
    obj.union,
    obj.created_at ? new Date(obj.created_at) : obj.created_at,
    obj.updated_at ? new Date(obj.updated_at) : obj.updated_at
  );

  //   
  Object.keys(obj.data).forEach((key) => doc.set_attribute(key, obj.data[key]));

  //    
  base_files.forEach((base_file) => {
    doc.add_file(base_file.id, base_file);
  });

  //     
  doc.is_blocked = obj.is_blocked;

  //    
  doc.description = obj.description;

  return doc;
};

const get_document = async (event, uow) => {
  const folder = uow.folder_repository.get(event.folder_id);

  const request = new GetDocumentRequest({
    folder_id: event.folder_id,
    usergroup_id: event.usergroup_id,
    document_id: event.document_id,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof GetDocumentResponse) {
    const doc = convert_doc_response_obj_to_model(response.data, false);
    doc.description_pattern = folder.value.description_pattern;
    uow.push_message(
      new AddDocumentModel(event.folder_id, event.usergroup_id, doc, false)
    );
    return;
  }

  throw new WrongResponseError();
};

const get_many_documents = async (event, uow) => {
  uow.doc_repository.reset(event.folder_id);

  const folder = uow.folder_repository.get(event.folder_id);
  const query = structuredClone(event.query);

  if (event.limit) query["_LIMIT"] = event.limit;
  if (event.offset) query["_OFFSET"] = event.offset;
  if (event.sort_by) query["_SORT_BY"] = event.sort_by;

  if (event.sort_asc === false) query["_SORT_ASC"] = "DESC";

  if (event.sort_rule && event.sort_rule !== "DEFAULT")
    query["_SORT_RULE"] = event.sort_rule;

  const request = new GetManyDocumentsRequest({
    folder_id: event.folder_id,
    usergroup_id: event.usergroup_id,
    query: query,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof GetManyDocumentsResponse) {
    response.data.documents.forEach((obj) => {
      const doc = convert_doc_response_obj_to_model(obj, false);
      doc.description_pattern = folder.value.description_pattern;

      uow.push_message(
        new AddDocumentModel(event.folder_id, event.usergroup_id, doc, false)
      );
    });
    uow.doc_repository.set_count(response.data.count);
    return;
  }

  throw new WrongResponseError();
};

const set_document_attribute = (event, uow) => {
  if (uow.doc_repository.is_blocked(event.folder_id, event.document_id)) return;

  uow.doc_repository.set_attribute(
    event.folder_id,
    event.document_id,
    event.name,
    event.value,
    event.to_be_decoded
  );
  uow.doc_repository.mark_as_modified(event.folder_id, event.document_id);
  uow.push_message(
    new DocumentAttributesChanged(
      event.folder_id,
      event.usergroup_id,
      event.document_id
    )
  );
};

const save_documents_in_folder = async (event, uow) => {
  const doc_ids = uow.doc_repository.pure_keys(event.folder_id);
  let to_be_deleted = [];
  let to_be_created = [];

  const handle_function = async (doc_id) => {
    const is_deleted = uow.doc_repository.is_deleted(event.folder_id, doc_id);
    if (is_deleted) {
      const is_created_first_time = uow.doc_repository.is_created_first_time(
        event.folder_id,
        doc_id
      );
      //    ,    ,  
      //     
      if (is_created_first_time) {
        uow.doc_repository.delete(event.folder_id, doc_id);
      } else {
        //   ,      
        to_be_deleted.push(doc_id);
      }
      return true;
    }

    const is_saved = uow.doc_repository.is_saved(event.folder_id, doc_id);
    if (!is_saved) {
      const doc = uow.doc_repository.get(event.folder_id, doc_id).value;
      const attributes = doc.raw_attributes;
      const attach_files_ids = doc.files.map((obj) => obj.id);

      to_be_created.push({
        id: doc_id,
        union: doc.union,
        data: attributes,
        description: doc.description,
        files: attach_files_ids,
      });
    }
  };

  //     
  if (
    !doc_ids.every((id) => uow.doc_repository.uploaded(event.folder_id, id))
  ) {
    uow.push_message(new DocumentFilesAreUploading());
    return false;
  }

  doc_ids.forEach((id) => {
    //    ,   
    if (!uow.doc_repository.is_blocked(event.folder_id, id))
      handle_function(id);
  });

  //   
  if (to_be_deleted.length > 0)
    await uow.push_message(
      new DeleteManyDocuments(
        event.folder_id,
        event.usergroup_id,
        to_be_deleted
      )
    );

  //   
  if (to_be_created.length > 0)
    await uow.push_message(
      new SaveManyDocuments(event.folder_id, event.usergroup_id, to_be_created)
    );
};

const save_many_documents = async (event, uow) => {
  const folder = uow.folder_repository.get(event.folder_id);

  const request = new SaveManyDocumentsRequest({
    folder_id: event.folder_id,
    usergroup_id: event.usergroup_id,
    docs: event.docs,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return false;
  }

  if (response instanceof SaveManyDocumentsResponse) {
    //       ,  
    response.data.forEach((obj) => {
      const doc = convert_doc_response_obj_to_model(obj);
      doc.description_pattern = folder.value.description_pattern;
      uow.doc_repository.set(event.folder_id, obj.id, doc);
      uow.doc_repository.mark_as_saved(event.folder_id, obj.id);
    });
    uow.push_message(new SaveManyDocumentsSuccess());
    return true;
  }

  throw new WrongResponseError();
};

const mark_document_as_deleted = (event, uow) => {
  if (uow.doc_repository.is_blocked(event.folder_id, event.document_id)) return;

  uow.doc_repository
    .files(event.folder_id, event.document_id)
    .forEach((base_file) => {
      uow.file_in_folder_repository.set(
        event.folder_id,
        base_file.id,
        base_file
      );
    });
  uow.doc_repository.mark_as_deleted(event.folder_id, event.document_id);
};

const mark_document_as_not_deleted = (event, uow) => {
  if (uow.doc_repository.is_blocked(event.folder_id, event.document_id)) return;

  uow.doc_repository
    .files(event.folder_id, event.document_id)
    .forEach((base_file) => {
      uow.file_in_folder_repository.delete(event.folder_id, base_file.id);
    });
  uow.doc_repository.mark_as_not_deleted(event.folder_id, event.document_id);
};

const delete_many_documents = async (event, uow) => {
  const request = new DeleteManyDocumentsRequest({
    folder_id: event.folder_id,
    usergroup_id: event.usergroup_id,
    ids: event.document_ids,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return false;
  }

  if (response instanceof DeleteManyDocumentsResponse) {
    event.document_ids.forEach((id) =>
      uow.doc_repository.delete(event.folder_id, id)
    );
    uow.push_message(new DeleteManyDocumentsSuccess());
    return true;
  }

  throw new WrongResponseError();
};

const add_document = (event, uow) => {
  const doc = convert_doc_response_obj_to_model(
    {
      id: event.document_id,
      folder_id: event.folder_id,
      usergroup_id: event.usergroup_id,
      union: event.union_id,
      data: event.attributes,
      files: event.files,
      is_blocked: event.is_blocked,
    },
    event.is_created_first_time
  );

  const folder = uow.folder_repository.get(event.folder_id);
  doc.description_pattern = folder.value.description_pattern;

  const now_timestamp = Math.floor(Date.now() / 1000);
  doc.set_attribute("_CREATED_AT_DATE", now_timestamp);
  doc.set_attribute("_UPDATED_AT_DATE", now_timestamp);

  uow.push_message(
    new AddDocumentModel(
      event.folder_id,
      event.usergroup_id,
      doc,
      event.mark_as_modified
    )
  );
};

const add_document_model = (event, uow) => {
  //    
  uow.doc_repository.set(event.folder_id, event.doc.id, event.doc);

  //       
  event.doc.files.forEach((f) => uow.file_common_repository.set(f.id, f));

  if (event.mark_as_modified === true)
    uow.doc_repository.mark_as_modified(event.folder_id, event.doc.id);
};

const duplicate_document_to_usergroup = async (event, uow) => {
  const request = new DuplicateDocumentToUsergroupRequest({
    folder_id: event.folder_id,
    source_usergroup_id: event.source_usergroup_id,
    target_usergroup_id: event.target_usergroup_id,
    document_id: event.document_id,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return false;
  }

  if (response instanceof DuplicateDocumentToUsergroupResponse) {
    const original_doc = uow.doc_repository.get(
      event.folder_id,
      event.document_id
    ); //Ref
    const copied_doc = convert_doc_response_obj_to_model(response.data, false);
    original_doc.value.add_clone(copied_doc);
    // description_pattern  ,     

    uow.push_message(new DuplicateDocumentToUsergroupSuccess());
    return true;
  }

  throw new WrongResponseError();
};

const duplicate_many_documents_to_usergroup = async (event, uow) => {
  const request = new DuplicateManyDocumentsToUsergroupRequest({
    folder_id: event.folder_id,
    source_usergroup_id: event.source_usergroup_id,
    target_usergroup_id: event.target_usergroup_id,
    document_ids: event.document_ids,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return false;
  }

  if (response instanceof DuplicateManyDocumentsToUsergroupResponse) {
    uow.push_message(new DuplicateManyDocumentsToUsergroupSuccess());
    return true;
  }

  throw new WrongResponseError();
};

//   .       
const get_document_attributes_context = (event, uow) => {
  if (!uow.doc_repository.is_saved(event.folder_id, event.document_id)) return;

  const doc = uow.doc_repository.get(event.folder_id, event.document_id); // Ref
  const attrs = event.is_raw ? doc.value.raw_attributes : doc.value.attributes;

  uow.context_repository.set(event.ctx_id, event.ctx_name, attrs);
};

//  count     
//  .     attribute_name  
const get_document_previous_attribute_context = async (event, uow) => {
  if (!uow.doc_repository.is_saved(event.folder_id, event.document_id)) return;

  //   id  ,  -
  //     
  const folder = uow.folder_repository.get(event.folder_id);
  const usergroup_slug = folder.value.master_usergroup_slug;

  let usergroup_id =
    usergroup_slug === ""
      ? null
      : uow.usergroup_repository.get_id_by_slug(usergroup_slug);

  //     ,   
  //  ,   
  if (usergroup_id === null) {
    usergroup_id = event.usergroup_id;
  }

  const request = new GetPreviousDocumentsRequest({
    folder_id: event.folder_id,
    usergroup_id: usergroup_id,
    document_id: event.document_id,
    count: event.count,
  });

  const response = await uow.api.call(request);

  if (response instanceof NegativeResponse) {
    uow.push_message(new ApiError(response.data.code));
    return;
  }

  if (response instanceof GetPreviousDocumentsResponse) {
    const docs = response.data.map((obj) =>
      convert_doc_response_obj_to_model(obj, false)
    );

    const attrs = docs.map((doc) =>
      event.is_raw
        ? doc.raw_attributes[event.attribute_name]
        : doc.attributes[event.attribute_name]
    );

    uow.context_repository.set(
      event.ctx_id,
      event.ctx_name,
      attrs.filter((e) => e !== undefined)
    );
    return;
  }

  throw new WrongResponseError();
};


export {
  convert_doc_response_obj_to_model,
  get_document,
  get_many_documents,
  set_document_attribute,
  save_documents_in_folder,
  save_many_documents,
  mark_document_as_deleted,
  mark_document_as_not_deleted,
  delete_many_documents,
  add_document,
  add_document_model,
  duplicate_document_to_usergroup,
  duplicate_many_documents_to_usergroup,
  // CONTEXT
  get_document_attributes_context,
  get_document_previous_attribute_context,
};
